/**
 * @param {object} classOptions
 */
export function getCssClasses(classOptions) {
  let customClassOptions = ['base', 'complete', 'empty', 'invalid', 'focus', 'webkitAutofill'];
  let defaultClassOptions = {
    base: 'DRElement',
    complete: 'DRElement--complete',
    empty: 'DRElement--empty',
    focus: 'DRElement--focus',
    invalid: 'DRElement--invalid',
    webkitAutofill: 'DRElement--webkit-autofill'
  };
  let combinedClassOptions = {};
  if(typeof classOptions === 'object') {
    customClassOptions.forEach(function(cssClass) {
      combinedClassOptions[cssClass] = classOptions[cssClass] ?  classOptions[cssClass] : defaultClassOptions[cssClass];
    });
  } else {
    combinedClassOptions = defaultClassOptions;
  }
  return combinedClassOptions;
}

/**
 * This takes an object containing classes and removes them from element
 * @param {object} classOptions
 * @param {node} element
 */
export function removeClasses(classOptions, element) {
  if(typeof classOptions === 'object') {
    Object.keys(classOptions).forEach(function(cssClass) {
      element.classList.remove(classOptions[cssClass]);
    });
  }
}

/**
 * @param {object} styleOptions
 */
export function getElementHeight(styleOptions) {
  // set minimum height and size factor
  const minHeight = 40;
  const sizeFactor = 1.2;
  let iframeHeight = `${minHeight * sizeFactor}px`;
  if(styleOptions !== null && typeof styleOptions === 'object') {
    if(styleOptions.hasOwnProperty('base') && typeof styleOptions.base === 'object') {
      // if user specified height, use it
      if(styleOptions.base.hasOwnProperty('height')) {
        iframeHeight = styleOptions.base.height;
      // if user didn't specify height, but did specify fontSize, multiply fontSize times size factor
      } else if(styleOptions.base.hasOwnProperty('fontSize')) {
        iframeHeight = `calc(${sizeFactor}*${styleOptions.base.fontSize})`;
      }
    }
  }
  return iframeHeight;
}

/**
 * This takes an object containing classes and returns a list of the ones in the element's classList
 * @param {object} classOptions
 * @param {node} element
 */
export function getActiveClasses(classOptions, element) {
  let activeClasses = [];
  Object.keys(classOptions).forEach((key) => {
    if(element.classList.contains(classOptions[key])) {
      activeClasses.push(key);
    }
  });
  return activeClasses;
}

/**
 * applyActiveClasses takes an array of classNames and applies them to an element
 * @param {array} activeClassTypes - list of active types of classes (base, empty, invalid, etc)
 * @param {object} classOptions - current class options
 * @param {node} element
 */
export function applyActiveClasses(activeClassTypes, classOptions, element) {
  activeClassTypes.forEach((cssClass) => {
    element.classList.add(classOptions[cssClass]);
  });
}

/**
 * formatStyleCss takes style options and returns formatted CSS
 * @param {object} styleOptions - style options
 */
export function formatStyleCss(styleOptions) {
  let customStyles = '';
  let customPseudoStyles = '';
  // define approved style types and attributes
  let styleTypes = ['base', 'complete', 'empty', 'invalid'];
  // key is what clients enter in options, value is what we use for css
  let styleAttributes = {
    'color': 'color',
    'height': 'height',
    'fontFamily': 'font-family',
    'fontSize': 'font-size',
    'fontSmoothing': 'font-smoothing',
    'fontStyle': 'font-style',
    'fontVariant': 'font-variant',
    'fontWeight': 'font-weight',
    'letterSpacing': 'letter-spacing',
    'padding': 'padding',
    'paddingLeft': 'padding-left',
    'paddingRight': 'padding-right',
    'paddingTop': 'padding-top',
    'paddingBottom': 'padding-bottom',
    'textAlign': 'text-align',
    'textDecoration': 'text-decoration',
    'textShadow': 'text-shadow',
    'textTransform': 'text-transform',
    ':hover': ':hover',
    ':focus': ':focus',
    '::placeholder': '::placeholder',
    '::selection': '::selection',
    ':-webkit-autofill': ':-webkit-autofill',
    ':disabled': ':disabled',
    '::-ms-clear': '::-ms-clear',
    'iconColor': 'icon-color',
  };
  Object.keys(styleOptions).map(styleType => {
    if (styleTypes.includes(styleType)) {
      let tempStyleString = '';
      let tempPseudoString = '';
      Object.entries(styleOptions[styleType]).forEach(function (styleAttribute) {
        //check that this item is an approved style
        if (styleAttributes.hasOwnProperty(styleAttribute[0])) {
          if (styleAttribute[0].charAt(0) !== ':') {
            //this item is not a pseudo element or pseudo selector
            tempStyleString += `${styleAttributes[styleAttribute[0]]}: ${styleAttribute[1]}; `;
          } else {
            //this item is a pseudo element or pseudo selector, so we have to get all its properties
            if (typeof styleAttribute[1] === 'object') {
              let pseudoStyleName = `.${styleType}${styleAttribute[0]}`;
              let pseudoStyleAttributes = '';
              Object.entries(styleAttribute[1]).forEach(function (pseudoAttribute) {
                //check that this item is an approved css attribute
                if (styleAttributes.hasOwnProperty(pseudoAttribute[0])) {
                  //if webkit autofill color, change to -webkit-text-fill-color
                  if(styleAttribute[0] === ':-webkit-autofill' && pseudoAttribute[0] === 'color') {
                    pseudoStyleAttributes += `-webkit-text-fill-color: ${pseudoAttribute[1]}; `;
                  } else {
                    pseudoStyleAttributes += `${styleAttributes[pseudoAttribute[0]]}: ${pseudoAttribute[1]}; `;
                  }
                } else {
                  console.warn(`An invalid value with the key ${pseudoAttribute[0]} was passed into options`);  // eslint-disable-line no-console
                }
              });
              tempPseudoString += `${pseudoStyleName} { ${pseudoStyleAttributes}}\n`;
            }
          }
        } else {
          console.warn(`An invalid value with the key ${styleAttribute[0]} was passed into options`);  // eslint-disable-line no-console
        }
      });
      customPseudoStyles += tempPseudoString;
      customStyles += `.${styleType} { ${tempStyleString} }\n`;
    } else {
      console.warn(`An invalid value with the key ${styleType} was passed into options`);  // eslint-disable-line no-console
    }

  });
  let formattedCss = customStyles + customPseudoStyles;
  return formattedCss;
}

/**
 *
 * @param styleElementID {string} id for style block element that needs to be updated for select elements
 * @param options {object} component options
 */
export function setSelectStyles(styleElementID, options) {
  //TODO calc max-height
  let styleEl = document.getElementById(styleElementID);
  let styleElCss = styleEl.innerHTML;
  let hoverBackgrounds = {
    base: '',
    empty: '',
    complete: '',
    invalid: ''
  };
  for (let [key] of Object.entries(hoverBackgrounds)) {
    try {
      hoverBackgrounds[key] = convertToAlphaColor(options.style[key][':hover'].color, 20);
      styleElCss = styleElCss.replace('.' + [key] + ':hover {', '.' + [key] + ' .has-focus { background-color: ' + hoverBackgrounds[key] + '; ');
    } catch (e) {
      hoverBackgrounds[key] = 'transparent';
    }
  }
  styleEl.innerHTML = styleElCss;
}

/**
 * Changes a css color string to an rgba() format css color string
 * Useful for calculating a background color that is a transparent percentage of the text color for hover states
 * @param cssColor {string} value of a css color attribute.  Can be a hex value, color codeword, or rgb() value
 * @param percent {number || string} value representing the percentage of opacity that the returned rgba color should have
 * @returns {string} rgba() value that can be used as a CSS color.  If cssColor is not a valid css color, will return empty string.
 */
export function convertToAlphaColor(cssColor, percent) {
  //create a temp div with the color so we can get computed style
  let tempDiv = document.createElement('div');
  const rgbaValueLength = 4;
  tempDiv.setAttribute('style', 'color:' + cssColor);
  tempDiv.setAttribute('id', 'tempDiv');
  document.body.appendChild(tempDiv);
  let percentDecimal = parseInt(percent)/100;
  // convert to rgba
  let rgbColor;
  // checking if style.color will return an rgb or rgba value first because getComputedStyle is expensive
  // and also sometimes returns incorrect values if client happens to have the element in a hidden div
  // because they are using tabbed content, etc.
  if(document.getElementById('tempDiv').style.color.indexOf('rgb') !== -1) {
    rgbColor = document.getElementById('tempDiv').style.color;
  } else {
    let computedStyle = window.getComputedStyle(document.getElementById('tempDiv'));
    rgbColor = computedStyle.color;
  }
  let values = rgbColor.substring(
    rgbColor.lastIndexOf('(') + 1,
    rgbColor.lastIndexOf(')')
  );
  // check if color was already an rgba color and multiply times new percent for new alpha
  let valuesArray = values.split(',');

  // take the existing alpha and multiply times new alpha and put array back into value string
  if(valuesArray.length === rgbaValueLength) {
    percentDecimal = percentDecimal * valuesArray[valuesArray.length-1];
    valuesArray.pop();
    values = valuesArray.join();
  }

  let rgbaColor = 'rgba(' + values + ', ' + percentDecimal + ')';
  //remove the temp div and return alpha color
  document.body.removeChild(document.getElementById('tempDiv'));
  return rgbaColor;
}

export function createCssSpinner(altText) {
  return `<div class="DR-spinner">
      <span class="DR-spinnerText">${altText}</span>
      <div class="DR-bounce1"></div>
      <div class="DR-bounce2"></div>
      <div class="DR-bounce3"></div>
      </div>`;
}