import {getComponentWindow} from './createComponent';
import postRobot from '../post-robot-wrapper';
import {getRedirectReceiverURL} from './url-utils';

export function getAuthenticateSource(controllerId, sessionId, sourceId, sourceClientSecret, cvvComponent) {

  const controllerWindow = getComponentWindow(controllerId);

  if (!controllerWindow) {
    throw new Error(`Unable to locate controller '${controllerId}'`);
  }


  if(!sessionId) {
    throw new Error('You must provide a sessionId');
  }

  if(!sourceId) {
    throw new Error('You must provide a sourceId');
  }

  if(!sourceClientSecret) {
    throw new Error('You must provide a sourceClientSecret');
  }

  const returnUrl = getRedirectReceiverURL(controllerId);

  // Send message to Controller Frame to call createSource
  return postRobot.send(controllerWindow, 'authenticateSource', {
    sessionId,
    sourceId,
    sourceClientSecret,
    returnUrl,
    cvvComponent
  })
    .then(response => {
      return response.data;
    });

}