import {config} from '../config';
import {paymentServiceRequest, paymentServiceGetRequest} from '../../payment-service-request';
import {chooseCreateSourceCatchMessage} from './controller-create-source-utils';
import {
  getComponentWindow,
  sendAdyenAction
} from '../../../client/createComponent';
import postRobot from '../../../post-robot-wrapper';

export function runAuthenticateSourceAndHandleResponse(apiKey, sessionId, sourceId, sourceClientSecret, browserInfo, returnUrl, cvv) {

  const data = {
    sessionId,
    sourceId,
    sourceClientSecret,
    browserInfo,
    returnUrl,
  };
  if(typeof cvv !== 'undefined' && cvv !== '') {
    data.cvv = cvv;
  }
  const paymentApiUrl = `${config.paymentServiceBaseUrl}/source-authentications`;
  return paymentServiceRequest(data, apiKey, paymentApiUrl)
    .then((response) => {
      return response.data;
    })
    .catch((error) => {
      return chooseCreateSourceCatchMessage(error);
    });
}


export function handleAdyenResponseForSourceAuthentication(adyenResponseType, adyenResponseChallengeResult, instanceData) {
  const {clientSecret, id} = instanceData.paymentServiceResponse;

  if(!clientSecret || !id) {
    return Promise.reject('Cannot send data to paymentservice because required data is missing.');
  }

  const secret = clientSecret.split('_')[1];
  const data = {
    [adyenResponseType]: adyenResponseChallengeResult
  };


  return authenticateSourceWithAdyen(instanceData.controllerId, data, secret, id)
    .then(response => {
      if(typeof response !== 'undefined' && response.state === 'requires_action' && response.nextAction !== null) {
        response.requestType = 'authenticateSource';
        sendAdyenAction(instanceData.componentId, instanceData.controllerId, response, instanceData.resolve);
        return response;
      } else {
        instanceData.resolve(response);
        return response;
      }
    });
}


export function authenticateSourceWithAdyen(controllerId, data, clientSecret, id) {

  const controllerWindow = getComponentWindow(controllerId);

  if (!controllerWindow) {
    throw new Error(`Unable to locate controller '${controllerId}'`);
  }

  // Send message to Controller Frame to call authenticate source
  return postRobot.send(controllerWindow, 'authenticateSourceFromAdyenRequest', { data, clientSecret, id }, { timeout: 10000 })
    .then(response => {
      // This is a Post Robot Response object so you have to get the data out
      const {state} = response.data;
      if(state !== 'requires_action') {
        return {
          status: state
        };
      } else {
        return response.data;
      }

    })
    .catch((error) => {
      return chooseCreateSourceCatchMessage(error);
    });
}

export function runAuthenticateSourceFromAdyenRequestAndHandleResponse(apiKey, data, clientSecret, id) {
  const paymentApiUrl = `${config.paymentServiceBaseUrl}/source-authentications/${id}?secret=${clientSecret}`;
  return paymentServiceRequest(data, apiKey, paymentApiUrl)
    .then((response) => {
      return response.data;
    })
    .catch((error) => {
      return chooseCreateSourceCatchMessage(error);
    });
}

export function runHandleCheckSourceAuthenticationStatus(apiKey, id, clientSecret) {
  const paymentApiUrl = `${config.paymentServiceBaseUrl}/source-authentications/${id}?secret=${clientSecret}`;
  return paymentServiceGetRequest(apiKey, paymentApiUrl)
    .then((response) => {
      return response.data;
    })
    .catch((error) => {
      return chooseCreateSourceCatchMessage(error);
    });
}