import {getLocaleMessage, getMappedLocale} from './localization/localized-messages';
import iframeTitleText from '../../json/accessibility.json';

export function getIframeTitle(locale, id) {
  const mappedLocale = getMappedLocale(locale);

  if (!iframeTitleText.hasOwnProperty(mappedLocale)) {
    return getIframeTitle('en-US', id);
  }

  if(typeof iframeTitleText[mappedLocale].iframeTitles[id] !== 'undefined') {
    return iframeTitleText[mappedLocale].iframeTitles[id];
  }

  return typeof iframeTitleText['en-US'].iframeTitles[id] !== 'undefined' ? iframeTitleText['en-US'].iframeTitles[id] : null;
}

export function getAriaLabel(locale, type) {
  switch (type) {
    case 'cardnumber' : {
      return getLocaleMessage(locale, 'cardNumber');
    }
    case 'cardexpiration' : {
      return getLocaleMessage(locale, 'cardExpirationDate');
    }
    case 'cardcvv' : {
      return getLocaleMessage(locale, 'cardSecurityCode');
    }
    case 'konbini' : {
      return getLocaleMessage(locale, 'konbini');
    }
    case 'onlinebanking' : {
      return getLocaleMessage(locale, 'onlineBanking');
    }
    case 'iban' : {
      return getLocaleMessage(locale, 'iban');
    }
    default: {
      return null;
    }
  }
}

export function setAriaInvalid(el, value) {
  el.setAttribute('aria-invalid', value);
}

export function setAriaLabel(el, locale, componentType) {
  const ariaLabel = getAriaLabel(locale, componentType);
  el.setAttribute('aria-label', ariaLabel);
}