import buttonsText from '../../json/buttons.json';
import {getMappedLocale} from '../../app/components/localization/localized-messages';

export function getButtonTextByLocale(locale, code) {
  const mappedLocale = getMappedLocale(locale);
  if (!buttonsText.hasOwnProperty(mappedLocale)) {
    // eslint-disable-next-line no-console
    console.warn('Could not find button text for locale', locale);
    return getButtonTextByLocale('en-US', code);
  }

  if (buttonsText.hasOwnProperty(mappedLocale) && buttonsText[mappedLocale].buttons.hasOwnProperty(code) && buttonsText[mappedLocale].buttons[code].length === 0) {
    // eslint-disable-next-line no-console
    console.warn('Could not find button text for locale', locale);
    return getButtonTextByLocale('en-US', code);
  }

  if (!buttonsText[mappedLocale].buttons.hasOwnProperty(code) ) {
    // eslint-disable-next-line no-console
    console.warn('Could not find code', code);
    return getButtonTextByLocale(mappedLocale, 'payNow');
  }

  return buttonsText[mappedLocale].buttons[code];
}

