import data from './msts.json';
import {getMappedLocale} from '../../app/components/localization/localized-messages';

function getData(locale) {
  return data[locale];
}

export function getMSTSLocalizedStrings(title, locale) {
  const mappedLocale = getMappedLocale(locale);
  if (typeof getData(mappedLocale) === 'undefined') {
    // eslint-disable-next-line no-console
    console.warn('Locale is not supported, defaulting to en-US', locale);
    return getData('en-US')[title];
  }
  return getData(mappedLocale)[title];
}