import { config } from '../../app/components/config';
import postRobot from '../../post-robot-wrapper';
import { generateComponentId, onEventHandler, destroy, unmount, update, mount } from '../createComponent';
import {mergeOptions, sanitizeOptionsForGoogleApplePay } from '../../app/components/options';
import {
  createApplePayButton,
  setApplePayPaymentRequest,
  getApplePaySession,
  sendAppleClickEvent,
  sendShippingAddressChangeEvent,
  sendShippingMethodChangeEvent,
  appleResponseToPaymentService,
  paymentSourceToEventData,
  createAppleCompleteFunction, paymentSourceToEventDataForSession,
} from './applepay-utils';
import { emitComponentReady, emitComponentCancelled } from '../../app/components/payment-api-events';
import { addHandleOptions, mountComponentFromClient } from '../../app/components/payment-events';
import { generateInstanceData } from '../../app/components/create-initial-data';
import { sendCreateSourceRequest } from '../../app/components/google-apple-pay-events';
import { generateComponentData } from '../../app/components/payment-component-data';

const version = 3;

function isSessionMode(instanceData) {
  return typeof instanceData.options.sessionId !== 'undefined';
}

export function getPaymentServiceData(instanceData, appleResponseData) {
  if (isSessionMode(instanceData)) {
    const paymentServiceRequest = {
      'type': 'applePay',
      'sessionId': instanceData.options.sessionId,
      'owner': instanceData.options.billingAddress,
      'applePay': appleResponseData.payment.token.paymentData,
      'amount': instanceData.options.total.amount,
      'currency': instanceData.options.currency,
    };

    return {
      eventFunction: paymentSourceToEventDataForSession,
      paymentServiceRequestData: paymentServiceRequest
    };
  } else {
    return {
      eventFunction: paymentSourceToEventData,
      paymentServiceRequestData: appleResponseToPaymentService(appleResponseData, instanceData)
    };
  }
}

/**
 * Processes payment sent from apple and sends it to the payment service to create a payment source
 * @param {object} appleResponseData - data received from Apple
 * @param {function} resolve -
 * @param {object} instanceData
 */
export function processPayment(appleResponseData, resolve, instanceData) {
  const complete = createAppleCompleteFunction(resolve);

  const paymentServiceRequest = getPaymentServiceData(instanceData, appleResponseData);
  sendCreateSourceRequest(instanceData.controllerEmitter, instanceData.componentData, appleResponseData, paymentServiceRequest.paymentServiceRequestData,  paymentServiceRequest.eventFunction, complete);

}

/**
 * Process the click event for Apple pay
 * @param {object} instanceData
 * @param {Function} sendAppleClickEvent
 */
export function processAppleClickEvent(instanceData, sendAppleClickEvent) {
  sendAppleClickEvent(instanceData);
}

/**
 * Returns boolean true if Apple Pay is supported, false if not supported
 */
export function applePaymentCanMakePayment() {
  if (window.ApplePaySession) { //eslint-disable-line no-undef
    const merchantIdentifier = config.applePayMerchantId;
    return ApplePaySession.canMakePayments(merchantIdentifier); //eslint-disable-line no-undef
  }
  return false;
}

/**
 * Validates merchant
 * @param {string} validationUrl
 * @param {object} controllerEmitter
 */
export function validateMerchant(controllerEmitter, validationUrl) {
  //getting domain this way so that it is supported by Safari and iOS Safari
  const domain = window.location.origin.split('//')[1];
  const validationData = {
    validationUrl: validationUrl,
    domain: domain,
    displayName: domain
  };
  return controllerEmitter.send('validateAppleMerchant', validationData);
}

/**
 *
 * @param event - event content from Apple
 * @param processPayment - function for sending the event to the client
 * @param instanceData
 */
export function paymentAuthorization(event, processPayment, instanceData) {
  return new Promise(function (resolve) {
    return processPayment(event, resolve, instanceData);
  }).then(function (data) {
    if (data.status === 0) {
      instanceData.options = mergeOptions(instanceData.options, data);
      delete instanceData.options.errors;
      instanceData.applepaySession.completePayment(data);
    } else {
      instanceData.applepaySession.abort();
      emitComponentCancelled(instanceData.componentData);
    }
  });
}

/**
 * Calls validateMerchant and completes merchant validation
 * @param {object} event
 * @param {object} instanceData
 */
export function handleValidateMerchant(event, instanceData) {
  return validateMerchant(instanceData.controllerEmitter, event.validationURL).then(function (response) {
    instanceData.applepaySession.completeMerchantValidation(response.data.data);
  }).catch(err => {
    throw new Error(err);
  });
}

/**
 *
 * @param event - event content from Apple
 * @param sendShippingAddressChangeEvent - function for sending the event to the client
 * @param instanceData
 */
export function shippingAddressChange(event, sendShippingAddressChangeEvent, instanceData) {
  let getUpdatedDetails = new Promise(function (resolve) {
    sendShippingAddressChangeEvent(event.shippingContact, resolve, instanceData);
  });
  getUpdatedDetails.then(function (data) {
    instanceData.applepaySession.completeShippingContactSelection(data);
  });
}

/**
 *
 * @param event - event content from Apple
 * @param sendShippingMethodChangeEvent - function for sending the event to the client
 * @param instanceData
 */
export function shippingOptionChange(event, sendShippingMethodChangeEvent, instanceData) {
  let getUpdatedDetails = new Promise(function (resolve) {
    sendShippingMethodChangeEvent(event.shippingMethod, resolve, instanceData);
  });
  getUpdatedDetails.then(function (data) {
    instanceData.applepaySession.completeShippingMethodSelection(data);
  });
}


export function createApplePay() {

  let componentData;
  let controllerEmitter;
  let controllerListener;
  let instanceData;
  let supportedInstruments;

  function getInstanceData() {
    return instanceData;
  }

  function handleAppleOptions(data) {
    setOptions(data.options);
  }

  /**
   * Mounts component
   * @param node - string or html element where component should be mounted
   */
  function mountApplepay(node) {
    instanceData.parentNode = node;
    let applePayMount = mount.bind(this);
    applePayMount(node);
    return mountComponentFromClient(instanceData.controllerEmitter, instanceData.componentData, handleAppleOptions, emitComponentReady, undefined, instanceData);
  }


  /**
   * Sanitizes options, stores them in variable, changes amount to string
   * @param {object} unsafeOptions
   */
  function setOptions(unsafeOptions) {
    const options = sanitizeOptionsForGoogleApplePay(unsafeOptions);
    options.total.amount = options.total.amount.toString();
    instanceData.options = options;
    const node = typeof instanceData.parentNode === 'string' ? document.getElementById(instanceData.parentNode) : instanceData.parentNode;
    createApplePayButton(node, instanceData.componentData.componentId, instanceData.options, onApplePayButtonClick);
  }

  /**
   * applies updated options sent by the client
   * @param {object} options
   */
  function handleUpdate(options) {
    let applePayUpdate = update.bind(this);
    applePayUpdate(options);
  }

  /**
   * Returns payment options
   */
  function getPaymentOptions() {
    return instanceData.options;
  }

  /**
   * Returns Apple Pay button element
   */
  function getElement() {
    return document.getElementById(instanceData.componentData.componentId);
  }

  /**
   * Handles click on Apple Pay button and inits Payment Request
   */
  function onApplePayButtonClick() {
    processAppleClickEvent(instanceData, sendAppleClickEvent);
    let applepayPaymentRequest = setApplePayPaymentRequest(instanceData.options);
    let applepaySession = getApplePaySession(version, applepayPaymentRequest);
    applepaySession.onvalidatemerchant = (event) => handleValidateMerchant(event, instanceData);
    applepaySession.onshippingcontactselected = (event) => shippingAddressChange(event, sendShippingAddressChangeEvent, instanceData);
    applepaySession.onshippingmethodselected = (event) => shippingOptionChange(event, sendShippingMethodChangeEvent, instanceData);
    applepaySession.onpaymentauthorized = (event) => paymentAuthorization(event, processPayment, instanceData);
    applepaySession.oncancel = () => handleCancel(componentData);
    applepaySession.begin();
    instanceData.applepaySession = applepaySession;
  }

  /**
   * Sends cancel event data to controller to be sent on to client
   */
  function handleCancel(componentData) {
    emitComponentCancelled(componentData);
  }

  function createApplepayComponent(controllerId, key, options) {
    const type = 'applepay';
    let id = generateComponentId(type);
    const applepayComponent = {
      id: id,
      key: key,
      type: type,
      parentNode: null,
      controllerId: controllerId,
      canMakePayment: applePaymentCanMakePayment,
      mount: mountApplepay,
      destroy: destroy,
      on: onEventHandler,
      options: sanitizeOptionsForGoogleApplePay(options),
      show: onApplePayButtonClick,
      unmount: unmount,
      update: handleUpdate
    };

    componentData = generateComponentData(type, id, controllerId);
    componentData.key = key;
    controllerListener = postRobot.listener({window: componentData.controller.window, domain: config.domain});
    controllerEmitter = postRobot.client({window: componentData.controller.window, domain: config.domain});
    supportedInstruments = [{
      supportedMethods: 'https://apple.com/apple-pay',
      data: {
        version: 3,
        merchantIdentifier: config.applePayMerchantId,
        merchantCapabilities: ['supports3DS', 'supportsCredit', 'supportsDebit'],
        supportedNetworks: ['amex', 'masterCard', 'visa', 'JCB', 'chinaUnionPay', 'discover', 'privateLabel'],
        countryCode: applepayComponent.options && applepayComponent.options.hasOwnProperty('country') ? applepayComponent.options.country : 'US',
        requiredBillingContactFields: ['postalAddress', 'email', 'name', 'phone'],
        requiredShippingContactFields: ['postalAddress', 'email', 'name', 'phone']
      },
    }];
    instanceData = generateInstanceData(
      controllerEmitter,
      componentData,
      getPaymentOptions,
      getElement,
      setOptions,
      supportedInstruments
    );
    instanceData.waitBeforeShow = true;
    instanceData.options = applepayComponent.options;

    addHandleOptions(controllerListener, handleAppleOptions);

    return applepayComponent;
  }

  return {

    /**
     * Creates applepay component
     * @param {string} controllerId
     * @param {string} key
     * @param {object} options
     */
    createApplepayComponent: createApplepayComponent,

    setOptions: setOptions,
    getElement: getElement,
    getPaymentOptions: getPaymentOptions,
    handleUpdate: handleUpdate,
    handleCancel: handleCancel,
    handleAppleOptions: handleAppleOptions,
    handleValidateMerchant: handleValidateMerchant,
    mountApplepay: mountApplepay,
    onApplePayButtonClick: onApplePayButtonClick,
    getInstanceData: getInstanceData
  };

}