import { createSourceWithAdyen } from '../../../client/createSource';
import {
  sendAdyenAction,
} from '../../../client/createComponent';


/**
 * Method to handle the finger print response and prepare the payload for createSource adyen.
 * @param adyenResponseFingerprint
 * @param instanceData
 * @return {Promise<never>|*|Promise<T | never>|undefined}
 */
export function handleFingerPrintCreateSource(adyenResponseFingerprint, instanceData) {
  let clientSecret = instanceData.paymentServiceResponse.clientSecret;

  if(!clientSecret) {
    return Promise.reject('Cannot send data to paymentservice because required data is missing.');
  }

  const sourceRequest = {
    type: instanceData.paymentServiceResponse.type,
    [instanceData.paymentServiceResponse.type]: {
      fingerprint: adyenResponseFingerprint
    }
  };

  const clientSecretId = {
    clientSecret: clientSecret.split('_')
  };

  return createSourceWithAdyen(instanceData.controllerId, sourceRequest, clientSecretId)
    .then(response => {
      if(typeof response !== 'undefined' && response.source !== null && response.source.state === 'requires_action' && response.source.nextAction !== null) {
        sendAdyenAction(instanceData.componentId, instanceData.controllerId, response.source, instanceData.resolve);
        return response;
      } else {
        instanceData.resolve(response);
        return response;
      }
    });
}


