import {getComponentWindow} from './createComponent';
import postRobot from '../post-robot-wrapper';
import {getRedirectReceiverURL} from './url-utils';

export function sendHandleNextAction(controllerId, data) {

  const controllerWindow = getComponentWindow(controllerId);

  if (!controllerWindow) {
    throw new Error(`Unable to locate controller '${controllerId}'`);
  }

  if(!data) {
    throw new Error('You must provide data');
  }

  const returnUrl = getRedirectReceiverURL(controllerId);

  const dataToSend = Object.assign({}, data, {returnUrl: returnUrl});

  return postRobot.send(controllerWindow, 'authenticateSession', { nextActionData: dataToSend}).then(response => {
    return response.data;
  });
}