import {
  createComponent, destroy, generateComponentId,
  getComponentWindow, onEventHandler, unmount, update,
  registerComponentWithController, getComponentsBasePath
} from '../createComponent';
import {sanitizeOptionsForType} from '../../app/components/options';
import dataStore from '../dataStore';
import postRobot from '../../post-robot-wrapper';
import {config} from '../../app/components/config';
import {sendEventData} from '../../app/components/input-events';
import {
  getLocaleMessage,
} from '../../app/components/localization/localized-messages';
import {getButtonTextByLocale} from '../dropin/button-text';

let elements = {};
let fields = [];
let componentData = {};
let controllerEmitter;
let controllerListener;

const COMPONENT_TYPE = 'offlinerefund';

export function getElementsState() {
  return elements;
}

/**
 *
 * @param controllerId
 * @param key
 * @param options
 * @returns {{unmount: unmount, controllerId: (*|null), options: (*|*), destroy: destroy, update: update, id: string, parentNode: null, type: string, mount: mountElement, key: *, on: onEventHandler}}
 */
export function createOfflineRefund(controllerId, key, options) {
  const id = generateComponentId(COMPONENT_TYPE);

  const component = {
    id: id,
    key: key,
    type: COMPONENT_TYPE,
    parentNode: null,
    controllerId: controllerId,
    mount: mountElement,
    destroy: destroyElement,
    on: onEventHandler,
    options: sanitizeOptionsForType(options, COMPONENT_TYPE) || {},
    unmount: unmountElement,
    update: updateElement,
    window: Window
  };

  componentData = {
    componentType: COMPONENT_TYPE,
    componentId: id,
    controller: {
      id: controllerId,
      window: getComponentWindow(controllerId)
    },
    prevState: {
      empty: true,
      complete: false,
      error: null,
    },
    options: component.options,
    key: key
  };

  controllerEmitter = postRobot.client({ window: componentData.controller.window, domain: config.domain });
  controllerListener = postRobot.listener({window: componentData.controller.window, domain: config.domain});

  addHandleOptions(controllerListener, handleOptions);
  addHandleGetValue(controllerListener, componentData, elements, fields);

  return component;
}

function createDOMContainerUniqueId(controllerId, componentId) {
  return controllerId + '-' + componentId;
}

export function addContainerToDom(parentId, controllerId, componentId) {

  const uniqueIdForHTML = createDOMContainerUniqueId(controllerId, componentId);
  const pathToCheckmark = getComponentsBasePath() + '/offline-refund';
  const locale = componentData.instanceOptions.locale;
  const submitButtonText = getButtonTextByLocale(locale, 'submit');

  const structureHTML = `<div id="${componentId}" class="main">
    <div id="${uniqueIdForHTML}-offlineRefundForm">
      <div id="${uniqueIdForHTML}-content" class="content">
      </div>
      <div id="${uniqueIdForHTML}-error" class="DR-offline-refund-error"></div>
      <div className="buttongroup">
        <button id="${uniqueIdForHTML}-submitButton" type="button" class="DR-offline-refund-button">${submitButtonText}</button>
      </div>
    </div>
    <div id="${uniqueIdForHTML}-success" style="display: none" class="DR-offline-refund-success">
      <div>
        <img src="${pathToCheckmark}/checkmark.svg" style="height: 50px; width: 50px"/>
      </div>
    </div>
  </div>`;

  document.getElementById(parentId).innerHTML += structureHTML;

  document.getElementById(`${uniqueIdForHTML}-submitButton`).addEventListener('click', handleFormSubmit, false);

}

function getContainerElement() {
  return document.getElementById(componentData.componentId);
}

function alreadyRendered() {
  return getContainerElement() !== null;
}

function getError(element) {
  return element.fieldData.mandatory === true && element.empty === true;
}

export function addHandleGetValue(controllerListener, componentData, elements) {
  controllerListener.on('getComponentData' + componentData.componentId, () => {
    return Object.keys(elements).map( (key) => {
      const element = elements[key];
      return {
        id: element.component.id,
        type: element.elementType,
        error: getError(element)
      };
    });
  });
}

function addFieldDetailsToState(field, uniqueId) {
  fields.push({
    name: field.name,
    pattern: field.pattern,
    uniqueId: uniqueId,
    fieldData: field
  });
}

export function handleOptions(data) {

  const parentNodeId = componentData.parentNodeId;
  const componentId = componentData.componentId;

  componentData.instanceOptions = data.instanceOptions;
  componentData.options = data.options;
  componentData.fields = data.fields;
  componentData.refundToken = data.options.refundToken;

  const controllerId = componentData.controller.id;

  const fields = data.fields;

  if (fields.length === 0) {
    emitComponentReady();
    return;
  }

  if (alreadyRendered()) {
    destroySubElements();
    resetData();
    document.getElementById(parentNodeId).removeChild(document.getElementById(componentId));
  }

  addContainerToDom(parentNodeId, controllerId, componentId);

  const contentContainer = document.getElementById(createDOMContainerUniqueId(controllerId, componentId)+'-content');
  fields.forEach(field => {
    const fieldUniqueId = `${field.name}-${componentId}`;
    addFieldToDOM(contentContainer, field, componentId, fieldUniqueId);
    addFieldDetailsToState(field, fieldUniqueId);
  });

  elements = createElements(controllerId, componentData.key, data.options);

}

function addFieldToDOM(container, field, componentId, uniqueId) {
  const row = document.createElement('div');
  row.className = 'DR-offline-refund-row';

  const dataParent = document.createElement('div');
  dataParent.className = 'DR-offline-refund-fields';

  const displayName = field.mandatory ? field.displayName + '<span class="DR-Required">*</span>': field.displayName;

  const nameField = document.createElement('label');
  nameField.innerHTML = displayName;
  nameField.className = 'DR-offline-refund-label';

  const inputFieldParent = document.createElement('div');
  inputFieldParent.id = uniqueId;
  inputFieldParent.className = 'DR-offline-refund-parent';

  const errorMessage = document.createElement('div');
  errorMessage.className = 'DR-offline-refund-error';
  errorMessage.id = `${uniqueId}-error`;

  dataParent.appendChild(nameField);
  dataParent.appendChild(inputFieldParent);
  row.appendChild(dataParent);
  row.appendChild(errorMessage);

  container.appendChild(row);

}

/**
 *
 * @param controllerId
 * @param key
 * @returns {{cardcvv: {parentId: string, component}, cardnumber: {parentId: string, component}, cardexpiration: {parentId: string, component}}}
 */
export function createElements(controllerId, key, options) {

  fields.forEach(field => {
    const defaultOptions = {
      options: {
        placeholderText: ''
      },
      value: '',
      elementType: field.name,
      componentType: field.name,
      sendValue: false,
      isRequired: field.fieldData.mandatory === true,
      type: field.fieldData.type === 'numeric' ? 'number' : 'text'
    };

    if (typeof options.classes !== 'undefined') {
      defaultOptions.classes = options.classes;
    }

    if (typeof options.style !== 'undefined') {
      defaultOptions.style = options.style;
    }

    if (typeof field.fieldData.options !== 'undefined') {
      defaultOptions.items = field.fieldData.options;
    }

    const componentType = field.fieldData.type === 'select' ? 'dynamicselect' : 'dynamicfield';
    const accessibilityName = `offlinerefund-${field.name.toLowerCase()}`;

    elements[field.name] = {
      parentId: field.uniqueId,
      elementType: field.name,
      options: defaultOptions,
      focus: false,
      empty: true,
      complete: false,
      ready: false,
      fieldData: field.fieldData,
      component: createComponent(componentType, controllerId, key, defaultOptions, accessibilityName)
    };
  });

  const data = dataStore.get(key);
  Object.keys(elements).forEach(function (key) {
    const componentData = elements[key];

    data.components = Object.assign({}, data.components, { [componentData.component.id]: componentData.component.id });
    const component = componentData.component;

    registerComponentWithController(controllerId, component, componentData.options);

    component.on('ready', () => {handleFieldReady(componentData.elementType);});
    component.on('blur', (details) => {handleFieldBlur(componentData.elementType, details);});
    component.on('focus', (details) => {handleFieldFocus(componentData.elementType, details);});
    component.on('change', (details) => {handleFieldChange(componentData.elementType, details);});
  });

  return elements;
}


export function mountElement(parentNodeId) {
  const parent = document.getElementById(parentNodeId);
  componentData.window = window;
  const data = dataStore.get(this.key);

  data.components[this.type] = { 'parent': parent, 'options': sanitizeOptionsForType(this.options, this.type) };
  dataStore.set(this.key, data);

  this.parentNode = parent;

  const componentId = this.id;

  componentData.parentNodeId = parentNodeId;

  return controllerEmitter.send('mountClientComponent', {
    componentId: componentData.componentId,
    componentType: componentData.componentType
  }).then((response) => {
    handleOptions(response.data);
    mountElements(componentId, elements);
  });

}

function mountElements(parentNode, components) {
  Object.keys(components).forEach(function (key) {
    const componentData = components[key];
    const elementDivId = componentData.parentId;
    componentData.component.mount(elementDivId);
  });
}

export function unmountElement() {
  Object.keys(elements).forEach(function (elementType) {
    if (elements[elementType].component.parentNode) {
      elements[elementType].component.unmount();
    }
  });
  unmount.call(this);
}

function updateElement(options) {
  update.call(this, options);
  Object.keys(elements).forEach(function (elementType) {
    elements[elementType].component.update(options);
  });
}

function destroySubElements() {
  Object.keys(elements).forEach(function (elementType) {
    elements[elementType].component.destroy();
  });
}

function resetData() {
  elements = {};
  fields = [];
}

function destroyElement() {
  destroySubElements();
  resetData();
  return destroy.call(this);
}

function updateElementsState(elements, element, details) {
  elements[element] = Object.assign(elements[element], details);
}

export function handleFieldChange(elementType, details) {
  updateElementsState(elements, elementType, details);
  //handleOnChangeEvent(elements, elementType, details, componentData);
}

export function handleFieldBlur(elementType) {
  updateElementsState(elements, elementType, {focus: false});
  //handleOnBlurEvent(elements, elementType, details, componentData);
}

export function handleFieldFocus(elementType) {
  updateElementsState(elements, elementType, {focus: true});
  //handleOnFocusEvent(elements, elementType, details, componentData);
}

export function handleFieldReady(elementType) {
  updateElementsState(elements, elementType, {ready: true});
  handleReady(elements);
}

function handleReady(elements) {
  const elementsNotReady = Object.keys(elements).filter( key =>elements[key].ready !== true);
  if(elementsNotReady.length === 0 && !componentData.isUpdate) {
    emitComponentReady();
  }
}

export function emitComponentReady() {
  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'ready', {});
}


/**
 * Adds handleOptions to component
 * @param controllerListener
 * @param {Function} handleOptions
 */
export function addHandleOptions(controllerListener, handleOptions) {
  controllerListener.on('offlineRefundOptions'+componentData.componentId, (event) => {
    handleOptions(event.data);
    mountElements(componentData.componentId, elements);
  });
}

export function handleFormSubmit(e) {
  e.preventDefault();

  const refundToken = getComponentData().refundToken;

  processRefund(getComponentData().controller.id, refundToken);

}

/**
 * getComponentData returns componentData
 * @returns {{componentType: string, prevState: {}, controller: {id: (string|*)}, componentId: (string|*)}}
 */
export function getComponentData() {
  return componentData;
}

function getMissingDataMessage() {
  const locale = componentData.instanceOptions.locale;
  return getLocaleMessage(locale, 'offlineRefundInsufficientData');
}

function getErrorToDisplay(response) {
  if (typeof response.errors !== 'undefined' && response.errors.length > 0 && response.errors[0].code === 'missing_parameter') {
    return getMissingDataMessage();
  } else if (typeof response.errors !== 'undefined' && response.errors.length > 0 && response.errors[0].code === 'unauthorized') {
    return  response.errors[0].message;
  } else {
    return  getMissingDataMessage();
  }
}

function getSubmitButtonInDOM(controllerId, componentId) {
  return document.getElementById(`${createDOMContainerUniqueId(controllerId, componentId)}-submitButton`);
}

function getFormElement(controllerId, componentId) {
  return document.getElementById(`${createDOMContainerUniqueId(controllerId, componentId)}-offlineRefundForm`);
}

function getSuccessElement(controllerId, componentId) {
  return document.getElementById(`${createDOMContainerUniqueId(controllerId, componentId)}-success`);
}

function getErrorElement(controllerId, componentId) {
  return document.getElementById(`${createDOMContainerUniqueId(controllerId, componentId)}-error`);
}

export function processRefund(controllerId, refundToken) {

  const controllerWindow = getComponentWindow(controllerId);

  // Send message to Controller Frame to call createSource
  postRobot.send(controllerWindow, 'processRefund', { refundToken: refundToken })
    .then(() => {

      getFormElement(controllerId, componentData.componentId).style.display = 'none';
      getSuccessElement(controllerId, componentData.componentId).style.removeProperty('display');

      const eventData = {
        complete: true
      };

      getSubmitButtonInDOM(controllerId, componentData.componentId).disabled = true;
      getErrorElement(controllerId, componentData.componentId).innerText = '';

      sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'change', eventData);
    }).catch((error) => {

      const isMissingData = error.errorCode === 'REQUIRED_FIELDS_NOT_ENTERED';
      if (isMissingData) {
        getErrorElement(controllerId, componentData.componentId).innerText = getMissingDataMessage() || 'Missing information or incorrect values submitted. Please try again.';

        const eventData = {
          complete: false
        };
        sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'change', eventData);

        return;
      }

      getErrorElement(controllerId, componentData.componentId).innerText = getErrorToDisplay(error);

      const eventData = {
        complete: false
      };
      sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'change', eventData);
    });
}