import {getDelayedPaymentInstructionsLocalizedStrings} from './delayedPaymentInstructions-localization-data';

export function getBPayContent(source, locale) {
  const translationsNeeded = [
    'amount',
    'billerCode',
    'bPayBoxInstructions',
    'bPayInstructions',
    'bPayInstructionsMoreDetail',
    'reference',
  ];
  const {
    amount,
    billerCode,
    reference,
    bPayBoxInstructions,
    bPayInstructions,
    bPayInstructionsMoreDetail
  } = getDelayedPaymentInstructionsLocalizedStrings(translationsNeeded, locale);

  return `
    <style type="text/css">
        #DR-bPayInfo {color:#022c6a; border: 2px solid #022c6a; width:285px; padding:10px 10px 0 10px; font:normal 12px Helvetica, Arial, san-serif}
        #DR-bPayInfo p {clear:both; padding:10px 0; margin:0}
        #DR-bPayInfo img {float:left; margin-right:5px;}
        #DR-bPayCustomerInfo {color:red; border:2px solid #022c6a; padding:0; font:bold 16px Helvetica, Arial, san-serif; float:left; width:225px; margin:0; clear:none; height:66px; line-height:11px;}
        #DR-bPayCustomerInfo p {margin:15px 0px 0px 5px; padding:0;}
        #DR-bPayCustomerInfo p span {color:#022c6a;}
        .DR-bPayMax {font-weight:bold;}
        .DR-bPayMin {font-size:85%;}
        .DR-bPayReg {font-size:9px;}
    </style>
    <p>${bPayInstructions}</p>
    <p id="DR-bPayAmount">
        <strong>${amount}</strong> ${source.amount} ${source.currency}
    </p>
        
    <div id="DR-bPayInfo">
        <img src="https://ui1.img.digitalrivercontent.net/Storefront/images/paymentMethodLogos/paymentInstructions/bpay_logo.png" alt="BPay Logo"/>
        <div id="DR-bPayCustomerInfo">
            <p><strong>${billerCode}</strong> ${source.bPay.billerId}</p>
            
            <p><strong>${reference}</strong> ${source.bPay.referenceId}</p>
            
        </div>
        <p>${bPayBoxInstructions}</p>
    </div>
    <p>${bPayInstructionsMoreDetail}</p>
    
  `;
}