import postRobotDefault from 'post-robot';

const timeout = 20000;

export function listener(object) {
  return {
    on: function (name, callback) {
      return on(name, object, callback);
    }
  };
}

export function client(object) {
  return {
    send: function (name, data, callback) {
      return send(object.window, name, data, { domain: object.domain } , callback);
    }
  };
}

export function send(window, name, data) {
  return postRobotDefault.send(window, name, data, { timeout: timeout } );
}

export function on(name, data, callback) {
  return postRobotDefault.on(name, data, callback);
}

export default {
  listener: listener,
  client: client,
  on: on,
  send: send
};