import {
  createComponent,
  destroy,
  generateComponentId,
  getComponentWindow,
  onEventHandler,
  registerComponentWithController,
  unmount
} from '../../../client/createComponent';
import {sanitizeOptionsForType} from '../options';
import dataStore from '../../../client/dataStore';
import {handleEvent, sendEventData} from '../input-events';
import postRobot from '../../../post-robot-wrapper';
import {config} from '../config';
import {isShallowEquivalent} from '../utils';
import {getLocaleMessage} from '../localization/localized-messages';
import {setElementErrorMessage} from '../../../utils';

let elements = {};
let componentData = {};
let controllerEmitter;

function buildLogosDiv(logos) {

  const logoImages = logos.map(logo => {
    return `<img class="DR-Card-Brand" id="DR-Card-Brand-${logo.code}" src="${logo.url}" alt="${logo.code}" />`;
  });

  return '<div class="DR-Card-Brands">'+logoImages.join('')+'</div>';
}

function removeSelectedCSSClass() {
  const brandImages = document.querySelectorAll('.DR-Card-Brand');
  Array.from(brandImages).forEach(brandImage => {
    brandImage.classList.remove('DR-Card-Brand-Selected');
  });
}

function setSelectedCSSClassOnBrand(brandCode) {
  if (brandCode.toLowerCase() !== 'unknown') {
    document.getElementById(`DR-Card-Brand-${brandCode}`).classList.add('DR-Card-Brand-Selected');
  }
}

function setSelectedBrand(brandCode) {

  removeSelectedCSSClass();

  setSelectedCSSClassOnBrand(brandCode);
}

function createDom(parentNodeId, uniqueId, locale, logos) {
  document.getElementById(parentNodeId).innerHTML = `<div id="${uniqueId}" class="DR-flex-column">
        ${typeof logos !== 'undefined' ? buildLogosDiv(logos) : ''}
        <label>${getLocaleMessage(locale,'cardNumber',) || 'Card Number'}</label>
        <div id="DRCardNumber-${uniqueId}" class="DR-card-number"></div>
        <div id="DRCardNumber-${uniqueId}-error" class="DR-error-message"></div>
        <div class="DR-flex-row">
          <div class="DR-flex-column DR-width-half DR-card-expiration">
            <label>${getLocaleMessage(locale,'cardExpirationDate',) || 'Expiration Date'}</label>
            <div id="DRCardExpiration-${uniqueId}"></div>
            <div id="DRCardExpiration-${uniqueId}-error" class="DR-error-message"></div>
          </div>
          <div class="DR-flex-column DR-width-half DR-card-cvv">
            <label>${getLocaleMessage(locale,'cardSecurityCode',) || 'Card Security Code'}</label>
            <div id="DRCardCvv-${uniqueId}"></div>
            <div id="DRCardCvv-${uniqueId}-error" class="DR-error-message"></div>
          </div>
        </div>
    </div>`;
}

/**
 *
 * @param controllerId
 * @param key
 * @param options
 * @returns {{unmount: unmount, controllerId: (*|null), options: (*|*), destroy: destroy, update: update, id: string, parentNode: null, type: string, mount: mountCreditCard, key: *, on: onEventHandler}}
 */
export function createCreditCard(controllerId, key, options) {
  const type = 'creditcard';
  const id = generateComponentId(type);

  const creditcardComponent = {
    id: id,
    key: key,
    type: type,
    parentNode: null,
    controllerId: controllerId,
    mount: mountCreditCard,
    destroy: destroyCreditCard,
    on: onEventHandler,
    options: sanitizeOptionsForType(options, type) || {},
    unmount: unmountCreditCard,
    update: updateCreditCard,
    window: Window
  };

  const creditcardElements = createElements(controllerId, key, options);

  // we don't want the classes on the parent div for this one, we just need them to pass down to child components
  creditcardComponent.options.classes = {};
  elements = Object.assign(elements, creditcardElements);

  componentData = {
    componentType: type,
    componentId: id,
    controller: {
      id: controllerId,
      window: getComponentWindow(controllerId)
    },
    prevState: {
      empty: true,
      complete: false,
      error: null,
    },
    options: creditcardComponent.options
  };
  controllerEmitter = postRobot.client({ window: componentData.controller.window, domain: config.domain });

  return creditcardComponent;
}


/**
 *
 * @param controllerId
 * @param key
 * @param options
 * @returns {{cardcvv: {parentId: string, component}, cardnumber: {parentId: string, component}, cardexpiration: {parentId: string, component}}}
 */
export function createElements(controllerId, key, options) {

  const optionsWithoutLogos = Object.assign({}, options);
  delete optionsWithoutLogos.logos;

  const data = dataStore.get(key);
  const elements = {
    cardnumber: {
      parentId: 'DRCardNumber',
      options: {
        placeholderText: optionsWithoutLogos.cardNumberPlaceholderText || '1234 5678 9012 3456'
      },
      focus: false,
      empty: true,
      complete: false,
    },
    cardexpiration: {
      parentId: 'DRCardExpiration',
      options: {
        placeholderText: optionsWithoutLogos.cardExpirationPlaceholderText || 'MM/YY'
      },
      focus: false,
      empty: true,
      complete: false
    },
    cardcvv: {
      parentId: 'DRCardCvv',
      options: {
        placeholderText: optionsWithoutLogos.cardCvvPlaceholderText || '123'
      },
      focus: false,
      empty: true,
      complete: false
    }
  };

  Object.keys(elements).forEach(function (elementType) {
    elements[elementType].options = Object.assign(elements[elementType].options, optionsWithoutLogos);
    elements[elementType].component = createComponent(elementType, controllerId, key, elements[elementType].options);
    data.components = Object.assign({}, data.components, { [elementType]: elements[elementType].component.id });

    registerComponentWithController(controllerId, elements[elementType].component, elements[elementType].options);

    elements[elementType].component.on('ready', () => {handleFieldReady(elements, elementType);});
    elements[elementType].component.on('blur', () => {handleFieldBlur(elements, elementType);});
    elements[elementType].component.on('change', (details) => {handleFieldChange(elements, elementType, details);});
  });
  return elements;
}


export function mountCreditCard(parentNodeId) {
  let parent = document.getElementById(parentNodeId);
  componentData.window = window;
  let data = dataStore.get(this.key);
  data.components[this.type] = { 'parent': parent, 'options': sanitizeOptionsForType(this.options, this.type) };
  dataStore.set(this.key, data);

  this.parentNode = parent;

  const componentId = this.id;

  createDom(parentNodeId, componentId, data.instanceOptions.locale, this.options.logos);

  mountElements(componentId, elements);

  return controllerEmitter.send('mountClientComponent', {
    componentId: componentData.componentId,
    componentType: componentData.componentType
  });
}

function mountElements(parentNode, elements) {
  Object.keys(elements).forEach(function (elementType) {
    const elementDivId = elements[elementType].parentId + '-' +parentNode;
    elements[elementType].component.mount(elementDivId);
  });
}

function unmountCreditCard() {
  Object.keys(elements).forEach(function (elementType) {
    if(elements[elementType].component.parentNode) {
      elements[elementType].component.unmount();
    }
    elements[elementType].ready = false;
  });
  unmount.call(this);
}

function updateCreditCard(options) {
  Object.keys(elements).forEach(function (elementType) {
    elements[elementType].component.update(options);
  });
  // we do not want classes applied to the container div, so we strip them before update
  if(options.hasOwnProperty('classes')) {
    options.classes = {};
  }
}

function destroyCreditCard() {
  Object.keys(elements).forEach(function (elementType) {
    elements[elementType].component.destroy();
    elements[elementType].ready = false;
  });
  destroy.call(this);
}

function getNextElement(elementType) {
  if (elementType === 'cardnumber') {
    return 'cardexpiration';
  } else if (elementType === 'cardexpiration') {
    return 'cardcvv';
  }
}

// handlers for field component events
function handleFieldChange(elements, elementType, details) {
  const nextElement = getNextElement(elementType);
  if(details.complete && typeof nextElement !== 'undefined') {
    elements[nextElement].component.focus();
  }
  updateCard(elements, elementType, details);
  handleCardFieldEvent(elements, 'change');
}

function handleFieldBlur(elements, elementType) {
  updateCard(elements, elementType, {focus: false});
  handleCardFieldEvent(elements, 'blur', {});
}

function handleFieldReady(elements, elementType) {
  updateCard(elements, elementType, {ready: true});
  handleReady(elements);
}

function setErrorMessages(uniqueId, elements) {
  Object.keys(elements).forEach(function (elementType) {
    const error = getError(elementType);
    if (error) {
      const parentId = elements[elementType].parentId;
      setElementErrorMessage(`${parentId}-${uniqueId}-error`, error.message);
    }
  });
}

function resetErrorMessages(uniqueId, elements) {
  Object.keys(elements).forEach(function (elementType) {
    const parentId = elements[elementType].parentId;
    if(!elements[elementType].empty) {
      setElementErrorMessage(`${parentId}-${uniqueId}-error`, '');
    }
  });
}


function hasBrandChanged(brand) {
  return componentData.prevState.brand !== brand;
}

function handleCardFieldEvent(elements, eventType) {
  componentData.empty = isCardEmpty(elements);
  componentData.complete = isCardComplete(elements);
  componentData.error = isCardError(elements);

  const dataToSend = {
    empty: componentData.empty,
    complete: componentData.complete,
    error: componentData.error,
    brand: elements.cardnumber.brand
  };

  if (typeof elements.cardnumber.brand !== 'undefined' && hasBrandChanged(elements.cardnumber.brand)) {
    setSelectedBrand(elements.cardnumber.brand);
  }

  handleCreditCardEvent(componentData, eventType, dataToSend);

  resetErrorMessages(componentData.componentId, elements);

  if(componentData.error) {
    setErrorMessages(componentData.componentId, elements);
  }

}

// handlers for creditcard component events
function handleReady(elements) {
  let numberReady = elements.cardnumber.ready === true;
  let expirationReady = elements.cardexpiration.ready === true;
  let cvvReady = elements.cardcvv.ready === true;
  if(numberReady && expirationReady && cvvReady ) {
    handleEvent(componentData, 'ready');
  }
}

export function handleCreditCardEvent(componentData, eventType, event) {
  const eventData = {
    'empty': event.empty,
    'complete': event.complete,
    'error':  event.error,
    'brand': event.brand || 'unknown'
  };
  //For event type "change" Fire only if the event data has changed from the previous event
  if (eventType === 'change' && isShallowEquivalent(componentData.prevState, eventData)) {
    return false;
  }

  //If the user blurs, but something has changed, send a change event
  if (eventType === 'blur' && componentData.prevState !== {} && !isShallowEquivalent(componentData.prevState, eventData)) {
    sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'change', eventData);
  }

  componentData.prevState = Object.assign({}, eventData);

  if (eventType === 'blur') {
    // we only send blur for this component if something has changed
    return false;
  }

  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, eventType, eventData);
  return true;
}

// checks for creditcard component status
function isCardComplete(elements) {
  return elements.cardnumber.complete && elements.cardexpiration.complete && elements.cardcvv.complete;
}

function isCardEmpty(elements) {
  return elements.cardnumber.empty && elements.cardexpiration.empty && elements.cardcvv.empty;
}

function isCardError(elements) {
  let errors = [];
  Object.keys(elements).forEach(function (elementType) {
    const error = getError(elementType);
    if(error) errors.push(error);
  });
  errors = errors.length > 0 ? errors : null;
  return errors;
}

function getError(elementType) {
  return elements[elementType].hasOwnProperty('error') && elements[elementType].error ? elements[elementType].error : null;
}

// utility functions
function updateCard(elements, element, details) {
  elements[element] = Object.assign(elements[element], details);
}