import data from './data.json';

export function getTaxSpecificStrings(country, locale) {
  const formattedLocale = locale.replace('-','_');
  const specificStrings = data.taxIdSpecificStrings[country][formattedLocale];
  if (typeof specificStrings === 'undefined') {
    const stringsForCountry = data.taxIdSpecificStrings[country];
    const defaultLocale = Object.keys(stringsForCountry)[0];
    // eslint-disable-next-line no-console
    console.warn('Locale not supported. Using Default.', defaultLocale);
    return stringsForCountry[defaultLocale];
  }
  return data.taxIdSpecificStrings[country][formattedLocale];
}
export function getLocalizedStrings(locale) {
  const formattedLocale = locale.replace('-','_');
  return data.localizedStrings[formattedLocale];
}