const restrictedSymbolPattern = new RegExp(/["'`{};<>%]/);
const paymentApiRestrictedSymbolPattern = new RegExp(/["'`{};<>]/);

function hasInvalidType(input) {
  return typeof input !== 'string' && typeof input !== 'number' && typeof input !== 'boolean';
}

/**
 * This function only removes illegal characters as defined above.
 * It isn't intended to validate the Options Schema nor validate CSS.
 * @param {string} input
 */

export function sanitizeInput(input) {
  if (hasInvalidType(input)) {
    return null;
  }

  let result = restrictedSymbolPattern.test(input);
  if (result === true) {
    return null;
  }

  return input;
}

export function sanitizeInputForPaymentApi(input) {
  if (hasInvalidType(input)) {
    return null;
  }

  let result = paymentApiRestrictedSymbolPattern.test(input);
  if (result === true) {
    return sanitizeInputForPaymentApi(input.replace(paymentApiRestrictedSymbolPattern, ''));
  }

  return input;
}