import postRobot from '../post-robot-wrapper';
import { createComponent } from './createComponent';
import { processEvent, processNonCreditCardEvents } from './event-middleware';

/**
 * Creates controller
 * @param {HTMLElement} domNode
 * @param {string} type
 * @returns {{unmount, controllerId, clear, destroy, blur, focus, update, parentNode, type, mount, options, id, key, on}}
 */
export function createController(domNode, type) {
  const controller = createComponent(type);
  controller.mount(domNode);
  return controller;
}

/**
 * Registers controller events
 * @param {string} key
 * @param {Window} controllerWindow
 * @param {string} domain
 */
export function registerControllerEvents(key, controllerWindow, domain) {
  postRobot.on('componentEventToClient', { window: controllerWindow, domain: domain }, (event) => {
    const {componentType, componentId, eventData, eventType} = event.data;
    if (eventType === 'googlepay' || eventType === 'applepay' || eventType === 'paypal') { // TODO Look into this.... does this ever run. eventType wont ever be these values....? Should be componentType
      processNonCreditCardEvents(key, componentType, eventType, eventData);
    } else {
      processEvent(key, componentType, componentId, eventType, eventData);
    }
  });
}