import {
  createComponent, destroy, generateComponentId,
  getComponentWindow, onEventHandler, unmount, update,
  registerComponentWithController
} from '../createComponent';
import {sanitizeOptionsForType} from '../../app/components/options';
import dataStore from '../dataStore';
import postRobot from '../../post-robot-wrapper';
import {config} from '../../app/components/config';
import {sendEventData} from '../../app/components/input-events';
import {getMSTSLocalizedStrings} from './msts-localization-data';
import {setElementErrorMessage} from '../../utils';

let elements = {};
let fields = [];
let componentData = {};
let controllerEmitter;
let controllerListener;

const COMPONENT_TYPE = 'msts';

/**
 *
 * @param controllerId
 * @param key
 * @param options
 * @returns {{unmount: unmount, controllerId: (*|null), options: (*|*), destroy: destroy, update: update, id: string, parentNode: null, type: string, mount: mountElement, key: *, on: onEventHandler}}
 */
export function createMSTS(controllerId, key, options) {
  const id = generateComponentId(COMPONENT_TYPE);

  const component = {
    id: id,
    key: key,
    type: COMPONENT_TYPE,
    parentNode: null,
    controllerId: controllerId,
    mount: mountElement,
    destroy: destroyElement,
    on: onEventHandler,
    options: sanitizeOptionsForType(options, COMPONENT_TYPE) || {},
    unmount: unmountElement,
    update: updateElement,
    window: Window
  };

  componentData = {
    componentType: COMPONENT_TYPE,
    componentId: id,
    controller: {
      id: controllerId,
      window: getComponentWindow(controllerId)
    },
    prevState: {
      empty: true,
      complete: false,
      error: null,
    },
    options: component.options,
    key: key
  };

  controllerEmitter = postRobot.client({window: componentData.controller.window, domain: config.domain});
  controllerListener = postRobot.listener({window: componentData.controller.window, domain: config.domain});

  addHandleGetValue(controllerListener, componentData, elements);

  return component;
}

export function addContainerToDom(parentId, componentId) {
  document.getElementById(parentId).innerHTML += `<div id="${componentId}" class="DR-msts-container"></div>`;
}


function getContainerElement() {
  return document.getElementById(componentData.componentId);
}


function addFieldDetailsToState(parsedSchema, localeDataForSchema) {
  fields.push({
    name: parsedSchema.title,
    pattern: parsedSchema.pattern,
    localeDataForSchema: localeDataForSchema,
    type: COMPONENT_TYPE,
    textArea: parsedSchema.textArea,
    maxLength: parsedSchema.maxLength
  });
}


const individualTaxIdentifiersSchemas = [
  {
    title: 'poNumber',
    textArea: false
  },
  {
    title: 'notes',
    textArea: true,
    maxLength: '500'
  }
];

export function handleOptions(data) {

  const locale = data.instanceOptions.locale;
  const parentNodeId = componentData.parentNodeId;
  const componentId = componentData.componentId;

  componentData.instanceOptions = data.instanceOptions;
  componentData.options = data.options;

  addContainerToDom(parentNodeId, componentId);

  const fieldsParent = document.createElement('div');
  fieldsParent.id = componentId + 'fieldsParent';
  individualTaxIdentifiersSchemas.forEach(schema => {
    const localeDataForSchema = getMSTSLocalizedStrings(schema.title, locale);
    addFieldToDOM(fieldsParent, schema.title, localeDataForSchema, componentId);
    addFieldDetailsToState(schema, localeDataForSchema);
  });

  getContainerElement().appendChild(fieldsParent);

  const controllerId = componentData.controller.id;

  elements = createElements(controllerId, componentData.key, data.options);

}

function addFieldToDOM(container, fieldType, localeData, componentId) {

  const row = document.createElement('div');
  row.className = 'DR-row DR-msts-field';

  const dataParent = document.createElement('div');
  dataParent.className = 'DR-fieldGroup';

  const nameField = document.createElement('label');
  nameField.innerHTML = localeData;
  nameField.className = 'DR-field-label';

  const inputFieldParent = document.createElement('div');
  inputFieldParent.id = `${fieldType}-${componentId}`;
  inputFieldParent.className = 'DR-field-parent';

  const errorMessage = document.createElement('div');
  errorMessage.className = 'DR-error-message';
  errorMessage.id = `${fieldType}-${componentId}-error`;

  dataParent.appendChild(nameField);
  dataParent.appendChild(inputFieldParent);
  row.appendChild(dataParent);
  row.appendChild(errorMessage);

  container.appendChild(row);

}

export function createElements(controllerId, key, options) {
  fields.forEach(field => {
    const defaultOptions = {
      options: {
        placeholderText: ''
      },
      focus: false,
      empty: true,
      complete: false,
      ready: false,
      value: '',
      elementType: field.name,
      componentType: field.name,
      textArea: field.textArea,
    };

    const accessibilityName = `msts-${field.name.toLowerCase()}`;
    if (typeof options.classes !== 'undefined') {
      defaultOptions.classes = options.classes;
    }

    if (typeof options.style !== 'undefined') {
      defaultOptions.style = options.style;
    }

    if (typeof field.maxLength !== 'undefined') {
      defaultOptions.maxLength = field.maxLength;
    }

    elements[field.name] = {
      parentId: field.name,
      elementType: field.name,
      options: defaultOptions,
      component: createComponent('dynamicfield', controllerId, key, defaultOptions, accessibilityName)
    };
  });

  const data = dataStore.get(key);
  Object.keys(elements).forEach(function (key) {
    const componentData = elements[key];

    data.components = Object.assign({}, data.components, {[componentData.component.id]: componentData.component.id});
    const component = componentData.component;

    registerComponentWithController(controllerId, component, componentData.options);

    component.on('ready', () => {
      handleFieldReady(componentData.elementType);
    });
    component.on('blur', (details) => {
      handleFieldBlur(componentData.elementType, details);
    });
    component.on('focus', (details) => {
      handleFieldFocus(componentData.elementType, details);
    });
    component.on('change', (details) => {
      handleFieldChange(componentData.elementType, details);
    });
  });

  return elements;
}


export function mountElement(parentNodeId) {
  const parent = document.getElementById(parentNodeId);
  componentData.window = window;
  const data = dataStore.get(this.key);

  data.components[this.type] = {'parent': parent, 'options': sanitizeOptionsForType(this.options, this.type)};
  dataStore.set(this.key, data);

  this.parentNode = parent;

  const componentId = this.id;

  componentData.parentNodeId = parentNodeId;

  return controllerEmitter.send('mountClientComponent', {
    componentId: componentData.componentId,
    componentType: componentData.componentType
  }).then((response) => {
    handleOptions(response.data);
    mountElements(componentId, elements);
  });

}

function mountElements(parentNode, components) {
  Object.keys(components).forEach(function (key) {
    const componentData = components[key];
    const elementDivId = componentData.parentId + '-' + parentNode;
    componentData.component.mount(elementDivId);
  });
}

export function unmountElement() {
  Object.keys(elements).forEach(function (elementType) {
    if (elements[elementType].component.parentNode) {
      elements[elementType].component.unmount();
    }
  });
  return unmount.call(this);
}

function updateElement(options) {
  update.call(this, options);
  Object.keys(elements).forEach(function (elementType) {
    elements[elementType].component.update(options);
  });
}

function destroySubElements() {
  Object.keys(elements).forEach(function (elementType) {
    elements[elementType].component.destroy();
  });
}

function resetData() {
  elements = {};
  fields = [];
}

function destroyElement() {
  destroySubElements();
  resetData();
  return destroy.call(this);
}

function updateElementsState(elements, element, details) {
  elements[element] = Object.assign(elements[element], details);
}

export function handleFieldChange(elementType, details) {
  updateElementsState(elements, elementType, details);
  handleOnChangeEvent(elements, elementType, details, componentData);
}

export function handleFieldBlur(elementType, details) {
  updateElementsState(elements, elementType, {focus: false});
  handleOnBlurEvent(elements, elementType, details, componentData);
}

export function handleFieldFocus(elementType, details) {
  updateElementsState(elements, elementType, {focus: true});
  handleOnFocusEvent(elements, elementType, details, componentData);
}

export function handleFieldReady(elementType) {
  updateElementsState(elements, elementType, {ready: true});
  handleReady(elements);
}

function setErrorMessages(uniqueId, elements) {
  Object.keys(elements).forEach(function (elementType) {
    const error = getError(elementType);
    if (error) {
      const parentId = elements[elementType].parentId;
      setElementErrorMessage(`${parentId}-${uniqueId}-error`, error.message);
    }
  });
}

function resetErrorMessages(uniqueId, elements) {
  Object.keys(elements).forEach(function (elementType) {
    const parentId = elements[elementType].parentId;
    setElementErrorMessage(`${parentId}-${uniqueId}-error`, '');
  });
}

function handleOnChangeEvent(elements, elementType, eventData, componentData) {

  eventData[COMPONENT_TYPE] = {
    value: elements[elementType].value,
    type: elementType,
  };

  eventData.elementType = componentData.elementType;

  delete eventData.value;

  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'change', eventData);

  resetErrorMessages(componentData.componentId, elements);

  if (eventData.error) {
    setErrorMessages(componentData.componentId, elements);
  }

}

function handleOnBlurEvent(elements, eventType, eventData, componentData) {
  eventData.elementType = componentData.elementType;
  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'blur', eventData);
}

function handleOnFocusEvent(elements, eventType, eventData, componentData) {
  eventData.elementType = componentData.elementType;
  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'focus', eventData);
}

function handleReady(elements) {
  const elementsNotReady = Object.keys(elements).filter(key => elements[key].ready !== true);
  if (elementsNotReady.length === 0 && !componentData.isUpdate) {
    emitComponentReady();
  }
}

export function emitComponentReady() {
  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'ready', {});
}


function getError(elementType) {
  return elements[elementType].hasOwnProperty('error') && elements[elementType].error ? elements[elementType].error : null;
}

export function addHandleGetValue(controllerListener, componentData, elements) {
  controllerListener.on('getMstsComponentData' + componentData.componentId, () => {
    return {
      poNumber: elements['poNumber'].value,
      notes: elements['notes'].value
    };
  });
}
