import {getLogoDetails} from '../create-dropin';

export function getIconImage(paymentMethodResponse, storedPaymentMethod) {

  if (typeof storedPaymentMethod.brand !== 'undefined') {
    return getLogoDetails().find(logo => logo.code.toLowerCase() === storedPaymentMethod.brand.toLowerCase()).url;
  }

  if (typeof storedPaymentMethod.iconUrl !== 'undefined') {
    return storedPaymentMethod.iconUrl;
  }

  const typeToSearch = isStoreCredit(storedPaymentMethod.type) ? 'creditCard' : storedPaymentMethod.type;

  const paymentMethod = paymentMethodResponse.paymentMethods.find(pm => pm.type === typeToSearch && typeof pm.images !== 'undefined' && pm.isStored !== true);

  if (typeof paymentMethod === 'undefined') {
    return '';
  }

  return paymentMethod.images.iconImage;

}

function removeOnClick(availablePaymentMethod, configuration) {
  runOnUpdateRequired(availablePaymentMethod, configuration, 'remove');
}

function getRemoveButtonClass(availablePaymentMethod) {
  return 'DR-' + availablePaymentMethod.type + '-' + availablePaymentMethod.id;
}

export function getAddedButton(availablePaymentMethod, configuration) {
  const storedAddedDiv = document.createElement('div');
  if (availablePaymentMethod.added) {
    const removeDiv = document.createElement('button');
    removeDiv.className = 'DR-stored-remove ' + getRemoveButtonClass(availablePaymentMethod);
    removeDiv.innerText = 'Remove';
    removeDiv.addEventListener('click', (event) => {
      event.preventDefault();
      event.stopPropagation();
      removeOnClick(availablePaymentMethod, configuration);
    });
    storedAddedDiv.appendChild(removeDiv);
    return storedAddedDiv;
  } else {
    return storedAddedDiv;
  }
}

export function formatAmountBasedOnCurrency(amount, currency) {

  const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: currency,
  });

  return formatter.format(amount);
}

export function runOnUpdateRequired(paymentMethodFromAPI, configuration, event) {

  if (typeof configuration.onUpdateRequired === 'undefined') {
    return;
  }

  const paymentMethod = Object.assign({}, paymentMethodFromAPI.storedPaymentMethod);
  paymentMethod.action = event;

  const storedPaymentMethodsToUse = configuration.onUpdateRequired({
    'selectedPaymentMethod': paymentMethod,
    'storedPaymentMethods': configuration.options.storedPaymentMethods.filter(spm => isStoreCredit(spm.type))
  });

  if (typeof storedPaymentMethodsToUse !== 'undefined') {
    paymentMethodFromAPI.refreshCallback({
      options: {
        storedPaymentMethods: storedPaymentMethodsToUse
      }
    });
  }
}

export function isStoredPaymentMethod(paymentMethod) {
  return paymentMethod.isStored === true;
}

export function isStoreCredit(type) {
  return type === 'storeCredit';
}
