'use strict';
import axios from 'axios';

export const paymentServiceRequest = (data, apiKey, paymentApiUrl, applicationHeader) => {
  const options = {
    maxRedirects: 0,
    responseType: 'json',
    timeout: 15000,
    headers: {
      'Content-Type': 'application/json',
      'version': 'new',
      'Authorization' : generateAuthHeader(apiKey),
      'x-debug-drjs-application' : applicationHeader,
    }
  };

  const url = paymentApiUrl !== undefined ? paymentApiUrl : process.env.PAYMENT_API_URL;  //eslint-disable-line no-undef

  return axios.post(url, data, options);
};

export const paymentServiceGetRequest = (apiKey, paymentApiUrl) => {

  const options = {
    maxRedirects: 0,
    responseType: 'json',
    timeout: 15000,
    headers: {
      'Content-Type': 'application/json',
      'Authorization' : generateAuthHeader(apiKey)
    }
  };

  return axios.get(paymentApiUrl, options);

};

const encodeApiKey = (apiKey) => {
  return btoa(apiKey);
};

const generateAuthHeader = (apiKey) => {

  if (apiKey.startsWith('pk_')) {
    return 'bearer ' + apiKey;
  } else {
    return 'basic ' + encodeApiKey(apiKey);
  }

};