import './dr3dsecure.html';
import {
  handleFingerPrintCreateSource,
  handleChallengeResultCreateSource
} from '../controller/controller-adyen-create-source';
import postRobot from '../../../post-robot-wrapper';
import {config} from '../config';
import {handleAdyenError} from '../controller/controller-create-source-utils';
import {getParentDomain} from '../utils';
import {isTestApiKey} from '../api-key-utils';
import {getComponentWindow} from '../../../client/createComponent';
import {handleAdyenResponseForSourceAuthentication} from '../controller/controller-authenticate-source';
import {handleAdyenResponseForSessionAuthentication} from '../controller/controller-handle-next-action';

const clientDomain = document.referrer === '' ? 'file://' : getParentDomain();
const clientListener = postRobot.listener({window: window.parent, domain: clientDomain});
clientListener.on('sendActions', handleAction);
clientListener.on('sendInitalize3dSecure', handleInitializeData);

const controllerListener = postRobot.listener({domain: config.domain});
controllerListener.on('sendActions', handleAction);
controllerListener.on('redirectComplete', handleRedirectComplete);

export function handleRedirectComplete(event) {
  components['payment3dsecure'].resolve(event.data.response);
}


controllerListener.on('sourceAuthenticationRedirectComplete', handleSourceAuthenticationRedirectComplete);

export function handleSourceAuthenticationRedirectComplete(event) {

  const sourceAuthenticationId = event.data.sourceAuthenticationId;
  const component = components['payment3dsecure'];
  const {clientSecret} = component.paymentServiceResponse;
  const secret = clientSecret.split('_')[1];
  const controllerWindow = getComponentWindow(components['payment3dsecure'].controllerId);
  postRobot.send(controllerWindow, 'getSourceAuthentication', {id:sourceAuthenticationId, clientSecret:secret}).then(response => {
    const {state} = response.data;
    components['payment3dsecure'].resolve({
      status: state
    });
  });

}

controllerListener.on('sessionAuthenticationRedirectComplete', handleSessionAuthenticationRedirectComplete);
export function handleSessionAuthenticationRedirectComplete(event) {

  const sessionAuthenticationId = event.data.sessionAuthenticationId;
  const component = components['payment3dsecure'];
  const {clientSecret} = component.paymentServiceResponse;
  const secret = clientSecret.split('_')[1];
  const controllerWindow = getComponentWindow(components['payment3dsecure'].controllerId);
  postRobot.send(controllerWindow, 'getSessionAuthentication', {id:sessionAuthenticationId, clientSecret:secret}).then(response => {
    const {state} = response.data;
    components['payment3dsecure'].resolve({
      status: state
    });
  });

}

let components = {
  payment3dsecure: {}
};

export function getInstanceData() {
  return components['payment3dsecure'];
}

/**
 * Just used for testing.
 */
export function clearComponentData() {
  components = {
    payment3dsecure: {}
  };
}

export function setAdyenCheckout(adyenCheckout) {
  components['payment3dsecure'].adyenCheckout = adyenCheckout;
}

export function getEnvironment(apiKey) {
  return isTestApiKey(apiKey) ? 'test' : 'live';
}

export function getAdyenURL(apiKey) {
  return isTestApiKey(apiKey) ? config.adyenTestUrl : config.adyenProdUrl;
}

export function getAdyenIntegrity(apiKey) {
  return isTestApiKey(apiKey) ? config.adyenTestIntegrity : config.adyenProdIntegrity;
}

export function getOriginKey(apiKey) {
  return isTestApiKey(apiKey) ? config.originTestKey : config.originProdKey;
}

/**
 * load the adyen script url
 * @param event
 */
export function handleInitializeData(event) {
  const {secureId, userLocale, apiKey, customerID} = event.data;

  components['payment3dsecure'].componentId = secureId;
  components['payment3dsecure'].customerID = customerID;

  const configuration = getConfiguration(getEnvironment(apiKey), userLocale, getOriginKey(apiKey));

  initializeAdyenJS(getAdyenURL(apiKey), getAdyenIntegrity(apiKey), configuration);

}

export function setInstanceData(secureId, controllerId, paymentServiceResponse, resolve, redirectWindow) {
  components['payment3dsecure'].componentId = secureId;
  components['payment3dsecure'].controllerId = controllerId;
  components['payment3dsecure'].paymentServiceResponse = paymentServiceResponse;
  components['payment3dsecure'].resolve = resolve;
  components['payment3dsecure'].redirectWindow = redirectWindow;
}

export function getConfiguration(environment, userLocale, originKey) {
  return {
    environment: environment,
    locale: userLocale,
    originKey: originKey,
  };
}

/**
 * create a configuration object & initalize adyen session
 * @param {Event} event
 */
export function handleAction(event) {
  const {paymentServiceResponse, secureId, controllerId, resolve, redirectWindow} = event.data;
  setInstanceData(secureId, controllerId, paymentServiceResponse, resolve, redirectWindow);

  return runAction(getInstanceData());
}

function runAction(instanceData) {
  const action = instanceData.paymentServiceResponse.nextAction.action;

  if (action === 'challenge_shopper') {
    extractChallengeShopper(instanceData);
  } else if (action === 'fingerprint_device') {
    extractFingerPrintDetails(instanceData);
  } else if (action === 'do_3ds_redirect') {
    const response = getInstanceData().paymentServiceResponse;

    const controllerWindow = getComponentWindow(components['payment3dsecure'].controllerId);

    return postRobot.send(controllerWindow, 'handleDropInRedirect', {
      sourceId: response.id,
      clientSecret: response.clientSecret.split('_')[1],
      redirectWindow: getInstanceData().redirectWindow,
      paymentMethodType: 'adyen_redirect',
      resolve: getInstanceData().resolve
    });
  } else if (action === 'mock_challenge_shopper') {
    handleMockChallengeAction();
  }
}


/**
 * this method is to trigger fingerprint & challenge shopper.
 * @param response
 * @param instanceData
 */
export function handleConfigResponse(response, instanceData) {
  if (response.isValid === true) {
    runAction(instanceData);
  }
}

/**
 * Get a Adyen Session
 * @param config
 * @returns {AdyenCheckout}
 */
export function createAdyenSession(config) {
  return new AdyenCheckout(config); //eslint-disable-line no-undef
}

/**
 * Trigger fingerprint device from adyen checkout and mount to container
 * @param componentData
 */
export function extractFingerPrintDetails(componentData) {
  const adyenCheckout = componentData.adyenCheckout;
  const paymentServiceResponse = componentData.paymentServiceResponse.nextAction;
  adyenCheckout
    .create('threeDS2DeviceFingerprint', {
      fingerprintToken: paymentServiceResponse.data.fingerprintToken,
      onComplete: function (response) {
        handleFingerPrintDevice(response, componentData);
      },
      onError: handleError
    }).mount('#DR3dsecure');
}

/**
 * Trigger challenge shopper from adyen checkout and mount to container
 * @param componentData
 */
export function extractChallengeShopper(componentData) {
  const adyenCheckout = componentData.adyenCheckout;
  const paymentServiceResponse = componentData.paymentServiceResponse.nextAction;
  openPopup('show');
  adyenCheckout.create('threeDS2Challenge', {
    challengeToken: paymentServiceResponse.data.challengeToken,
    onComplete: function (challengeShopper) {
      handleSuccessChallengeShopper(challengeShopper, componentData);
    },
    onError: handleError,
    size: getAdyenSize(window.innerWidth)
  }).mount('#DR3dsecure');
}


export function getAdyenSize(screenWidth) {
  if(screenWidth < 390) return '01';
  if(screenWidth < 500) return '02';
  if(screenWidth < 600) return '03';
  return '04';
}

/**
 * handle the response from finger print device and send to create source.
 * @param fingerprintData
 * @param component
 */
export function handleFingerPrintDevice(fingerprintData, component) {
  if (component.paymentServiceResponse.hasOwnProperty('requestType') && component.paymentServiceResponse.requestType === 'authenticateSource') {
    handleAdyenResponseForSourceAuthentication('fingerprint', fingerprintData.data.details['threeds2.fingerprint'], component);
  } else if (component.paymentServiceResponse.hasOwnProperty('requestType') && component.paymentServiceResponse.requestType === 'handleNextAction') {
    handleAdyenResponseForSessionAuthentication('fingerprint', fingerprintData.data.details['threeds2.fingerprint'], component);
  } else {
    handleFingerPrintCreateSource(fingerprintData.data.details['threeds2.fingerprint'], component);
  }

}

/**
 * handle the response from challenge shopper and send to create source.
 * @param challengeShopperData
 * @param component
 */
export function handleSuccessChallengeShopper(challengeShopperData, component) {
  openPopup('hide');
  if (component.hasOwnProperty('paymentServiceResponse') && component.paymentServiceResponse.hasOwnProperty('requestType') && component.paymentServiceResponse.requestType === 'authenticateSource') {
    handleAdyenResponseForSourceAuthentication('challengeResult', challengeShopperData.data.details['threeds2.challengeResult'], component);
  } else if (component.hasOwnProperty('paymentServiceResponse') && component.paymentServiceResponse.hasOwnProperty('requestType') && component.paymentServiceResponse.requestType === 'handleNextAction') {
    handleAdyenResponseForSessionAuthentication('challengeResult', challengeShopperData.data.details['threeds2.challengeResult'], component);
  } else {
    handleChallengeResultCreateSource(challengeShopperData.data.details['threeds2.challengeResult'], component);
  }
}


/**
 * Handling error scenarios for Adyen session
 * @param err
 * @return {Promise<{source: null, error: {type: 3ds2_error, errors: {message: string}[]}}>}
 */
export function handleError(err) {
  if (err.errorCode === 'timeout') { // TODO Return a promise here
    console.warn(err); //eslint-disable-line
  } else {
    return handleAdyenError();
  }
}

/**
 * This method to hide or show the popup window.
 * @param prop
 * @return {HTMLElement}
 */
export function openPopup(prop) {
  let popup = document.getElementById('secureOverlay');
  if (prop === 'show') {
    popup.style.display = 'flex';
  } else {
    popup.style.display = 'none';
  }
  return popup;
}

/**
 * Create a script tag with url and initalize the adyen session if it does not already exist
 * @param url
 * @param integrity
 * @param config
 */
export function initializeAdyenJS(url, integrity, config) {
  if (!document.getElementById('DR-Adyen')) {
    let scriptTag = document.createElement('script');
    scriptTag.id = 'DR-Adyen';
    scriptTag.src = url;
    scriptTag.integrity = integrity;
    scriptTag.onload = function () {
      setAdyenCheckout(createAdyenSession(config));
    };
    scriptTag.crossOrigin = 'anonymous';
    document.body.appendChild(scriptTag);
  }
}

export function handleMockChallenge(result) {
  const challengeShopperData = {
    data: {
      details: {
        'threeds2.challengeResult': result
      }
    }
  };
  handleSuccessChallengeShopper(challengeShopperData, getInstanceData());
  document.getElementById('mockChallenge').remove();
}

/**
 * Creates a modal with mock challenge authentication scenarios
 */
function handleMockChallengeAction() {
  let mockChallenge = document.createElement('div');
  mockChallenge.setAttribute('id', 'mockChallenge');
  mockChallenge.setAttribute('class', 'modal-content');
  mockChallenge.innerHTML = `
      <h1>3D Secure Test Payment Challenge</h1>
      <div class="content">
        <p>In live mode, shoppers will be asked to verify their identity. Please click the Complete Authentication button to proceed.</p>
        <button id="success" onclick="handleMockChallenge('success')">Complete Authentication</button>
      </div>`;
  document.body.appendChild(mockChallenge);
}


