'use strict';

import dataStore from './dataStore.js';
import {createSource} from './createSource.js';
import {complianceGetDetails} from './complianceData';
import {createComponent, getComponentWindow, registerComponentWithController, createOrExtractBeaconController, sendApiKey,
  createOrExtractAdyenController, sendAdyen3dDetails, sendInitalize3dSecure, updateOverlay } from './createComponent.js';
import {sendBeaconEventDetails} from '../app/components/td/td-utils';
import {createController, registerControllerEvents} from './createController.js';
import {config} from '../app/components/config';
import DigitalRiverPaymentRequest from './DigitalRiverPaymentRequest';
import {collectClientData} from '../beacon/beacon-client-data';
import postRobot from '../post-robot-wrapper';
import {
  DEFAULT_LOCALE,
  forceCasingOfLocale,
  getLocaleFromLanguage,
  localeMessagesContainsClientProvidedLocale
} from '../app/components/localization/localized-messages';
import {isManagePaymentMethodsMode, mountDropin, getLogoDetails} from './create-dropin';
import {getPaymentMethods} from './fetch-payment-methods';
import {getAuthenticateSource} from './authenticate-source';
import {handleKoreanCard, isKoreanCard} from './korean-card-utils';
import {canClientCreateComponent, getComponentCreate, isClientSideComponent} from '../app/config';
import {sendHandleNextAction} from './send-handle-next-action';
import {openWindow} from '../app/components/controller/controller-window-opener';

function localeNotProvided(instanceOptions) {
  return typeof instanceOptions.locale === 'undefined';
}


function updateInstanceOptionsWithDefaults(instanceOptions) {
  if (typeof instanceOptions === 'undefined') {
    instanceOptions = {};
  }

  if (localeNotProvided(instanceOptions)) {
    instanceOptions.locale = DEFAULT_LOCALE;
  } else if (instanceOptions.locale.length === 2) {
    instanceOptions.locale = getLocaleFromLanguage(instanceOptions.locale);
  } else if (!localeMessagesContainsClientProvidedLocale(forceCasingOfLocale(instanceOptions))) {
    instanceOptions.locale = DEFAULT_LOCALE;
  } else {
    instanceOptions.locale = forceCasingOfLocale(instanceOptions);
  }

  if (typeof instanceOptions.customerID === 'undefined') {
    instanceOptions.customerID = '';
  }

  return instanceOptions;
}

/**
 * Digital River Payments class
 * @constructor
 * @param {string} apiKey A Payment Service API key
 * @param {object} providedInstanceOptions
 */
function DigitalRiver(apiKey, providedInstanceOptions)  {
  if (typeof apiKey !== 'string') {
    throw new Error('Pass an API key.');
  }

  const instanceOptions = updateInstanceOptionsWithDefaults(providedInstanceOptions);

  this.Compliance = {
    getDetails: getDetails.bind(instanceOptions)
  };

  // creating controller component
  let component = createController(document.body, 'controller');

  // creating beacon component
  let beaconComponent = createOrExtractBeaconController();

  // creating 3dsecure component
  let dr3dsecure = createOrExtractAdyenController();

  this.key = dataStore.create({ apiKey: apiKey, controller: component, components: {}, customEvents: [], instanceOptions: instanceOptions });
  registerControllerEvents(this.key, getComponentWindow(component.id), config.domain);

  sendApiKey(component.id, 'sendInitialData', {
    apiKey: apiKey,
    browserInfo: collectClientData(window),
    instanceOptions: instanceOptions,
    controllerId: component.id
  });

  sendApiKey(beaconComponent.id, 'sendBeaconInitialData', {
    apiKey: apiKey,
    customerID: instanceOptions.customerID
  }).then(() => {
    return sendBeaconEventDetails(beaconComponent.id, 'controller_loaded');
  });

  sendInitalize3dSecure(dr3dsecure.id, apiKey);

  updateOverlay('DRPayment3ds', '0px', '');
}

function shouldSendCreateSourceBeaconEvent(response) {
  return typeof response !== 'undefined' && response.source !== null && typeof response.source.id !== 'undefined';
}



/**
 * ]
 * @param sourceRequest
 * @param componentInstanceOrSourceData
 * @returns {*}
 */

function createSourceForAllPaymentMethods(sourceRequest, componentInstanceOrSourceData) {

  const { controller } = dataStore.get(this.key);

  if (!controller) {
    throw new Error('Cannot create source without a controller');
  }

  const beaconComponent = createOrExtractBeaconController();

  if (typeof sourceRequest === 'undefined') {
    // In this case the componentInstance is actually the source data
    if (typeof componentInstanceOrSourceData !== 'object') {
      throw new Error('Please provide Source creation details to the createSource method.');
    }

    if(isKoreanCard(componentInstanceOrSourceData)) {
      const key = this.key;
      const self = this;
      return handleKoreanCard(self, key, componentInstanceOrSourceData, controller, {
        type: ''
      }, beaconComponent, handleCreateSource);
    }

    return handleCreateSource(controller.id, '', componentInstanceOrSourceData, beaconComponent);

  } else {
    if (typeof componentInstanceOrSourceData !== 'object') {
      throw new Error('Please provide a valid component to the createSource method');
    }

    if (typeof sourceRequest !== 'object') {
      throw new Error('Please provide Source creation details to the createSource method');
    }

    if(isKoreanCard(sourceRequest)) {
      const key = this.key;
      const self = this;
      return handleKoreanCard(self, key, sourceRequest, controller, componentInstanceOrSourceData, beaconComponent, handleCreateSource);
    }

    return handleCreateSource(controller.id, componentInstanceOrSourceData.type, sourceRequest, beaconComponent);
  }
}

export function handleCreateSourceResponse(response, controllerId, beaconComponent) {

  if (typeof response !== 'undefined' && response.source !== null && hasRequiresAction(response.source)) {
    // creating Adyen component
    const adyenComponent = createOrExtractAdyenController();
    const action = response.source.nextAction.action;

    if (action !== 'do_3ds_redirect') {
      updateOverlay('DRPayment3ds', '100%', 'rgba(0,0,0,0.3)');
    }
    if (is3DSAction(action)) {
      return handle3DSAction(adyenComponent, controllerId, response.source, getRedirectWindowWhenCorrectAction(action)).then((responseData) => {
        if (shouldSendCreateSourceBeaconEvent(responseData)) {
          sendBeaconEventDetails(beaconComponent.id, 'source', response.source.id);
        }
        return responseData;
      });
    } else {
      // nothing can be done for now
      if (shouldSendCreateSourceBeaconEvent(response)) {
        sendBeaconEventDetails(beaconComponent.id, 'source', response.source.id);
      }
      return response;
    }
  } else {
    if (shouldSendCreateSourceBeaconEvent(response)) {
      sendBeaconEventDetails(beaconComponent.id, 'source', response.source.id);
    }
    return response;
  }
}

function handleCreateSource(controllerId, componentType, sourceRequest, beaconComponent) {

  return createSource(controllerId, componentType, sourceRequest)
    .then((response) => {
      return handleCreateSourceResponse(response, controllerId, beaconComponent);
    });
}

/**
 * Submits a payment source transaction to the payment service
 * @param componentInstanceOrSourceData - Instance of a component that is being submitted
 * @param {object|JSON} sourceRequest JSON as an object containing the source request
 * @returns {Promise} A Promise that contains the successful response or throws on error or timeout
 */
DigitalRiver.prototype.createSource = function (componentInstanceOrSourceData, sourceRequest) {
  return createSourceForAllPaymentMethods.call(this, sourceRequest, componentInstanceOrSourceData);
};

function getSecretFromSessionClientSecret(sessionClientSecret) {
  if (sessionClientSecret.includes('_')) {
    return sessionClientSecret.split('_')[0];
  } else {
    return sessionClientSecret;
  }
}

/**
 * Creates a source for the credit card element that uses a session client secret to initiate 3DS
 * @param sessionClientSecret
 * @param element
 * @param sourceRequest
 */
DigitalRiver.prototype.createCreditCardSource = function (sessionClientSecret, element, sourceRequest) {

  if (!sessionClientSecret) {
    throw new Error('You must provide a sessionClientSecret');
  }

  // Copy source request so it does not modify the clients request
  const updatedSourceRequest = Object.assign({}, sourceRequest);

  updatedSourceRequest.sessionId = getSecretFromSessionClientSecret(sessionClientSecret);

  if (typeof updatedSourceRequest.creditCard === 'undefined') {
    updatedSourceRequest.creditCard = {};
  }

  if (typeof updatedSourceRequest.creditCard.returnUrl === 'undefined') {
    updatedSourceRequest.creditCard.returnUrl = window.location.href;
  }

  return createSourceForAllPaymentMethods.call(this, updatedSourceRequest, element, sessionClientSecret);
};

DigitalRiver.prototype.retrieveSource = function(sourceId, sourceClientSecret) {
  if (!sourceClientSecret) {
    throw new Error('You must provide a sourceClientSecret');
  }

  if (!sourceId) {
    throw new Error('You must provide a sourceId');
  }

  const { controller } = dataStore.get(this.key);

  if (!controller) {
    throw new Error('Cannot retrieve source without a controller');
  }

  const controllerWindow = getComponentWindow(controller.id);

  if (!controllerWindow) {
    throw new Error(`Unable to locate controller '${controller.id}'`);
  }

  const data = {
    sourceId: sourceId,
    sourceClientSecret: sourceClientSecret.split('_')[1]
  };

  return postRobot.send(controllerWindow, 'retrieveSource', data, { timeout: 10000 })
    .then(response => {
      return response.data;
    });
};

/**
 * Client can update owner and card info with this method
 * @param cardExpirationElement
 * @param sourceData
 */
DigitalRiver.prototype.updateSource = function(cardExpirationElement, sourceData) {
  const { controller, instanceOptions } = dataStore.get(this.key);
  if (!controller) {
    throw new Error('Cannot update source without a controller');
  }

  const controllerWindow = getComponentWindow(controller.id);

  if (!controllerWindow) {
    throw new Error(`Unable to locate controller '${controller.id}'`);
  }

  if (typeof sourceData === 'undefined') {
    // In this case the firstArg (cardExpirationElement) is actually sourceData
    // because client did not provide optional element
    sourceData = Object.assign({}, cardExpirationElement);
    cardExpirationElement = undefined;
  } else {
    if (cardExpirationElement.type !== 'cardexpiration') {
      return Promise.resolve({
        'error': {
          'type': 'bad_request',
          'errors': [{
            'code': 'invalid_element_type',
            'message': 'An element type of cardexpiration must be provided.'
          }]
        },
        'source':null});
    }
  }
  let errors = [];

  if (!sourceData.id) {
    errors.push({
      'code': 'missing_parameter',
      'message': 'A parameter is missing.',
      'parameter': 'id'
    });
  }

  if (!sourceData.clientSecret) {
    errors.push({
      'code': 'missing_parameter',
      'message': 'A parameter is missing.',
      'parameter': 'clientSecret'
    });
  }

  if(errors.length > 0) {
    return Promise.resolve({
      'error': {
        'type': 'bad_request',
        'errors': errors
      },
      'source':null});
  }

  const data = {
    sourceData,
    cardExpirationElement,
    locale: instanceOptions.locale
  };

  return postRobot.send(controllerWindow, 'updateSource', data, { timeout: 10000 })
    .then(response => {
      return response.data;
    });
};

DigitalRiver.prototype.retrieveOnlineBankingBanks = function (country, currency) {

  const { controller } = dataStore.get(this.key);

  if (!controller) {
    throw new Error('Cannot get banks without a controller');
  }

  const controllerWindow = getComponentWindow(controller.id);

  if (!controllerWindow) {
    throw new Error(`Unable to locate controller '${controller.id}'`);
  }

  // Send message to Controller Frame to get banks
  return postRobot.send(controllerWindow, 'getOnlineBankingBanks', { country: country, currency: currency }, { timeout: 10000 })
    .then(response => {
      // This is a Post Robot Response object so you have to get the data out
      return response.data;
    })
    .catch(() => {
      return [];
    });
};

function onlineBankingDoesNotHaveRequiredFields(options) {
  return typeof options === 'undefined' ||
    typeof options.onlineBanking === 'undefined' ||
    typeof options.onlineBanking.currency === 'undefined' ||
    typeof options.onlineBanking.country === 'undefined';
}

DigitalRiver.prototype.retrieveAvailablePaymentMethods = function(paymentMethodOptions = {}) {
  const {controller, instanceOptions} = dataStore.get(this.key);
  const {sessionId = null, country = null, currency = null, supportsFreeTrial = null, supportsRecurring = null, supportsStorage = null} = paymentMethodOptions;
  return getPaymentMethods(controller.id, sessionId, country, currency, instanceOptions.locale, supportsStorage, null, supportsRecurring, supportsFreeTrial);
};

function getRedirectWindowWhenCorrectAction(action) {
  return action === 'do_3ds_redirect' ? openWindow('') : undefined;
}

/**
 * Authenticates a source and performs 3DS validation if necessary
 * @param componentInstanceOrSourceInfo - Instance of a component that is being submitted
 * @param {object} sourceInfo
 * @param {string} sourceInfo.sessionId
 * @param {string} sourceInfo.sourceId
 * @param {string} sourceInfo.sourceClientSecret
 * @param {string} sourceInfo.[returnUrl = url for page where DR.js is instantiated]
 * @returns {Promise} containing status
 */
DigitalRiver.prototype.authenticateSource = function(componentInstanceOrSourceInfo, sourceInfo) {

  const sourceData = typeof sourceInfo === 'undefined' ? componentInstanceOrSourceInfo : sourceInfo;

  const {controller} = dataStore.get(this.key);
  const {sessionId, sourceId, sourceClientSecret} = sourceData;

  return getAuthenticateSource(controller.id, sessionId, sourceId, sourceClientSecret, componentInstanceOrSourceInfo)
    .then((sourceResponse) => {
      sourceResponse.requestType = 'authenticateSource';
      if (hasRequiresAction(sourceResponse)) {
        // creating Adyen component
        const adyenComponent = createOrExtractAdyenController();
        const action = sourceResponse.nextAction.action;
        if (action !==  'do_3ds_redirect') {
          updateOverlay('DRPayment3ds', '100%', 'rgba(0,0,0,0.3)');
        }

        if (is3DSAction(action)) {
          return handle3DSAction(adyenComponent, controller.id, sourceResponse, getRedirectWindowWhenCorrectAction(action));
        }
      } else {
        return {
          status: sourceResponse.state
        };
      }
    });
};

function handle3DSAction(adyenComponent, controllerId, source, redirectWindow) {
  return new Promise(function (resolve) {
    if (typeof redirectWindow !== 'undefined') {
      redirectWindow.location.replace(source.nextAction.data.redirectUrl);
    }
    return sendAdyen3dDetails(adyenComponent.id, controllerId, source, resolve, redirectWindow);
  }).then((responseData) => {
    updateOverlay('DRPayment3ds', '0px', '');
    return responseData;
  });
}

DigitalRiver.prototype.handleNextAction = function (nextActionData) {

  const {controller} = dataStore.get(this.key);

  return sendHandleNextAction(controller.id, nextActionData)
    .then((sourceResponse) => { // TODO Generify
      sourceResponse.requestType = 'handleNextAction'; // authenticateSource
      if (hasRequiresAction(sourceResponse)) {
        // creating Adyen component
        const adyenComponent = createOrExtractAdyenController();
        const action = sourceResponse.nextAction.action;
        if (action !==  'do_3ds_redirect') {
          updateOverlay('DRPayment3ds', '100%', 'rgba(0,0,0,0.3)');
        }

        if (is3DSAction(action)) {
          return handle3DSAction(adyenComponent, controller.id, sourceResponse, getRedirectWindowWhenCorrectAction(action));
        }
      } else {
        return {
          status: sourceResponse.state
        };
      }
    });
};

function is3DSAction(action) {
  return action === 'fingerprint_device' || action === 'challenge_shopper' || action === 'do_3ds_redirect' || action === 'mock_challenge_shopper';
}

function hasRequiresAction(response) {
  return typeof response !== 'undefined' && response.state === 'requires_action' &&  response.nextAction !== null;
}

/**
 * Creates a payment element
 * @param {string} type Element type
 * @param {object} options Element options
 */
DigitalRiver.prototype.createElement = function (type, options) {
  const formattedType = type.toLowerCase();
  if (!canClientCreateComponent(formattedType)) {
    throw new Error(`Invalid element type '${type}'`);
  }

  const {components, controller} = dataStore.get(this.key);
  if (components[formattedType]) {
    throw new Error(`Failed to create element. Only one element of type '${type}' allowed per instance.`);
  }

  if ((type === 'googlepay') && !(options instanceof DigitalRiverPaymentRequest)) {
    throw new Error('Use paymentRequest() to create options for google.');
  }

  if (type === 'onlinebanking' && onlineBankingDoesNotHaveRequiredFields(options)) {
    throw new Error('Element Creation Error: For onlineBanking, currency and country are required.');
  }

  let component;

  if (options instanceof DigitalRiverPaymentRequest) {
    options = options.getData();
  }

  if (isClientSideComponent(formattedType)) {
    component = getComponentCreate(formattedType).create(controller.id, this.key, options);
  } else {
    component = createComponent(formattedType, controller.id, this.key, options);
  }
  // Add component/element id to component map
  const data = dataStore.get(component.key);
  data.components = Object.assign({}, data.components, { [formattedType]: component.id });
  registerComponentWithController(controller.id, component, options);
  return component;

};

/**
 * Creates a payment request
 * @param {object} data of the request
 */
DigitalRiver.prototype.paymentRequest = function (data) {
  return new DigitalRiverPaymentRequest(data);
};

function getDetails(entityValue, userLocale) {

  if (!entityValue) {
    throw new Error('Without business entity value we cannot trigger this method.');
  }

  if(!userLocale) {
    userLocale = this.locale;
  }

  return complianceGetDetails(entityValue, userLocale);
}

DigitalRiver.prototype.retrieveKonbiniStores = function () {

  const { controller } = dataStore.get(this.key);

  if (!controller) {
    throw new Error('Cannot get stores without a controller');
  }

  const controllerWindow = getComponentWindow(controller.id);

  if (!controllerWindow) {
    throw new Error(`Unable to locate controller '${controller.id}'`);
  }

  // Send message to Controller Frame to get banks
  return postRobot.send(controllerWindow, 'getKonbiniStores', { })
    .then(response => {
      // This is a Post Robot Response object so you have to get the data out
      return response.data;
    })
    .catch(() => {
      return [];
    });
};

function removeUnsupportedPaymentMethods(storedPaymentMethods) {
  const allowedPaymentMethodsTypes = ['payPal', 'creditCard', 'storeCredit'];
  const allowedBrands = getLogoDetails().map(logoDetails => logoDetails.code);

  return storedPaymentMethods.filter( pm => {
    if (typeof pm.type === 'undefined' || typeof pm.id === 'undefined') {
      console.warn('Stored payment method is missing required data');
      return false;
    } else if (pm.type === 'creditCard' && (typeof pm.lastFour === 'undefined' || typeof pm.brand === 'undefined')) {
      console.warn('Credit card is missing required data');
      return false;
    } else if (pm.type === 'storeCredit' && (typeof pm.amount === 'undefined' || typeof pm.displayName === 'undefined')) {
      console.warn('Store Credit is missing required data');
      return false;
    } else {
      return true;
    }
  }).filter(pm => {
    if (allowedPaymentMethodsTypes.includes(pm.type)) {
      return true;
    } else {
      console.warn('An unsupported stored payment has been provided.', pm.type);
      return false;
    }
  }).filter( pm => {
    if (pm.type === 'creditCard' && !allowedBrands.includes(pm.brand)) {
      console.warn('An unsupported stored credit card brand has been provided', pm.brand);
      return false;
    } else {
      return true;
    }
  });
}

function validateBillingAddressFields(billingAddress) {

  if (typeof billingAddress.firstName === 'undefined' || billingAddress.firstName.length === 0) {
    throw Error('Please provide a billingAddress.firstName');
  }

  if (typeof billingAddress.lastName === 'undefined' || billingAddress.lastName.length === 0) {
    throw Error('Please provide a billingAddress.lastName');
  }

  if (typeof billingAddress.email === 'undefined' || billingAddress.email.length === 0) {
    throw Error('Please provide a billingAddress.email');
  }

  if (typeof billingAddress.phoneNumber === 'undefined' || billingAddress.phoneNumber.length === 0) {
    throw Error('Please provide a billingAddress.phoneNumber');
  }

  if (typeof billingAddress.address === 'undefined' || Object.keys(billingAddress.address).length === 0) {
    throw Error('Please provide a billingAddress.address');
  }

  if (typeof billingAddress.address.line1 === 'undefined' || billingAddress.address.line1.length === 0) {
    throw Error('Please provide a billingAddress.address.line1');
  }

  if (typeof billingAddress.address.postalCode === 'undefined' || billingAddress.address.postalCode.length === 0) {
    throw Error('Please provide a billingAddress.address.postalCode');
  }

  if (typeof billingAddress.address.country === 'undefined' || billingAddress.address.country.length === 0) {
    throw Error('Please provide a billingAddress.address.country');
  }

}

/**
 * Submits a request to create a stored payment source
 * @param componentInstanceOrSourceData - Instance of a component that is being submitted or if one param is passed, it is the source data
 * @param {object|JSON} sourceRequest JSON as an object containing the source request
 * @returns {Promise} A Promise that contains the successful response or throws on error or timeout
 */
DigitalRiver.prototype.createStoredPaymentSource = function (componentInstanceOrSourceData, sourceRequest) {
  if (typeof sourceRequest !== 'undefined' && typeof sourceRequest !== 'string') {
    sourceRequest.futureUse = true;
  } else {
    componentInstanceOrSourceData.futureUse = true;
  }
  return createSourceForAllPaymentMethods.call(this, sourceRequest, componentInstanceOrSourceData);
};

function validatePaymentMethodConfigurationHasDuplicateEntries(options) {

  if (typeof options.paymentMethodConfiguration === 'undefined') {
    return;
  }

  if (typeof options.paymentMethodConfiguration.enabledPaymentMethods !== 'undefined' &&
    options.paymentMethodConfiguration.enabledPaymentMethods.length > 0 &&
    typeof options.paymentMethodConfiguration.disabledPaymentMethods !== 'undefined' &&
    options.paymentMethodConfiguration.disabledPaymentMethods.length > 0
  ) {

    const enabledPaymentMethods = [...options.paymentMethodConfiguration.enabledPaymentMethods];

    options.paymentMethodConfiguration.disabledPaymentMethods.forEach( dpm => {
      if (enabledPaymentMethods.includes(dpm)) {
        throw Error(`Payment method ${dpm} cannot be enabled and disabled.`);
      }
    } );
  }

}

DigitalRiver.prototype.createDropin = function (configuration) {

  if (!isManagePaymentMethodsMode(configuration) && (typeof configuration.sessionId === 'undefined' || configuration.sessionId.length === 0)) {
    throw Error('Please provide a sessionId');
  }

  if ((typeof configuration.billingAddress === 'undefined' || Object.keys(configuration.billingAddress).length === 0) && isManagePaymentMethodsMode(configuration) && (typeof configuration.sessionId === 'undefined' || configuration.sessionId.length === 0)) {
    throw Error('Please provide a billingAddress');
  }

  if (typeof configuration.billingAddress !== 'undefined') {
    validateBillingAddressFields(configuration.billingAddress);
  }

  if (typeof configuration.options !== 'undefined' && typeof configuration.options.storedPaymentMethods !== 'undefined') {
    configuration.options.storedPaymentMethods = removeUnsupportedPaymentMethods(configuration.options.storedPaymentMethods);
  }

  validatePaymentMethodConfigurationHasDuplicateEntries(configuration);

  const key = this.key;
  const createElement = this.createElement.bind(this);
  const createSource = this.createSource.bind(this);
  const complianceDetails = complianceGetDetails;

  const {controller, instanceOptions} = dataStore.get(key);

  const paymentMethodsMounted = [];

  return {
    mount: mountDropin(controller, configuration, createSource, createElement, instanceOptions, complianceDetails, paymentMethodsMounted, false),
    refresh: mountDropin(controller, configuration, createSource, createElement, instanceOptions, complianceDetails, paymentMethodsMounted, true),
  };
};



export default DigitalRiver;