import messages from './messages.json';
import supportedLocales from './supported-locales.json';

export const DEFAULT_LOCALE = 'en-US';

function getLocaleMessages(locale) {
  return  messages.hasOwnProperty(locale) ? messages[locale] : undefined;

}

export function getLocaleMessage(locale, messageCode) {
  const mappedLocale = getMappedLocale(locale);
  const localizedMessages = getLocaleMessages(mappedLocale);
  return localizedMessages && localizedMessages.hasOwnProperty(messageCode) ? localizedMessages[messageCode] : undefined;
}

export function localeMessagesContainsClientProvidedLocale(locale) {
  const mappedLocale = getMappedLocale(locale);
  return Object.keys(messages).filter( key =>  key.toLowerCase() === mappedLocale.toLowerCase()).length > 0;
}

export function forceCasingOfLocale(instanceOptions) {
  const locale = instanceOptions.locale;
  const regex = /_|-/;
  const separatorIndex = locale.search(regex);
  if( separatorIndex !== -1) {
    const providedLocaleArray = locale.split(locale.charAt(separatorIndex));
    return providedLocaleArray[0] + '-' + providedLocaleArray[1].toUpperCase();
  } else {
    return DEFAULT_LOCALE;
  }

}

export function getMappedLocale(locale) {
  return supportedLocales.hasOwnProperty(locale) ? supportedLocales[locale].messageLocale : locale;
}

export function getLocaleFromLanguage(language) {
  const languageMap = {
    ar: 'ar-EG',
    cs: 'cs-CZ',
    da: 'da-DK',
    de: 'de-DE',
    el: 'el-GR',
    en: 'en-IE',
    es: 'es-ES',
    et: 'et-EE',
    fi: 'fi-FI',
    fr: 'fr-FR',
    hu: 'hu-HU',
    it: 'it-IT',
    iw: 'iw-IL',
    ja: 'ja-JP',
    ko: 'ko-KR',
    lt: 'lt-LT',
    lv: 'lv-LV',
    nl: 'nl-NL',
    no: 'no-NO',
    pl: 'pl-PL',
    pt: 'pt-PT',
    ro: 'ro-RO',
    ru: 'ru-RU',
    sk: 'sk-SK',
    sl: 'sl-SL',
    sr: 'sr-YU',
    sv: 'sv-SE',
    th: 'th-TH',
    tr: 'tr-TR',
    zh: 'zh-CN',
  };

  return languageMap.hasOwnProperty(language.toLowerCase()) ? languageMap[language.toLowerCase()] : DEFAULT_LOCALE;
}

