import mandate from '../json/mandate.json';
import {getMappedLocale} from '../app/components/localization/localized-messages';

export function getMandateByLocale(locale) {
  const mappedLocale = getMappedLocale(locale);
  const complianceLocale = mappedLocale.split('-').join('_');
  if (!mandate.hasOwnProperty(complianceLocale)) {
    // eslint-disable-next-line no-console
    console.warn('Could not find mandate error for locale', complianceLocale);
    return getMandateByLocale('en-US');
  }

  return mandate[complianceLocale];
}

export function getSaveCardMandateText(locale) {

  const mandateForLocale = getMandateByLocale(locale);

  return mandateForLocale.saveCardMandate;
}

export function getAddNewPaymentText(locale) {

  const mandateForLocale = getMandateByLocale(locale);

  return mandateForLocale.addNewPayment;
}

export function getMustAcceptTermsText(locale) {

  const mandateForLocale = getMandateByLocale(locale);

  return mandateForLocale.mustAcceptTerms;
}
