import postRobot from '../post-robot-wrapper';
import { collectClientData } from '../beacon/beacon-client-data';
import { getComponentWindow } from './createComponent';
import {chooseCreateSourceCatchMessage} from '../app/components/controller/controller-create-source-utils';

/**
 * Locate a controller frame and send a createSource event via postRobot
 * @private
 * @param {string} controllerId controllerId string
 * @param {string} type of the component
 * @param {object} data source request payload (JSON as object)
 */
export function createSource(controllerId, type,  data) {
  const controllerWindow = getComponentWindow(controllerId);

  if (!controllerWindow) {
    throw new Error(`Unable to locate controller '${controllerId}'`);
  }

  // Send message to Controller Frame to call createSource
  return postRobot.send(controllerWindow, 'createSourceFromRequest', { sourceRequest: data, type: type, browserInfo: collectClientData(window) }, { timeout: 10000 })
    .then(response => {
      // This is a Post Robot Response object so you have to get the data out
      return response.data;
    })
    .catch((error) => {
      throw new Error(error);
    });
}

/**
 * Locate a controller frame and send a createSourceWithAdyen event via postRobot
 * @private
 * @param {string} controllerId controllerId string
 * @param {object} data source request payload (JSON as object)
 * @param {object} clientData (JSON as object)
 */
export function createSourceWithAdyen(controllerId, data, clientData) {
  const controllerWindow = getComponentWindow(controllerId);

  if (!controllerWindow) {
    throw new Error(`Unable to locate controller '${controllerId}'`);
  }

  // Send message to Controller Frame to call createSource
  return postRobot.send(controllerWindow, 'createSourceFromAdyenRequest', { sourceRequest: data, clientSecretData: clientData }, { timeout: 10000 })
    .then(response => {
      // This is a Post Robot Response object so you have to get the data out
      return response.data;
    })
    .catch((error) => {
      return chooseCreateSourceCatchMessage(error);
    });
}