import { config } from '../../app/components/config';
import postRobot from '../../post-robot-wrapper';
import { generateComponentId, onEventHandler, destroy, unmount, update, mount } from '../createComponent';
import { sanitizeOptions } from '../../app/components/options';
import { mountComponentFromClient } from '../../app/components/payment-events';
import { generateInstanceData } from '../../app/components/create-initial-data';
import { generateComponentData } from '../../app/components/payment-component-data';
import {complianceGetDetails} from '../complianceData';
import {sendEventData} from '../../app/components/input-events';

function getComplianceData(sellingEntityId, locale) {
  try {
    return complianceGetDetails(sellingEntityId, locale);
  } catch (e) {
    return undefined;
  }
}

function addCompliance(sellingEntityId, locale, parent) {

  const compliance = getComplianceData(sellingEntityId, locale);

  if (typeof compliance === 'undefined' || typeof compliance.disclosure === 'undefined') {
    return;
  }

  const legalFooter = document.createElement('div');
  legalFooter.className = 'DR-Legal-Footer';

  const resellerDisclosure = document.createElement('div');
  resellerDisclosure.innerHTML = compliance.disclosure.resellerDisclosure.localizedText;
  legalFooter.appendChild(resellerDisclosure);

  const legalFooterLinks = document.createElement('div');
  legalFooterLinks.className = 'DR-Legal-Footer-Links';

  createAndAddLink(compliance.disclosure.privacyPolicy, legalFooterLinks);
  createAndAddLink(compliance.disclosure.termsOfSale, legalFooterLinks);
  createAndAddLink(compliance.disclosure.californiaPrivacyRights, legalFooterLinks);
  createAndAddLink(compliance.disclosure.cookiePolicy, legalFooterLinks);
  createAndAddLink(compliance.disclosure.cancellationRights, legalFooterLinks);
  createAndAddLink(compliance.disclosure.legalNotice, legalFooterLinks);
  createAndAddLink(compliance.disclosure.warrantyInformation, legalFooterLinks);

  legalFooter.appendChild(legalFooterLinks);
  parent.appendChild(legalFooter);
}

function createAndAddLink(legalLink, legalFooterLinks) {

  if (typeof legalLink === 'undefined') {
    return;
  }

  if(legalLink.localizedText === '') {
    return;
  }

  const linkContainer = document.createElement('span');

  const link = document.createElement('a');
  link.className = 'DR-Legal-Footer-Link';
  link.href = legalLink.url;
  link.innerText = legalLink.localizedText;
  link.target = '_blank';

  linkContainer.appendChild(link);
  legalFooterLinks.appendChild(linkContainer);
}


export function createCompliance() {

  let componentData;
  let controllerEmitter;
  let controllerListener;
  let instanceData;
  let supportedInstruments;

  function getInstanceData() {
    return instanceData;
  }

  /**
   * Adds handleOptions to component
   * @param controllerListener
   * @param {Function} handleOptions
   */
  function addHandleOptions(controllerListener, handleOptions) {
    controllerListener.on('complianceOptions'+componentData.componentId, (event) => {
      const { componentData } = event.data;
      handleOptions(componentData);
    });
  }

  function onReady() {
    sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'ready', {}, {});
  }


  function handleOptions(data) {

    if (typeof data.options.compliance === 'undefined') {
      data.options.compliance = {};
    }

    if(!data.options.compliance.hasOwnProperty('entity')) {
      console.warn('Invalid entity provided or no entity provided. Defaulting to DR_INC-ENTITY'); // eslint-disable-line no-console
      data.options.compliance.entity = 'DR_INC-ENTITY';
    }

    setOptions(data.options);

    const parent = document.getElementById(instanceData.parentNode);
    const locale = typeof data.options.compliance.locale !== 'undefined' ? data.options.compliance.locale : data.instanceOptions.locale;

    parent.innerHTML = '';

    const node = document.createElement('div');
    node.id = instanceData.componentData.componentId;

    parent.appendChild(node);
    addCompliance(data.options.compliance.entity, locale, node);
  }

  /**
   * Mounts component
   * @param node - string or html element where component should be mounted
   */
  function mountCompliance(node) {
    instanceData.parentNode = node;
    mount.bind(this)(node);
    return mountComponentFromClient(instanceData.controllerEmitter, instanceData.componentData, handleOptions, onReady, undefined, instanceData);
  }


  /**
   * Sanitizes options, stores them in variable, changes amount to string
   * @param {object} unsafeOptions
   */
  function setOptions(unsafeOptions) {
    instanceData.options = sanitizeOptions(unsafeOptions);
  }

  /**
   * applies updated options sent by the client
   * @param {object} options
   */
  function handleUpdate(options) {
    const complianceUpdate = update.bind(this);
    complianceUpdate(options);
  }

  function createComponent(controllerId, key, options) {
    const type = 'compliance';
    let id = generateComponentId(type);
    const component = {
      id: id,
      key: key,
      type: type,
      parentNode: null,
      controllerId: controllerId,
      mount: mountCompliance,
      destroy: destroy,
      on: onEventHandler,
      options: sanitizeOptions(options), // Other method
      unmount: unmount,
      update: handleUpdate
    };

    componentData = generateComponentData(type, id, controllerId);
    componentData.key = key;
    controllerListener = postRobot.listener({window: componentData.controller.window, domain: config.domain});
    controllerEmitter = postRobot.client({window: componentData.controller.window, domain: config.domain});
    instanceData = generateInstanceData(
      controllerEmitter,
      componentData,
      undefined,
      setOptions,
      supportedInstruments
    );
    instanceData.options = component.options;

    addHandleOptions(controllerListener, handleOptions);

    return component;
  }

  return {
    createComponent: createComponent,
    handleOptions: handleOptions,
    emitComponentReady: onReady,
    getInstanceData: getInstanceData,
    addHandleOptions: addHandleOptions
  };

}