import {
  destroy, generateComponentId,
  getComponentWindow, onEventHandler, unmount
} from '../createComponent';
import {sanitizeOptionsForType} from '../../app/components/options';
import dataStore from '../dataStore';
import postRobot from '../../post-robot-wrapper';
import {config} from '../../app/components/config';
import {sendEventData} from '../../app/components/input-events';
import {getLocaleFromLanguage} from '../../app/components/localization/localized-messages';
import {getWireTransferContent} from './wireTransferTemplate';
import {getBPayContent} from './bPayTemplate';
import {getKonbiniContent} from './konbiniTemplate';
import {getBoletoBancarioContent} from './boletoBancarioTemplate';

let componentData = {};
let controllerEmitter;


const COMPONENT_TYPE = 'delayedpaymentinstructions';

/**
 *
 * @param controllerId
 * @param key
 * @param options
 * @returns {{unmount: unmount, controllerId: (*|null), options: (*|*), destroy: destroy, update: update, id: string, parentNode: null, type: string, mount: mountElement, key: *, on: onEventHandler}}
 */

export function createDelayedPaymentInstructions(controllerId, key, options) {
  const id = generateComponentId(COMPONENT_TYPE);


  if (!options.sourceClientSecret) {
    throw new Error('You must provide a sourceClientSecret');
  }

  if (!options.sourceId) {
    throw new Error('You must provide a sourceId');
  }

  const controllerWindow = getComponentWindow(controllerId);


  if (!controllerWindow) {
    throw new Error(`Unable to locate controller '${controllerId}'`);
  }


  const delayedPaymentInstructionsComponent = {
    id: id,
    key: key,
    type: COMPONENT_TYPE,
    parentNode: null,
    controllerId: controllerId,
    mount: mountElement,
    destroy: destroyElement,
    on: onEventHandler,
    options: sanitizeOptionsForType(options, COMPONENT_TYPE),
    unmount: unmountElement,
    window: Window
  };

  componentData = {
    componentType: COMPONENT_TYPE,
    componentId: id,
    controller: {
      id: controllerId,
      window: controllerWindow
    },
    options: delayedPaymentInstructionsComponent.options,
    key: key
  };


  controllerEmitter = postRobot.client({ window: componentData.controller.window, domain: config.domain });
  return delayedPaymentInstructionsComponent;
}


export function addContainerToDom(parentId, componentId) {
  document.getElementById(parentId).innerHTML += `<div id="${componentId}" class="DR-delayed-payment-instructions-container"></div>`;
}

function getContainerElement() {
  return document.getElementById(componentData.componentId);
}

export function handleOptions(data) {
  const locale = data.instanceOptions.locale.length === 2 ? getLocaleFromLanguage(data.instanceOptions.locale) : data.instanceOptions.locale;
  const parentNodeId = componentData.parentNodeId;
  const {controller, options, componentId} = componentData;
  const {sourceId, sourceClientSecret} = options;

  componentData.instanceOptions = data.instanceOptions;
  componentData.options = data.options;

  addContainerToDom(parentNodeId, componentId);

  const container = getContainerElement();
  return getSource(sourceId, sourceClientSecret, controller).then((response) => {
    container.innerHTML = getInstructionsByType(response.source, locale);
    emitComponentReady();
  });

}

export function mountElement(parentNodeId) {
  const parent = document.getElementById(parentNodeId);
  componentData.window = window;
  const data = dataStore.get(this.key);

  data.components[this.type] = { 'parent': parent, 'options': sanitizeOptionsForType(this.options, this.type) };
  dataStore.set(this.key, data);

  this.parentNode = parent;

  componentData.parentNodeId = parentNodeId;

  return controllerEmitter.send('mountClientComponent', {
    componentId: componentData.componentId,
    componentType: componentData.componentType
  }).then((response) => {
    return handleOptions(response.data);
  });

}


export function unmountElement() {
  unmount.call(this);
}

function destroyElement() {
  return destroy.call(this);
}


export function emitComponentReady() {
  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'ready', {});
}


function getSource(sourceId, sourceClientSecret, controller) {

  const controllerWindow = getComponentWindow(controller.id);

  const data = {
    sourceId: sourceId,
    sourceClientSecret: sourceClientSecret.split('_')[1]
  };

  return postRobot.send(controllerWindow, 'retrieveSource', data, { timeout: 10000 })
    .then(response => {
      return response.data;
    });
}

function getInstructionsByType(source, locale) {
  const {type} = source;
  if (type === 'wireTransfer') {
    return getWireTransferContent(source, locale);
  } else if (type === 'bPay') {
    return getBPayContent(source, locale);
  } else if (type === 'konbini') {
    return getKonbiniContent(source, locale);
  }else if (type === 'boletoBancario') {
    return getBoletoBancarioContent(source);
  } else {
    return '';
  }
}

