export function getBoletoBancarioContent(source) {
  const firstName = source.owner.firstName;
  const {document:boletoDocument, documentCode:boletoDocumentCode} = source.boletoBancario;

  return `
    <p>Olá ${firstName},</p>


    <p>A sua compra foi iniciada e está aguardando o pagamento via boleto bancário.</p>
    
    <p id="DR-boletoBancarioInformation">
        <strong>Linha digitável:</strong> ${boletoDocumentCode}<br/>
        <strong>Link do boleto:</strong> <a href="${boletoDocument}" target="_blank">${boletoDocument}</a>
    </p>
    
    <p>Por favor imprima uma cópia do boleto para efetuar o pagamento ou utilize a linha digitável acima para pagar via Internet Banking. 
    Caso o pagamento não seja processado em até 5 dias úteis, seu pedido será automaticamente cancelado.</p>
    
    <p>Sugerimos imprimir uma cópia do boleto para seus registros.</p>
    
    <p>Agradecemos a sua compra.</p>
  `;
}