import {createTaxIdentifier} from '../client/tax-identifier/tax-identifier';
import {createApplePay} from '../client/applepay/applepay';
import {createCreditCard} from './components/creditcard/creditcard';
import {createCompliance} from '../client/compliance/compliance';
import {createDelayedPaymentInstructions} from '../client/delayedPaymentInstructions/delayedPaymentInstructions';
import {createMSTS} from '../client/msts/msts';
import {createOfflineRefund} from '../client/offline-refund/offline-refund';


export function isValidComponent(key) {
  return typeof config()[key] !== 'undefined'; 
}

export function canClientCreateComponent(key) {
  return isValidComponent(key) && config()[key].canClientCreate !== false;
}

export function config() {
  return {
    'controller' : {
      canClientCreate: false,
      iframe: '/controller/controller.html'
    },
    'td' : {
      canClientCreate: false,
      iframe: '/td/td.html'
    },
    'dr3dsecure': {
      canClientCreate: false,
      iframe: '/3dsecure/dr3dsecure.html'
    },
    'cardnumber': {
      iframe: '/cc-number/cc-number.html'
    },
    'cardexpiration': {
      iframe: '/cc-expiry/cc-expiry.html'
    },
    'cardcvv': {
      iframe: '/cc-cvv/cc-cvv.html'
    },
    'creditcard': {
      create: function () { return createCreditCard; },
    },
    'googlepay': {
      iframe: '/google-pay/google-pay.html',
      ignoreEmptyCssClass: true,
      ignoreBaseCssClass: true
    },
    'iban': {
      iframe: '/iban/iban.html'
    },
    'applepay': {
      create:  function () { return createApplePay().createApplepayComponent; },
      ignoreEmptyCssClass: true,
      ignoreBaseCssClass: true
    },
    'onlinebanking': {
      iframe: '/online-banking/online-banking.html'
    },
    'koreancard': {
      iframe: '/koreancard/koreancard.html'
    },
    'konbini': {
      iframe: '/konbini/konbini.html'
    },
    'offlinerefund': {
      ignoreEmptyCssClass: true,
      ignoreBaseCssClass: true,
      create: function () { return createOfflineRefund; }
    },
    'paypal': {
      iframe: '/paypal/paypal.html',
      ignoreEmptyCssClass: true,
      ignoreBaseCssClass: true
    },
    'paypalcredit': {
      iframe: '/paypal/paypal.html',
      ignoreEmptyCssClass: true,
      ignoreBaseCssClass: true
    },
    'compliance': {
      create: function () { return createCompliance().createComponent; },
      ignoreEmptyCssClass: true,
      ignoreBaseCssClass: true
    },
    'taxidentifier': {
      create: function () { return createTaxIdentifier; },
      ignoreEmptyCssClass: true,
      ignoreBaseCssClass: true
    },
    'msts': {
      create: function () { return createMSTS; }
    },
    'delayedpaymentinstructions': {
      create: function () { return createDelayedPaymentInstructions; },
      ignoreEmptyCssClass: true,
      ignoreBaseCssClass: true
    },
    'dynamicfield': {
      iframe: '/dynamic-field/dynamic-field.html',
      canMountMoreThanOne: true
    },
    'dynamicselect': {
      iframe: '/dynamic-select/dynamic-select.html',
      canMountMoreThanOne: true
    },
  };
}

function hasIframe(config) {
  return typeof config.iframe !== 'undefined';
}

export function isClientSideComponent(key) {
  const currentConfig = config();
  const clientComponents = Object.keys(currentConfig).filter(key => !hasIframe(currentConfig[key]));
  return clientComponents.includes(key);
}

export function getIframeSrc(key) {
  return getComponentConfig(key).iframe;
}

export function getComponentConfig(key) {
  return config()[key];
}

export function getComponentCreate(key) {
  return { create: getComponentConfig(key).create() };
}

export function canMountMoreThanOne(key) {
  const componentConfig = getComponentConfig(key);
  return typeof componentConfig !== 'undefined' ? componentConfig.canMountMoreThanOne === true : false;
}
export function shouldAddEmptyClass(key) {
  return typeof getComponentConfig(key).ignoreEmptyCssClass === 'undefined' || getComponentConfig(key).ignoreEmptyCssClass === false;
}

export function shouldAddBaseClass(key) {
  return typeof getComponentConfig(key).ignoreBaseCssClass === 'undefined' || getComponentConfig(key).ignoreBaseCssClass === false;
}
