import Fingerprint2 from 'fingerprintjs2';
import {collectClientData, getBattery} from '../../../beacon/beacon-client-data';
import {getComponentWindow} from '../../../client/createComponent';
import postRobot from '../../../post-robot-wrapper';

const fingerprintOptions = {
  excludes: {
    data: true,
    fontsFlash: true
  }
};

/**
 * Extract details from Fingerprintjs and send to sendBeaconEventToController
 * @param {string} beaconId
 * @param {string} eventName
 * @param {string} sourceId
 */
export function sendBeaconEventDetails(beaconId, eventName, sourceId = '') {
  const beaconWindow = getComponentWindow(beaconId);

  if (!beaconWindow) {
    throw new Error(`Unable to locate controller '${beaconId}'`);
  }
  // Send component Id to the controller, we return a promise but you don't really need to wait?
  Fingerprint2.get(fingerprintOptions, function (components) {
    let fingerPrintDetails = Object.assign({});
    fingerPrintDetails['deviceFingerprint'] = Fingerprint2.x64hash128(components.map(function (pair) { return pair.value;}).join(), 31);
    components.forEach((item) => {
      fingerPrintDetails[item.key] = item.value;
    });
    sendBeaconEventToController(beaconWindow, eventName, sourceId, fingerPrintDetails);
  });

}

/**
 * Sends beacon event to controller
 * @param {string} beaconWindow
 * @param {string} eventName
 * @param {string} sourceId
 * @param {object} fingerPrintDetails
 */
export function sendBeaconEventToController(beaconWindow, eventName, sourceId, fingerPrintDetails) {
  if(typeof window.navigator.getBattery == 'function') {
    getBattery(window).then(function (battery) {
      let batteryDetails = {
        batteryLevel: battery.level,
        batteryCharging: battery.charging ? 'True' : 'False'
      };
      return postRobot.send(beaconWindow, 'sendBeaconData', {
        eventName: eventName,
        sourceId: sourceId,
        clientData: collectClientData(window),
        batteryData:batteryDetails,
        fingerPrintData:fingerPrintDetails
      });
    });
  } else {
    return postRobot.send(beaconWindow, 'sendBeaconData', {
      eventName: eventName,
      sourceId: sourceId,
      clientData: collectClientData(window),
      fingerPrintData:fingerPrintDetails
    });
  }

}