import {getDelayedPaymentInstructionsLocalizedStrings} from './delayedPaymentInstructions-localization-data';

export function getKonbiniContent(source, locale) {
  const translationsNeeded = [
    'amount',
    'konbiniInsructionsMoreDetail',
    'konbiniInstructions',
    'konbiniPaymentLocation',
    'konbiniPrintableInvoice',
    'konbiniReferenceNumber',
    'konbiniSevenDailyInstructions',
  ];
  const {
    amount,
    konbiniInsructionsMoreDetail,
    konbiniInstructions,
    konbiniPaymentLocation,
    konbiniPrintableInvoice,
    konbiniReferenceNumber,
    konbiniSevenDailyInstructions
  } = getDelayedPaymentInstructionsLocalizedStrings(translationsNeeded, locale);

  const detailedInstructions = source.konbini.hasOwnProperty('printableInvoiceUrl') ? `
    <strong>${konbiniPrintableInvoice}</strong> <a href="${source.konbini.printableInvoiceUrl}" target="_blank">${source.konbini.printableInvoiceUrl}</a>
    <p>${konbiniSevenDailyInstructions}</p>` : `<p>${konbiniInsructionsMoreDetail}</p>`;


  return `
    <p>${konbiniInstructions}</p>
    <p id="DR-konbiniAmountAndReferenceId">
        <strong>${amount}</strong> ${source.amount} ${source.currency}
        <br/>
        <strong>${konbiniReferenceNumber}</strong> ${source.konbini.receiptNumber}
        <br/>
        <strong>${konbiniPaymentLocation}</strong> ${source.konbini.localizedStoreName}
        <br/>
        ${detailedInstructions}
    </p>
    
  `;
}