import dataStore from './dataStore';
import {
  createComponent,
  createOverlayDiv,
  getComponentWindow,
  registerComponentWithController,
  updateOverlay
} from './createComponent';
import postRobot from '../post-robot-wrapper';

function getKoreanCardOptions(components) {
  if (typeof components.cardnumber === 'undefined') {
    return {
      style: {base: {height:'400px'}}
    };
  }
  let options = Object.assign({}, components.cardnumber.options);
  try {
    options.style.base.height = '400px';
  } catch(err) {
    options = Object.assign(options, {style: {base: {height:'400px'}}});
  }
  delete options.placeholderText;
  delete options.style.base[':hover'];
  return options;
}

export function createKoreanCard(key, sourceRequest, resolve) {
  const {controller, components} = dataStore.get(key);
  if(!document.getElementById('DRKoreanCard')) {
    createOverlayDiv('DRKoreanCard');
  }
  updateOverlay('DRKoreanCard', '100%', 'rgba(0,0,0,0.3)');
  const options = getKoreanCardOptions(components);
  const koreanCard = createComponent('koreancard', controller.id, key, options);

  registerComponentWithController(controller.id, koreanCard, options);
  koreanCard.mount('DRKoreanCard');
  koreanCard.on('ready', function() {
    sendShowKoreanCardOverlay(controller.id, resolve);
  });

  return koreanCard;
}

function sendShowKoreanCardOverlay(controllerId, resolve) {
  const controllerWindow = getComponentWindow(controllerId);
  const data = {
    resolve: resolve
  };

  return postRobot.send(controllerWindow, 'showKoreanCardOverlay', data);
}

export function handleKoreanCard(self, key, sourceRequest, controller, componentInstanceOrSourceData, beaconComponent, handleCreateSource) {
  return new Promise(function (resolve) {
    self.koreanCard = createKoreanCard(key, sourceRequest, resolve);
  }).then((responseData) => {
    updateOverlay('DRKoreanCard', '0', '');
    if (responseData.error) {
      return responseData;
    } else {
      return handleCreateSource(controller.id, componentInstanceOrSourceData.type, sourceRequest, beaconComponent);
    }
  }).then(function (response) {
    self.koreanCard.destroy();
    return response;
  });
}

export function isKoreanCard(sourceRequest) {
  try {
    const type = sourceRequest.type;
    const country = sourceRequest.owner.address.country;
    return type === 'creditCard' && country === 'KR';
  } catch (err) {
    return false;
  }
}