import { v4 as uuidv4 } from 'uuid';

let state = {};

function get(key) {
  return state[key];
}

function set(key, data) {
  if (typeof data === 'undefined') {
    throw Error('Pass data to set please.');
  }

  if (typeof data !== 'object') {
    state[key] = data;
  } else {
    state[key] = Object.assign({}, data);
  }
}

function create(data) {
  let key = uuidv4();
  set(key, data);
  return key;
}

function reset() {
  state = {};
}

function getAll() {
  return state;
}

export default {
  get,
  set,
  create,
  reset,
  getAll
};
