import { v4 as uuidv4 } from 'uuid';
import { createFrame } from './createFrame';
import postRobot from '../post-robot-wrapper';
import { getFrameByName, getParent } from 'cross-domain-utils';
import dataStore from './dataStore';
import { createController } from './createController';
import { removeEventsForComponent } from './event-middleware';
import { getCssClasses, getElementHeight, removeClasses, getActiveClasses, applyActiveClasses } from './css-class-utils';
import { config } from '../app/components/config';
import {mergeOptions, sanitizeOptionsForType } from '../app/components/options';
import {collectClientData, getUserLocale} from '../beacon/beacon-client-data';
import {chooseCreateSourceCatchMessage} from '../app/components/controller/controller-create-source-utils';
import {fieldKey} from '../app/key-helper';
import {getIframeSrc, isValidComponent, shouldAddBaseClass, shouldAddEmptyClass} from '../app/config';

const MAX_TRIES = 5;

export function getComponentsBasePath() {
  return config.domain + config.basePath + '/' + config.componentsVersionId + '/components';
}

const eventNames = [
  'blur',
  'change',
  'focus',
  'ready',
  'click',
  'source',
  'shippingaddresschange',
  'shippingoptionchange',
  'cancel',
  'return'
];

/**
 * Stores event handlers in data store
 * @param {string} eventName
 * @param {Function} eventFunction
 */
export function onEventHandler(eventName, eventFunction) {
  if (eventNames.indexOf(eventName) === -1) {
    throw new Error(`Unknown event name: ${eventName}`);
  }

  const key = this.key;
  const data = dataStore.get(this.key);

  data.customEvents.push({
    eventType: this.type,
    componentId: this.id,
    eventName: eventName,
    eventFunction: eventFunction
  }); // Stores the custom function in the data store

  dataStore.set(key, data);
}

/**
 * Triggers component events
 * @param {string} controllerId
 * @param {string} id
 * @param {string} eventType
 * @returns {Function}
 */
function triggerEvent(controllerId, id, eventType) {
  return function () {
    postRobot.send(getComponentWindow(controllerId), 'triggerComponentEvent', {
      componentId: id,
      eventType: eventType
    });
  };
}

/**
 * unmounts an element
 */
export function unmount() {
  const parentNode = this.parentNode;
  const key = this.key;
  let data = dataStore.get(key);
  let cssClasses;

  if(data && data.hasOwnProperty('components')) {
    if(data.components[this.type] && data.components[this.type].hasOwnProperty('options')) {
      cssClasses = data.components[this.type].options.classes;
    }
  }


  if (parentNode instanceof HTMLElement === false) {
    throw new Error('This component can not be unmounted because it has not been mounted.');
  }
  try {
    postRobot.send(getComponentWindow(this.controllerId), 'unmountComponent', {
      componentId: this.id,
      componentType: this.type
    });
    if(document.getElementById(this.id)) {
      parentNode.removeChild(document.getElementById(this.id));
      removeClasses(cssClasses, parentNode);
    }
    this.parentNode = null; // this.parentNode set to null indicates that there is no parent this component is mounted to
    data.components[this.type] = null;
    dataStore.set(key, data);
  } catch (err) {
    throw new Error(`Failed to unmount component '${this.type}'.`);
  }
}

/**
 * Throws error indicating element has already been destroyed
 */
function alreadyDestroyed() {
  throw new Error('This Element has been destroyed. Please create a new Element to do this action.');
}

/**
 * Mounts element
 * @param node
 */
export function mount(node) {
  if (this.parentNode !== null) {
    throw new Error(`Can not mount, only one ${this.type} can be mounted at a time.`);
  }

  if (typeof node === 'string') {
    // Query by id
    node = document.getElementById(node);
  }

  if (node instanceof HTMLElement === false) {
    throw new Error('mount() requires a valid HTMLElement or id attribute.');
  }

  try {
    let attributes = {
      id: this.id,
      name: this.id
    };

    this.parentNode = node;

    if (this.type === 'controller') {
      // Controller
      createFrame(this.type, node, getComponentURL(this.type, this.id, this.controllerId), attributes);
    } else {
      // Other Component
      const key = this.key;
      let data = dataStore.get(key);
      if(data && data.hasOwnProperty('components')) {
        Object.keys(data.components).forEach((key) => {
          if (data.components[key] && data.components[key].parent === node) {
            console.error('Can not mount, only one element can be mounted inside an HTML element.');  //eslint-disable-line no-console
            throw new Error('Can not mount, only one element can be mounted inside an HTML element.');
          }
        });
      }
      if (typeof this.options !== 'undefined') {
        //arbitrate custom vs default options
        this.options.classes = getCssClasses(this.options.classes);
        const elementHeight = getElementHeight(this.options.style);
        // If we have options, send them and wait for them to be sent before creating the component

        if(this.type !== 'applepay' && this.type !== 'compliance') {
          createFrame(this.type, node, getComponentURL(this.type, this.id, this.controllerId), attributes, elementHeight, data.instanceOptions.locale, this.id, this.nameForAccessibility);
        }

        const dataKey = fieldKey(this.type, this.id);
        data.components[dataKey] = { 'parent': node, 'options': sanitizeOptionsForType(this.options, this.type) };

        dataStore.set(key, data);

        // Set base css class & empty class since field is empty
        if(shouldAddBaseClass(this.type)) {
          const DRElementClass = data.components[dataKey].options.classes.base;
          node.classList.add(DRElementClass);
        }

        if (shouldAddEmptyClass(this.type)) {
          const DREmptyClass = data.components[dataKey].options.classes.empty;
          node.classList.add(DREmptyClass);
        }

      } else {
        // If we don't have any options just create the frame
        if(this.type !== 'applepay' && this.type !== 'compliance') {
          createFrame(this.type, node, getComponentURL(this.type, this.id, this.controllerId), attributes);
        }
      }

    }
  } catch (err) {
    throw new Error(`Failed to mount component '${this.type}'.`); //eslint-disable-line no-console
  }
}

/**
 * Updates element functions to the alreadyDestroyed function
 */
function modifyThisForDestroy() {
  for (let member in this) {
    if (typeof this[member] === 'function') {
      this[member] = alreadyDestroyed;
    } else {
      delete this[member];
    }
  }
}

/**
 * unmounts and destroys elements
 * @returns {Promise}
 */
export function destroy() {

  if (this.parentNode) {
    this.unmount();
  }

  const controllerWindow = getComponentWindow(this.controllerId);

  if (!controllerWindow) {
    return Promise.reject(`Unable to locate controller '${this.controllerId}'`);
  }

  return postRobot.send(controllerWindow, 'unregisterComponent', {
    componentId: this.id,
    componentType: this.type
  }).then(() => {
    removeEventsForComponent(this.key, this.id);

    modifyThisForDestroy.call(this);
  });
}

/**
 * Creates a component
 * @private
 * @param {string} type
 * @param controllerId
 * @return {object} component
 */
export function createComponent(type, controllerId, key, options, nameForAccessibility) {
  if (typeof type !== 'string' || !isValidComponent(type)) {
    throw new Error('createComponent() requires a valid component type');
  }

  const formattedType = type.toLowerCase();

  let id = generateComponentId(type);
  const component = {
    id: id,
    key: key,
    type: formattedType,
    nameForAccessibility: nameForAccessibility || type,
    parentNode: null,
    controllerId: controllerId || null,
    mount: mount,
    destroy: destroy,
    on: onEventHandler,
    options: sanitizeOptionsForType(options, formattedType),
    unmount: unmount,
    blur: triggerEvent(controllerId, id, 'blur'),
    clear: triggerEvent(controllerId, id, 'clear'),
    focus: triggerEvent(controllerId, id, 'focus'),
    update: update
  };

  // If the type is a googlepay component then call googlePayCanMakePayment
  if (formattedType === 'googlepay') {
    component.canMakePayment = googlePayCanMakePayment;
    component.show = triggerEvent(controllerId, id, 'show');
  }

  if(type === 'paypal' || formattedType === 'paypalcredit') {
    if(typeof window.paypal === 'undefined' && !document.getElementById('DR-PayPal')) {
      const scriptTag = document.createElement('script');
      scriptTag.id = 'DR-PayPal';
      scriptTag.src = 'https://www.paypalobjects.com/api/checkout.js';
      document.body.appendChild(scriptTag);
    }
  }

  return component;
}

/**
 * Returns true if browser/device can support google pay
 * @returns {boolean|*}
 */
export function googlePayCanMakePayment() {
  // TODO False for IE 11 or less
  return true;
}

/**
 * Returns component url
 * @param {string} type
 * @param {string} id
 * @param {string} controllerId
 * @returns {string} component url
 */
export function getComponentURL(type, id, controllerId) {
  if (!isValidComponent(type)) {
    throw new Error('getComponentURL() requires a valid component type');
  }

  const url = getComponentsBasePath() + getIframeSrc(type) + '?componentId=' + id;
  if (type === 'controller') {
    return url;
  } else {
    return url + '&controllerId=' + controllerId;
  }
}

/**
 * Returns component id
 * @param {string} type
 * @returns {string} componentId
 */
export function generateComponentId(type) {
  if(type === 'td' || type === 'dr3dsecure') {
    return type;
  } else {
    return type + '-' + uuidv4();
  }
}

/**
 * Gets a window from a component name or id for use with postRobot
 * @param {String} id a component id
 * @returns {Window}
 */
export function getComponentWindow(id) {
  return findWindow(window, id, 0);
}

/**
 * Returns Window
 * @param {Window} currentWindow
 * @param {string} id
 * @param {number} tries
 * @returns {*}
 */
export function findWindow(currentWindow, id, tries = 0) {

  if (tries >= MAX_TRIES) {
    return getFrameByName(currentWindow, id);
  }

  const foundFrame = getFrameByName(currentWindow, id);
  if (typeof foundFrame !== 'undefined') {
    return foundFrame;
  }

  const parentWindow = getParent(currentWindow);
  if (typeof parentWindow  === 'undefined') {
    return foundFrame;
  }

  return findWindow(parentWindow, id, ++tries);

}

/**
 * Sends a message to the controller so it can track the component by type and id
 * @param {string} controllerId
 * @param {object} component
 * @param {object} options
 * @returns {Promise}
 */
export function registerComponentWithController(controllerId, component, options) {
  const controllerWindow = getComponentWindow(controllerId);

  if (!controllerWindow) {
    throw new Error(`Unable to locate controller '${controllerId}'`);
  }

  // Send component Id to the controller, we return a promise but you don't really need to wait?
  return postRobot
    .send(controllerWindow, 'registerNewComponent', {
      componentType: component.type,
      componentId: component.id,
      options: options
    })
    .catch(() => {
      throw new Error('Unable to register component.');
    });
}

/**
 * Sends options to controller
 * @param {string} controllerId
 * @param {object} component
 * @param {object} unsafeOptions
 * @returns {Promise<T | never> | * | *}
 */
export function sendOptions(controllerId, component, unsafeOptions) {
  const controllerWindow = getComponentWindow(controllerId);

  if (!controllerWindow) {
    throw new Error(`Unable to locate controller '${controllerId}'`);
  }
  // Send component Id to the controller, we return a promise but you don't really need to wait?
  return postRobot.send(controllerWindow, 'sendOptions', {
    componentType: component.type,
    componentId: component.id,
    unsafeOptions: unsafeOptions
  }).catch(() => {
    throw new Error('Sending options error');
  });
}

/**
 * Sends apiKey to controller
 * @param {string} controllerId
 * @param {string} eventName
 * @param {object} data
 * @returns {Promise<T | never> | * | *}
 */
export function sendApiKey(controllerId, eventName, data) {
  const controllerWindow = getComponentWindow(controllerId);

  if (!controllerWindow) {
    throw new Error(`Unable to locate controller '${controllerId}'`);
  }
  // Send component Id to the controller, we return a promise but you don't really need to wait?
  return postRobot.send(controllerWindow, eventName, data).catch(() => {
    throw new Error('Sending apiKey error');
  });
}

/**
 * Extract iframe from dom and return.
 * @param {string} type
 * @returns {Element}
 */
export function getComponentIFrame(type) {
  let iFrameWindow = document.getElementsByTagName('iframe');

  for( let win = 0; win < iFrameWindow.length; win++) {
    if(iFrameWindow[win].id.startsWith(type)) {
      return iFrameWindow[win];
    }
  }
}

/**
 * This method is used to create/extract the beacon component.
 * @returns {Element}
 */
export function createOrExtractBeaconController() {
  let type = 'td';
  let beaconComponent = getComponentIFrame(type);

  if(!beaconComponent) {
    beaconComponent = createController(document.body, type);
  }

  return beaconComponent;
}

/**
 * This method is used to create a div with adyen style and id.
 * @param {string} overlayId
 * @returns {Element}
 */
export function createOverlayDiv(overlayId) {
  let overlay;
  let overlayStyle = 'position: fixed;top: 0;width:100%;z-index: 10000;overflow:hidden;display:flex;justify-content:center;align-items:center';
  overlay = document.createElement('div');
  overlay.setAttribute('id', overlayId);
  overlay.setAttribute('style', overlayStyle);
  overlay.setAttribute('class', 'DR-spinner-overlay');
  document.body.appendChild(overlay);
  return overlay;
}

/**
 * This method is used to create/extract the adyen component.
 * @returns {Element}
 */
export function createOrExtractAdyenController() {
  let type = 'dr3dsecure';
  let adyenComponent = getComponentIFrame(type);

  if(!adyenComponent) {
    let adyenDiv = createOverlayDiv('DRPayment3ds');
    adyenComponent = createController(adyenDiv,type);
  }

  return adyenComponent;
}

/**
 * Updates a component with new options
 * @param {object} options
 */
export function update(options) {
  const key = this.key;
  const data = dataStore.get(key);
  const el = data.components[fieldKey(this.type, this.id)].parent;
  const activeClasses = getActiveClasses(this.options.classes, el);
  removeClasses(data.components[fieldKey(this.type, this.id)].options.classes, el);
  const mergedOptions = mergeOptions(data.components[fieldKey(this.type, this.id)].options, options);
  // we need to run classes through default/custom arbitration
  mergedOptions.classes = getCssClasses(mergedOptions.classes);
  sendOptions(this.controllerId, { 'type': this.type, 'id': this.id}, mergedOptions);
  data.components[fieldKey(this.type, this.id)].options = sanitizeOptionsForType(mergedOptions, this.type);
  delete data.components[fieldKey(this.type, this.id)].options.sourceData; // Note: Do not store the sourceData
  dataStore.set(key, data);
  this.options = data.components[fieldKey(this.type, this.id)].options;
  if(this.type !== 'onlinebanking') {
    //online banking update means changing select options, so we have to set classes back to empty
    applyActiveClasses(activeClasses, this.options.classes, el);
  } else {
    applyActiveClasses(['base', 'empty'], this.options.classes, el);
  }

}
/**
 * Initalize 3d secure div on instantiation
 * @param {string} adyenId
 * @param {string} apiKey
 */
export function sendInitalize3dSecure(adyenId, apiKey) {
  const adyenWindow = getComponentWindow(adyenId);

  if (!adyenWindow) {
    throw new Error(`Unable to locate 3ds '${adyenId}'`);
  }

  return postRobot.send(adyenWindow, 'sendInitalize3dSecure', {
    secureId: adyenId,
    apiKey: apiKey,
    userLocale: getUserLocale(window)
  }).catch(() => {
    return chooseCreateSourceCatchMessage('Unable to configure Adyen.');
  });

}

/**
 * Sends adyen configuration details to adyen controller
 * @param {string} adyenId
 * @param {string} controllerId
 * @param {object} paymentServiceResponse
 * @param {function} resolve
 * @param {object} redirectWindow
 */
export function sendAdyen3dDetails(adyenId, controllerId, paymentServiceResponse, resolve, redirectWindow) {
  const adyenWindow = getComponentWindow(adyenId);

  if (!adyenWindow) {
    throw new Error(`Unable to locate 3ds '${adyenId}'`);
  }

  return postRobot.send(adyenWindow, 'sendActions', {
    controllerId: controllerId,
    secureId: adyenId,
    resolve: resolve,
    paymentServiceResponse: paymentServiceResponse,
    clientData: collectClientData(window),
    redirectWindow: redirectWindow
  }).catch(() => {
    return chooseCreateSourceCatchMessage('Unable to configure Adyen.');
  });

}

/**
 * Sends adyen challenge shopper details to adyen controller
 * @param {string} adyenId
 * @param {string} controllerId
 * @param {object} response
 * @param {function} resolve
 */
export function sendAdyenAction(adyenId, controllerId, response, resolve) {
  const adyenWindow = getComponentWindow(adyenId);

  if (!adyenWindow) {
    throw new Error(`Unable to locate 3ds '${adyenId}'`);
  }

  return postRobot.send(adyenWindow, 'sendActions', {
    controllerId: controllerId,
    secureId: adyenId,
    resolve: resolve,
    paymentServiceResponse: response,
    clientData: collectClientData(window),
  }).catch(() => {
    return chooseCreateSourceCatchMessage('Unable to intialize adyen challenge.');
  });

}

/**
 * Update the height & background-color of overlay div
 * @param {string} overlayId
 * @return {HTMLElement}
 */
export function updateOverlay(overlayId, height, bgColor) {
  let overLayDiv = document.getElementById(overlayId);

  if(overLayDiv !== null && overLayDiv.style !== undefined) {
    overLayDiv.style.height = height;
    overLayDiv.style.backgroundColor = bgColor;
  }

  return overLayDiv;
}