import {getComponentWindow} from './createComponent';
import postRobot from '../post-robot-wrapper';

/**
 *
 * @param controllerId
 * @param sessionId
 * @param country
 * @param currency
 * @param locale
 * @param supportsStorage
 * @param recurringModel
 * @param supportsRecurring
 * @param supportsFreeTrial
 * @returns {*}
 */
export function getPaymentMethods(controllerId, sessionId, country, currency, locale, supportsStorage, recurringModel, supportsRecurring, supportsFreeTrial) {

  const controllerWindow = getComponentWindow(controllerId);

  if (!controllerWindow) {
    throw new Error(`Unable to locate controller '${controllerId}'`);
  }

  // Send message to Controller Frame to call createSource
  return postRobot.send(controllerWindow, 'fetchAvailablePaymentMethods', {
    sessionId: sessionId,
    country: country,
    currency: currency,
    locale: locale,
    supportsFreeTrial: supportsFreeTrial,
    supportsRecurring: supportsRecurring,
    supportsStorage: supportsStorage,
    recurringModel: recurringModel
  })
    .then(response => {
      return response.data;
    });

}
