import {config} from '../config';
import {paymentServiceRequest, paymentServiceGetRequest} from '../../payment-service-request';
import {chooseCreateSourceCatchMessage} from './controller-create-source-utils';
import {
  getComponentWindow,
  sendAdyenAction
} from '../../../client/createComponent';
import postRobot from '../../../post-robot-wrapper';

export function runSessionAuthenticationAndHandleResponse(apiKey, data) {

  const paymentApiUrl = `${config.paymentServiceBaseUrl}/session-authentications`;
  return paymentServiceRequest(data, apiKey, paymentApiUrl)
    .then((response) => {
      return response.data;
    })
    .catch((error) => {
      return chooseCreateSourceCatchMessage(error);
    });
}


export function handleAdyenResponseForSessionAuthentication(adyenResponseType, adyenResponseChallengeResult, instanceData) {
  const {clientSecret, id} = instanceData.paymentServiceResponse;

  if(!clientSecret || !id) {
    return Promise.reject('Cannot send data to paymentservice because required data is missing.');
  }

  const secret = clientSecret.split('_')[1];
  const data = {
    [adyenResponseType]: adyenResponseChallengeResult
  };

  return authenticateSessionWithAdyen(instanceData.controllerId, data, secret, id)
    .then(response => {
      if(typeof response !== 'undefined' && response.state === 'requires_action' && response.nextAction !== null) {
        response.requestType = 'handleNextAction';
        sendAdyenAction(instanceData.componentId, instanceData.controllerId, response, instanceData.resolve);
        return response;
      } else {
        instanceData.resolve(response);
        return response;
      }
    });
}


export function authenticateSessionWithAdyen(controllerId, data, clientSecret, id) {

  const controllerWindow = getComponentWindow(controllerId);

  if (!controllerWindow) {
    throw new Error(`Unable to locate controller '${controllerId}'`);
  }

  return postRobot.send(controllerWindow, 'authenticateSessionFromAdyenRequest', { data, clientSecret, id }, { timeout: 10000 })
    .then(response => {
      const {state} = response.data;
      if(state !== 'requires_action') {
        return {
          status: state
        };
      } else {
        return response.data;
      }

    })
    .catch((error) => {
      return chooseCreateSourceCatchMessage(error);
    });
}

export function runAuthenticateSessionFromAdyenRequestAndHandleResponse(apiKey, data, clientSecret, id) {
  const paymentApiUrl = `${config.paymentServiceBaseUrl}/session-authentications/${id}?secret=${clientSecret}`;
  return paymentServiceRequest(data, apiKey, paymentApiUrl)
    .then((response) => {
      return response.data;
    })
    .catch((error) => {
      return chooseCreateSourceCatchMessage(error);
    });
}

export function runHandleCheckSessionAuthenticationStatus(apiKey, id, clientSecret) {
  const paymentApiUrl = `${config.paymentServiceBaseUrl}/session-authentications/${id}?secret=${clientSecret}`;
  return paymentServiceGetRequest(apiKey, paymentApiUrl)
    .then((response) => {
      return response.data;
    })
    .catch((error) => {
      return chooseCreateSourceCatchMessage(error);
    });
}