import postRobot from '../../post-robot-wrapper';
import creditCardTypeDetection from './credit-card-type';
import validator from './validator';
import {isShallowEquivalent} from './utils';
import {getLocaleMessage} from './localization/localized-messages';
import {createEvent} from './cross-browser-support';
import {isClientSideComponent} from '../config';
import {setAriaInvalid} from './accessibility';

/**
 * Sends event data to controller
 * @param {object} controllerDetails
 * @param {string} componentId
 * @param {string} componentType
 * @param {string} event - name of event type
 * @param {object} data - data to send on event
 */
export function sendEventData(controllerDetails, componentId, componentType, event, data = {}) {
  // send component type for all events
  const dataToSend = Object.assign({}, data);
  dataToSend.elementType = componentType;

  const message = !isClientSideComponent(componentType) ? 'componentEventToController' : 'clientComponentEventToController';

  postRobot.send(controllerDetails.window, message, {
    controllerId: controllerDetails.id,
    componentId: componentId,
    componentType: componentType,
    eventType: event,
    eventData: dataToSend
  });
}

/**
 * Returns validation function for the given type
 * @param {string} type
 * @returns {function}
 */
function chooseValidation(type) {
  switch (type) {
    case 'cardnumber' : {
      return validator.validateCreditCard;
    }
    case 'cardexpiration' : {
      return validator.validateExpiry;
    }
    case 'cardcvv' : {
      return validator.validateCsv;
    }
    case 'onlinebanking' : {
      return validator.validateOnlineBanking;
    }
    case 'konbini' : {
      return validator.validateKonbini;
    }
    case 'taxidentifier' : {
      return validator.validateTaxIdentifier;
    }
    default: throw new Error('Invalid component type, try a valid component type such as cardnumber, cardcvv, cardexpiration, onlinebanking, konbini and taxidentifier.');
  }
}

function validate(componentData, value, type, brand) {
  if (componentData.dynamic === true) {
    return validator.validateTaxIdentifier(value, componentData.validationPattern);
  } else if (type === 'iban') {
    return validator.validateIban(value, componentData.isValid);
  } else {
    const validationMethod = chooseValidation(type);
    if (brand !== null) {
      return validationMethod(value, brand);
    } else if (typeof componentData.validationPattern !== 'undefined') {
      return validationMethod(value, componentData.validationPattern);
    } else {
      return validationMethod(value);
    }
  }
}

/**
 * Generates event data for input elements
 * @param {string} type - component type
 * @param {string} value - field value
 * @param {string} eventType - type of event
 * @param {object} componentData
 * @param {string} eventTrigger - name of event trigger
 * @returns {{complete: boolean, error: *, empty: boolean}}
 */
function generateEventData(type, value, eventType, componentData, eventTrigger) {
  const previousState = componentData.prevState;

  // if Credit card number, then add type
  let brand = null;
  if (type === 'cardnumber') {
    brand = creditCardTypeDetection.detectType(value);
  }

  if (type === 'cardcvv' && componentData.creditCardData) {
    brand = componentData.creditCardData.brand;
  }

  const validated = validate(componentData, value, type, brand);

  const isRequired = typeof componentData.required !== 'undefined' ? componentData.required === true : true;

  const isChangeEventForcedErrorToFalse = eventTrigger === 'clear' || (isRequired && eventType === 'change' && eventTrigger !== 'showError' && (validated.errorType.startsWith('incomplete_') || validated.errorType === 'ELEMENT_DATA_REQUIRED'));
  const isError = isChangeEventForcedErrorToFalse ? false : validated.error;
  const isComplete = isChangeEventForcedErrorToFalse ? false : isError === false;

  // On Change event - If a value has not been set (no change event has been fired) then return empty as false
  const isEmpty = eventType === 'change' && eventTrigger !== 'showError' && typeof previousState.empty === 'undefined' ? false : value.length === 0;

  let errorObject = null;
  if (isError && isRequired && (value.length > 0 || eventTrigger === 'showError')) { // Required errors are ignored.

    const locale = componentData.instanceOptions.locale;
    const localizedMessage = getLocaleMessage(locale, validated.messageCode); // TODO Need to check if locale exists

    const isDynamicFieldChangeEvent = eventType === 'change' && typeof componentData.validationPattern !== 'undefined';
    if (!isDynamicFieldChangeEvent) {
      errorObject = {
        type: 'validation_error',
        code: validated.errorType,
        message: localizedMessage
      };
    }
  }

  const eventData = {
    'empty': isEmpty,
    'complete': isComplete,
    'error':  errorObject,
  };

  if (type === 'cardnumber') {
    eventData.brand = brand;
  }

  if (componentData.componentType === 'onlinebanking' || componentData.componentType === 'konbini') {
    eventData.value = value === '' ? null : value;
  }

  if (componentData.sendValue === true) {
    eventData.value = value;
  }

  return eventData;

}

/**
 * Applies appropriate styles to input based on event data
 * @param {object} eventData
 * @param {Event} event
 */
export function applyInputStylesToEvent(eventData,  event) {
  let element = event.srcElement;
  //removing classes in 3 lines instead of one because IE11 won't support all in one line

  if (element) {
    element.classList.remove('complete');
    element.classList.remove('invalid');
    element.classList.remove('empty');
    if (eventData.empty) {
      element.classList.add('empty');
    }
    if (eventData.complete) {
      element.classList.add('complete');
    }
    if (eventData.error) {
      element.classList.add('invalid');
    }
  }
}

/**
 * Applies appropriate aria-invalid values to input based on event data
 * @param {object} eventData
 * @param {Event} event
 */
export function applyInputAriaInvalidIndicatorToEvent(eventData, event) {
  let element = event.target;
  eventData.error ? setAriaInvalid(element, true) : setAriaInvalid(element, false);
}

/**
 * Returns true if previous state of an input element was not empty
 * @param prevState
 * @returns {boolean}
 */
function hasPreviousState(prevState) {
  return typeof prevState !== 'undefined' && typeof prevState.empty !== 'undefined';
}

function copyEventData(data){
  return {
    empty: data.empty,
    complete: data.complete,
    error: data.error,
    brand: data.brand,
    elementType: data.elementType,
    value: data.value
  };
}

function shouldRemoveWhiteSpace(componentData) {
  return componentData.removeWhiteSpace === true;
}

function getRemovedWhiteSpaces(determinedValue) {
  return determinedValue.replace(/\s/g, '');
}

/**
 * To be used on input components.
 * Applies appropriate styles, removes whitespaces, sets prevState, applies aria-invalid attribute and sends event data to controller
 * @param {object} componentData
 * @param {string} eventType
 * @param {Event} event
 * @param {string} overrideValue
 * @returns {boolean}
 */
export function handleEvent(componentData, eventType, event, overrideValue) {

  let eventData = {};
  let privateData = null;

  if (eventType === 'change' || eventType === 'blur' || eventType === 'clear') {
    // when event data has changed since last change event
    const determinedValue = typeof overrideValue !== 'undefined' ?  overrideValue : event.target.value ;

    const removedWhiteSpaces = shouldRemoveWhiteSpace(componentData) ? getRemovedWhiteSpaces(determinedValue) : determinedValue;

    eventData = generateEventData(componentData.componentType, removedWhiteSpaces, eventType, componentData, event.trigger);
    applyInputStylesToEvent(eventData, event);
    applyInputAriaInvalidIndicatorToEvent(eventData, event);
  }

  if(event && event.hasOwnProperty('trigger') && event.trigger === 'clear') {
    sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'clear', eventData, privateData);
  }

  const eventDataForCompare = copyEventData(eventData);
  const prevStateForCompare =  hasPreviousState(componentData.prevState) ? copyEventData(componentData.prevState) : componentData.prevState;

  // For event type "change" Fire only if the event data has changed from the previous event
  if (eventType === 'change' && isShallowEquivalent(prevStateForCompare, eventDataForCompare)) {
    return false;
  }

  // If the user blurs, but something has changed, send a change event
  if (eventType === 'blur' && hasPreviousState(componentData.prevState) && !isShallowEquivalent(prevStateForCompare, eventDataForCompare)) {
    sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'change', eventData, privateData);
  }

  if (eventType === 'change' || eventType === 'blur') {
    componentData.prevState = Object.assign({}, eventData);
  }

  if (eventType === 'blur') {
    sendEventData(componentData.controller, componentData.componentId, componentData.componentType, eventType, {}, privateData);
    return false;
  }

  if(event && event.detail) {
    Object.assign(eventData, event.detail);
  }

  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, eventType, eventData, privateData);

  return true;

}


const ENTER_KEY_CODE = 13;
export function handleReturnKeyEvent(event, componentData) {
  if (event.keyCode === ENTER_KEY_CODE) {
    handleEvent(componentData, 'return');
  }
}

function getComponentError(triggerData) {
  let componentError;
  if (typeof triggerData.componentData === 'object' && triggerData.componentData.hasOwnProperty('prevState')) {
    componentError = triggerData.componentData.prevState.error;
  }
  return componentError;
}

/**
 * Runs a client triggered event on an element
 * @param {Event} event
 * @param {object} triggerData
 * @returns {*}
 */
export function runEventOnElement(event, triggerData) {
  const element = triggerData.getElement();
  switch (event) {

    case 'blur' : {
      let blurEl = element;
      if(triggerData.hasOwnProperty('getFocusElement')) {
        blurEl = triggerData.getFocusElement();
      }
      blurEl.blur();
      return;
    }
    case 'clear' : {
      element.value = '';
      const onChangeEvent = createEvent('input'); // Required to force the input event to fire
      onChangeEvent.trigger = 'clear';
      element.dispatchEvent(onChangeEvent);
      return;
    }
    case 'focus' : {
      let focusEl = element;
      if(triggerData.hasOwnProperty('getFocusElement')) {
        focusEl = triggerData.getFocusElement();
      }
      focusEl.focus();
      return;
    }
    case 'createSource' : {
      const eventType = triggerData.select === true ? 'change' : 'input';
      const onChangeEvent = createEvent(eventType); // Required to force the input event to fire
      onChangeEvent.trigger = 'showError';
      element.dispatchEvent(onChangeEvent);
      return getComponentError(triggerData);
    }
    case 'onOnlineBankingCreateSource' : {
      const onChangeEvent = createEvent('change'); // Required to force the change event to fire
      onChangeEvent.trigger = 'showError';
      element.dispatchEvent(onChangeEvent);
      return getComponentError(triggerData);
    }
    case 'onKonbiniCreateSource' : {
      const onChangeEvent = createEvent('change'); // Required to force the change event to fire
      onChangeEvent.trigger = 'showError';
      element.dispatchEvent(onChangeEvent);
      return getComponentError(triggerData);
    }
  }
}