import * as postRobot from '../post-robot-wrapper';
import {getComponentWindow} from './createComponent';
import {config} from '../app/components/config';
import {isRedirectDisabled, updateButton} from './create-dropin';

export function sendRedirectSourceData(controllerId, sourceId, clientSecret, redirectWindow, paymentMethodType) {
  const controllerWindow = getComponentWindow(controllerId);

  return postRobot.send(controllerWindow, 'handleDropInRedirect', {sourceId: sourceId, clientSecret: clientSecret, redirectWindow: redirectWindow, paymentMethodType: paymentMethodType}).catch(() => {
    throw new Error('Failed to store data');
  });
}

export function registerRedirectOnComplete(controller, options, buttonText, paymentMethodsFromAPI) {

  const controllerWindow = getComponentWindow(controller.id);

  postRobot.on('redirectComplete', { window: controllerWindow, domain: config.domain }, (event)  => {
    const {response, action, paymentMethodType} = event.data;

    const cssSpinner = document.querySelector('.DR-pay-button .DR-spinner');
    const buttonElement = cssSpinner != null ? cssSpinner.parentNode : null;

    if (action === 'return') {
      const paymentMethodFromAPI = paymentMethodsFromAPI.find( pm =>  pm.type === paymentMethodType);
      runClientProvidedCompleteEvents(options, response, buttonElement, buttonText, paymentMethodFromAPI);
    } else {
      runClientProvidedCancelEvent(options, paymentMethodType, buttonElement, buttonText);
    }
  });
}

export function isReadyForStorage(sourceData) {
  const hasSignedMandate = typeof sourceData.source.mandate !== 'undefined' && sourceData.source.mandate.hasOwnProperty('terms') && sourceData.source.mandate.hasOwnProperty('signedTime');
  return typeof sourceData.source !== 'undefined' && sourceData.source.state === 'chargeable' && hasSignedMandate === true;
}

function isSupportsStorage(paymentMethodFromAPI) {
  return typeof paymentMethodFromAPI !== 'undefined' ? paymentMethodFromAPI.supportsStorage : false;
}

export function runClientProvidedCompleteEvents(options, sourceData, buttonElement, buttonText, paymentMethodFromAPI) {

  const isSuccess = typeof sourceData.source !== 'undefined' && sourceData.source !== null;
  const redirectDisabled = isRedirectDisabled(options);

  if (options.onSuccess && isSuccess && (sourceData.source.state === 'pending_redirect' || sourceData.source.state === 'cancelled') && !redirectDisabled) {
    runClientProvidedCancelEvent(options, sourceData.source.type, buttonElement, buttonText);
  } else if (options.onSuccess && isSuccess) {
    updateButton(buttonElement, buttonText, false);
    options.onSuccess({
      source: sourceData.source,
      readyForStorage: isReadyForStorage(sourceData),
      supportsStorage: isSupportsStorage(paymentMethodFromAPI)
    });
  } else if (options.onError && !isSuccess) {
    updateButton(buttonElement, buttonText, false);
    options.onError(sourceData.error);
  }
}

export function createCancelEvent(paymentMethodType) {
  return {
    paymentMethod: paymentMethodType
  };
}

export function runClientProvidedCancelEvent(options, paymentMethodType, buttonElement, buttonText) {
  updateButton(buttonElement, buttonText, false);
  if (options.onCancel) {
    options.onCancel(createCancelEvent(paymentMethodType));
  }
}
