import {validateCreditCardLength} from './credit-card-type';

const CSV_REGEX = new RegExp('^[0-9]+$');
const CREDIT_CARD_REGEX = new RegExp('^[0-9]+$');
const EXPIRY_REGEX = new RegExp('^[0-9]{2}/[0-9]{2}$');

// Codes
const DATE_IN_PAST = 'invalid_expiration_date';
const INVALID_MONTH = 'invalid_expiration_month';

const SECURITY_CODE_INCOMPLETE_CODE = 'cardSecurityCodeInvalid';
const SECURITY_CODE_INVALID = 'cardSecurityCodeInvalid';

const CARD_NUMBER_INCOMPLETE = 'cardNumberInvalid';
const CARD_NUMBER_INVALID = 'cardNumberInvalid';

const CARD_EXPIRATION_INCOMPLETE = 'cardExpirationYearInvalid';
const CARD_EXPIRATION_INVALID = 'cardExpirationYearInvalid';
const CARD_EXPIRATION_INVALID_MONTH = 'cardExpirationMonthInvalid';

const ONLINE_BANKING_INCOMPLETE = 'noBankSelected';
const KONBINI_INCOMPLETE = 'konbiniSelectStore';

const TAX_IDENTIFIER_INCOMPLETE = 'incompleteTaxIdentifier';
const TAX_IDENTIFIER_INVALID = 'invalidTaxIdentifier';

const IBAN_INCOMPLETE = 'incompleteIban';

const ERROR_FALSE = {
  error: false,
  errorType: '',
  messageCode: ''
};

/**
 * Validates card security code value
 * @param {string} csv
 * @param {string} brand
 * @returns {object}
 */
function validateCsv(csv, brand) {

  if (typeof csv === 'undefined' || csv === '') {
    return {
      error: true,
      errorType: 'incomplete_security_code',
      messageCode: SECURITY_CODE_INCOMPLETE_CODE
    };
  } else if (!CSV_REGEX.test(csv)) {
    return {
      error: true,
      errorType: 'invalid_security_code',
      messageCode: SECURITY_CODE_INVALID
    };
  } else if (csv.length < 3 || csv.length > 4) {
    return {
      error: true,
      errorType: 'incomplete_security_code',
      messageCode: SECURITY_CODE_INCOMPLETE_CODE
    };
  }

  if(!validateCsvLength(csv, brand)) {
    return {
      error: true,
      errorType: 'incomplete_security_code',
      messageCode: SECURITY_CODE_INCOMPLETE_CODE
    };
  }

  return ERROR_FALSE;
}

/**
 * Validates card security code length
 * @param {string} cvv
 * @param {string} brand
 * @returns {boolean}
 */
function validateCsvLength(cvv, brand) {
  if(typeof brand === 'undefined' ||  brand.toLowerCase() === 'unknown') {
    return true;
  }
  return csvLength(brand) === cvv.length;
}

/**
 * Returns appropriate card security code length for brand
 * @param {string} brand
 * @returns {number}
 */
export function csvLength(brand) {
  if(brand === 'amex' || typeof brand === 'undefined' || brand === 'unknown') {
    return 4;
  } else {
    return 3;
  }
}

/**
 * Validates credit card number format
 * @param {string} number
 * @param {string} brand
 * @returns {object}
 */
function validateCreditCard(number, brand) {

  if (typeof number === 'undefined' || number === '') {
    return {
      error: true,
      errorType: 'incomplete_card_number',
      messageCode: CARD_NUMBER_INCOMPLETE
    };
  } else {
    if (!CREDIT_CARD_REGEX.test(number)) {
      return {
        error: true,
        errorType: 'invalid_card_number',
        messageCode: CARD_NUMBER_INVALID
      };
    } else if (!validateCreditCardLength(number, brand)) {
      return {
        error: true,
        errorType: 'incomplete_card_number',
        messageCode: CARD_NUMBER_INCOMPLETE
      };
    } else if (!validateCreditCardNumberMod10(number)) {
      return {
        error: true,
        errorType: 'invalid_card_number',
        messageCode: CARD_NUMBER_INVALID
      };
    }
  }

  return ERROR_FALSE;
}

/**
 * Validates expiration date
 * @param {string} expiry
 * @returns {*}
 */
function validateExpiry(expiry) {
  
  if (typeof expiry === 'undefined' || expiry === '') {
    return {
      error: true,
      errorType: 'incomplete_expiration_date',
      messageCode: CARD_EXPIRATION_INCOMPLETE
    };
  } else if (!EXPIRY_REGEX.test(expiry)) {

    if (expiry.length  !== 5) { // mm/yy (5 includes the slash)
      return {
        error: true,
        errorType: 'incomplete_expiration_date',
        messageCode: CARD_EXPIRATION_INCOMPLETE
      };
    }

    return {
      error: true,
      errorType: 'invalid_expiration_date',
      messageCode: CARD_EXPIRATION_INVALID
    };
  }

  const month = expiry.replace('/','').substr(0,2);
  const year = expiry.replace('/','').substr(2,2);

  const currentDate = new Date();
  const currentYearFirstTwoDigits = new Date().getFullYear().toString().substr(0, 2);
  const inputDate = new Date(currentYearFirstTwoDigits + year, month, 0, 0, 0, 0);// 20 appended to year

  if (inputDate < currentDate) {
    return {
      error: true,
      errorType: DATE_IN_PAST,
      messageCode: CARD_EXPIRATION_INVALID
    };
  } else {
    if (  parseInt(month,10) > 12 || parseInt(month, 10) === 0 ) {
      return {
        error: true,
        errorType: INVALID_MONTH,
        messageCode: CARD_EXPIRATION_INVALID_MONTH
      };
    }
  }

  return ERROR_FALSE;
}

/**
 * Checks to see if the number passed meets matches against The Luhn Mod-10 Method
 * @param {string} number
 * @returns {boolean}
 */
function validateCreditCardNumberMod10(number) {
  let ca, sum = 0, mul = 1;
  let len = number.length;
  while (len--) {
    ca = parseInt(number.charAt(len),10) * mul;
    sum += ca - (ca>9)*9;// sum += ca - (-(ca>9))|9
    // 1 <--> 2 toggle.
    mul ^= 3; // (mul = 3 - mul);
  }
  return (sum%10 === 0) && (sum > 0);
}

/**
 * Validates Online Banking Value
 * @param {string} code
 * @returns {object}
 */
function validateOnlineBanking(code) {
  if (typeof code === 'undefined' || code === '') {
    return {
      error: true,
      errorType: 'incomplete_online_banking',
      messageCode: ONLINE_BANKING_INCOMPLETE
    };
  }

  return ERROR_FALSE;
}

/**
 * Validates Konbini Value
 * @param {string} code
 * @returns {object}
 */
function validateKonbini(code) {
  if (typeof code === 'undefined' || code === '') {
    return {
      error: true,
      errorType: 'incomplete_konbini',
      messageCode: KONBINI_INCOMPLETE
    };
  }

  return ERROR_FALSE;
}

function validateTaxIdentifier(value, pattern) {
  if (typeof value === 'undefined' || value === '') {
    return {
      error: true,
      errorType: 'incomplete_tax_identifier',
      messageCode: TAX_IDENTIFIER_INCOMPLETE
    };
  }

  if (!value.match(pattern)) {
    return {
      error: true,
      errorType: 'invalid_tax_identifier',
      messageCode: TAX_IDENTIFIER_INVALID
    };
  }

  return ERROR_FALSE;
}

function validateIban(value, isValid) {
  if (typeof value === 'undefined' || value === '' || !isValid(value)) {
    return {
      error: true,
      errorType: 'incomplete_iban',
      messageCode: IBAN_INCOMPLETE
    };
  }
  return ERROR_FALSE;
}

export default {validateCsv, validateCreditCard, validateExpiry, validateCreditCardNumberMod10, validateOnlineBanking, validateKonbini, validateTaxIdentifier, validateIban};