import {getButtonTextByLocale} from './button-text';

export function determineButtonConfigurationText(configuration, locale) {
  const configurationSupplied = typeof configuration.options !== 'undefined' &&
    typeof configuration.options.button !== 'undefined' &&
    typeof configuration.options.button.type !== 'undefined';
  if (configurationSupplied && configuration.options.button.type.toLowerCase() !== 'custom') {
    return getButtonTextByLocale(locale, configuration.options.button.type);
  } else if (configurationSupplied &&
    configuration.options.button.type.toLowerCase() === 'custom' &&
    typeof configuration.options.button.buttonText !== 'undefined') {
    return configuration.options.button.buttonText;
  } else {
    return getButtonTextByLocale(locale, 'continue');
  }
}

