import {openWindow} from '../../app/components/controller/controller-window-opener';
import {getButtonTextByLocale} from '../dropin/button-text';

export function getMSTSEnrollmentURL(paymentMethodFromAPI, configuration) {
  if (typeof paymentMethodFromAPI.enrollmentUrl !== 'undefined') {
    return paymentMethodFromAPI.enrollmentUrl;
  }

  return typeof configuration !== 'undefined' &&
    typeof configuration.paymentMethodConfiguration !== 'undefined' &&
    typeof configuration.paymentMethodConfiguration.msts !== 'undefined' &&
    typeof configuration.paymentMethodConfiguration.msts.enrollmentUrl !== 'undefined' ? configuration.paymentMethodConfiguration.msts.enrollmentUrl : '';
}

export function createApplyNowButton(url, locale) {
  const button = document.createElement('button');
  button.className = 'DR-button-text DR-button-secondary';
  button.type = 'button';
  button.innerText = getButtonTextByLocale(locale, 'enrollNowButton');
  button.onclick = function () {
    openWindow(url);
  };
  return button;
}