import {getDelayedPaymentInstructionsLocalizedStrings} from './delayedPaymentInstructions-localization-data';

export function getWireTransferContent(source, locale) {
  const translationsNeeded = [
    'wireTransferInstructions',
    'amount',
    'wireTransferReferenceNumber',
    'includeReferenceNumber',
    'company',
    'bank',
    'location',
    'accountHolder',
    'accountNumber',
    'wireTransferIban',
    'swiftCode',
    'wireTransferInstructionsMoreDetail',
    'ibanAdditionalInformation'
  ];
  const {
    wireTransferInstructions,
    amount,
    wireTransferReferenceNumber,
    includeReferenceNumber,
    company,
    bank,
    location,
    accountHolder,
    accountNumber,
    wireTransferIban,
    swiftCode,
    wireTransferInstructionsMoreDetail,
    ibanAdditionalInformation
  } = getDelayedPaymentInstructionsLocalizedStrings(translationsNeeded, locale);

  const ibanNumber = source.wireTransfer.hasOwnProperty('iban') && typeof source.wireTransfer.iban !== 'undefined' ? `<strong>${wireTransferIban}</strong> ${source.wireTransfer.iban}` : '';
  const ibanNote = source.wireTransfer.hasOwnProperty('iban') && typeof source.wireTransfer.iban !== 'undefined' ? `<p>${ibanAdditionalInformation}</p>` : '';


  return `
    <p>${wireTransferInstructions}</p>
    <p id="DR-wireTransferAmountAndReferenceId">
        <strong>${amount}</strong> ${source.amount} ${source.currency}
        <br/>
        <strong>${wireTransferReferenceNumber}</strong> ${source.wireTransfer.referenceId}
    </p>
    <p>${includeReferenceNumber}</p>


    <p id="DR-wireTransferInformation">
    <strong>${company}</strong> ${source.wireTransfer.accountHolder}<br/>
    <strong>${bank}</strong> ${source.wireTransfer.bankName}<br/>
    <strong>${location}</strong> ${source.wireTransfer.city}<br/>
    <strong>${accountHolder}</strong> ${source.wireTransfer.accountHolder}<br/>
    <strong>${accountNumber}</strong> ${source.wireTransfer.accountNumber}<br/>
    <strong>${swiftCode}</strong> ${source.wireTransfer.swiftCode}<br/>
    ${ibanNumber}
    </p>
    
    <p>${wireTransferInstructionsMoreDetail}</p>
   
    ${ibanNote}
    
  `;
}