import dataStore from './dataStore';
import {handleCreateSourceResponse} from './DigitalRiver';
import {createOrExtractBeaconController} from './createComponent';

/**
 * Wraps updateWith function in function to validate that it is an object
 * @param {object} publicData
 */
export function wrapUpdateWith(publicData) {
  const originalUpdateWith = publicData.updateWith;

  publicData.updateWith = function (data) {

    validateIsObject(data);

    originalUpdateWith(data);
  };
}

/**
 * Validates that data is an object
 * @param {object} data
 */
function validateIsObject(data) {
  if (data !== Object(data) || Array.isArray(data)) {
    throw new Error('Invalid data. Please use Object.');
  }
}

/**
 * Validates that data has a status property
 * @param {object} data
 */
function validateStatusExists(data) {
  if (!data.status) {
    throw new Error('Please provide a status.');
  }
}

/**
 * Validates that status is either 'success' or 'failure'
 * @param {object} data
 */
function validateStatusIsCorrectValue(data) {
  if (data.status !== 'success' && data.status !== 'failure') {
    throw new Error('Status must be either a success or a failure.');
  }
}

/**
 * Wraps updateWith for shipping address event in functions to validate object type and status
 * @param {object} publicData
 */
export function wrapUpdateWithShippingAddress(publicData) {
  const originalUpdateWith = publicData.updateWith;

  publicData.updateWith = function (data) {

    validateIsObject(data);

    validateStatusExists(data);

    validateStatusIsCorrectValue(data);

    // Note throwing errors will cause it to hang. We can still call the updateWith function and handle the error on the component?

    originalUpdateWith(data);
  };
}

/**
 * Wraps updateWith in appropriate functions based on component type and event type and then runs event function
 * @param {object} data
 * @param {string} key
 * @param {string} componentType
 * @param {string} componentId
 * @param {string} eventName
 * @param {object} publicData
 */
function runListenersBasedOnType(data, key, componentType, componentId, eventName, publicData) {

  const customListenerForType = data.customEvents;
  customListenerForType.forEach(function (listenerDetails) {
    if (listenerDetails.componentId === componentId && listenerDetails.eventName === eventName) {
      try {
        if (publicData.updateWith) {

          if (eventName === 'shippingaddresschange') {
            wrapUpdateWithShippingAddress(publicData);
          } else {
            wrapUpdateWith(publicData);
          }

        }

        listenerDetails.eventFunction(publicData);
      } catch (e) {
        console.error(e.message); // eslint-disable-line no-console
      }
    }
  });
}

export function handleChangeEvent(publicData, node, cssClasses) {
  if (publicData.complete === true) {
    node.classList.add(cssClasses.complete);
  } else {
    node.classList.remove(cssClasses.complete);
  }
  if (publicData.empty === true) {
    node.classList.add(cssClasses.empty);
  } else {
    node.classList.remove(cssClasses.empty);
  }
  if (publicData.error !== null) {
    node.classList.add(cssClasses.invalid);
  } else {
    node.classList.remove(cssClasses.invalid);
  }
}

export function applyCSSClassesBasedOnEvent(data, componentType, eventName, publicData) {
  const node = data.components[componentType].parent;
  const cssClasses = data.components[componentType].options.classes;

  if (typeof cssClasses !== 'undefined') {
    if (eventName === 'focus') {
      node.classList.add(cssClasses.focus);
    } else if (eventName === 'blur') {
      node.classList.remove(cssClasses.focus);
    }

    if (eventName === 'change' || eventName === 'clear') {
      handleChangeEvent(publicData, node, cssClasses);
    }

    if (eventName === 'autofill') {
      node.classList.add(cssClasses.webkitAutofill);
    } else {
      node.classList.remove(cssClasses.webkitAutofill);
    }
  }
}

/**
 * Processes event and applies correct CSS classes to parent node
 * @param {string} key
 * @param {string} componentType
 * @param {string} componentId
 * @param {string} eventName
 * @param {object} publicData
 */
export function processEvent(key, componentType, componentId, eventName, publicData) {
  const data = dataStore.get(key);

  if (eventName === 'source' && componentType === 'googlepay' && typeof publicData.source !== 'undefined' && publicData.source !== null && publicData.source.state === 'requires_action') {
    const beaconComponent = createOrExtractBeaconController();
    if (typeof publicData.complete !== 'undefined') {
      publicData.complete('success');
    }

    const billingAddress = publicData.billingAddress;
    const contactInformation = publicData.contactInformation;
    const shippingAddress = publicData.shippingAddress;
    const elementType = publicData.elementType;

    return handleCreateSourceResponse(publicData, data.controller.id, beaconComponent).then(res => {
      res.complete = function() {}; // NOTE: Because the complete function on the actual response returned does not have the complete function, not having this will cause errors for the client.
      res.contactInformation = contactInformation;
      res.billingAddress = billingAddress;
      res.shippingAddress = shippingAddress;
      res.elementType = elementType;
      runListenersBasedOnType(data, key, componentType, componentId, eventName, res);
    });
  } else {
    runListenersBasedOnType(data, key, componentType, componentId, eventName, publicData);
  }

  if (eventName === 'resize') {
    const frame = document.getElementById(publicData.frame.id);
    frame.style.height = publicData.frame.height;
  }

  if (!isCSSExcludedComponent(componentType)) {
    applyCSSClassesBasedOnEvent(data, componentType, eventName, publicData);
  }
}

function isCSSExcludedComponent(componentType) {
  return componentType === 'paypal' || componentType === 'compliance' || componentType === 'taxidentifier' || componentType === 'offlinerefund';
}

/**
 * Processes events that are not credit card events
 * @param {string} key
 * @param {string} componentType
 * @param {string} componentId
 * @param {string} eventName
 * @param {string} publicData
 */
export function processNonCreditCardEvents(key, componentType, componentId, eventName, publicData) {
  const data = dataStore.get(key);

  runListenersBasedOnType(data, key, componentType, componentId, eventName, publicData);
}

/**
 * Removes any events from the customEvents array that are of type componentType
 * @param {String} key datastore key
 * @param {String} id a component id
 */
export function removeEventsForComponent(key, id) {
  const data = dataStore.get(key);

  data.customEvents = data.customEvents.filter(event => event.componentId !== id);

  dataStore.set(key, data);
}