import { sendEventData } from './input-events';
import { sendBeaconEventDetails } from './td/td-utils';

/**
 * Returns true when error is a network error
 * @param {object} response
 * @returns {boolean}
 */
function isNetworkError(response) {
  return response.data.error && response.data.error.message === 'Network Error';
}

/**
 * Sends a create source request and returns response
 * @param controllerEmitter
 * @param {object} componentData
 * @param {object} responseData
 * @param {object} paymentServiceSourceData
 * @param {function} toClientSourceEventData - function that will convert data to data contract for client
 * @param {function} complete - function that will complete the transaction acceptance
 */
export function sendCreateSourceRequest(controllerEmitter, componentData, responseData, paymentServiceSourceData, toClientSourceEventData, complete) {

  controllerEmitter.send('createSourceFromRequest', {
    componentId: componentData.componentId,
    componentType: componentData.componentType,
    sourceRequest: paymentServiceSourceData,
    type: componentData.componentType
  }).then(response => {
    const result = toClientSourceEventData(responseData, response.data, complete);
    sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'source', result);

    if (isNetworkError(response)) {
      complete('failure');
    }

    if(typeof response != 'undefined' && response.data != null && response.data.source != null && response.data.source.id != null) {
      sendBeaconEventDetails('td', 'source', response.data.source.id);
    }
  }).catch((errorResponse) => {
    const error = {
      data: {
        error: {
          message: errorResponse.message
        },
        source: null
      }
    };
    const result = toClientSourceEventData(responseData, error.data, complete);
    sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'source', result);
    complete('failure');
  });

}
