import {getDomainFromUrl, isIframe} from 'cross-domain-utils';

/**
 * Returns true if object a and object b are shallow equivalents
 * @param a
 * @param b
 * @returns {boolean}
 */
export function isShallowEquivalent(a, b) {
  return JSON.stringify(a) === JSON.stringify(b);
}

/**
 * Strips letters from shopper input in field
 * @param {string} input
 * @returns {*}
 */
export function stripLetters(input) {
  const hasLetters = new RegExp(/[^0-9\s]/);
  if(hasLetters.test(input) === true) {
    let str = '';
    const length = input.length;
    for(let i = 0; i < length; i++) {
      if(hasLetters.test(input[i]) === true) {
        continue;
      } else {
        str += input[i];
      }
    }
    return str.trim();
  } else {
    return input.trim();
  }
}

/**
 * Adds a slash to conform to the date pattern MM/YY.
 * @param input
 * @returns {string}
 */
export function stripLettersAndAddForwardSlash(input) {
  let value;
  let strippedValue = stripLetters(input);
  let index = strippedValue.indexOf('/');
  if(index === -1 && strippedValue.length > 2) {
    value = strippedValue.substring(0, 2) + '/' + strippedValue.substring(2, 4);
  } else {
    value = strippedValue;
  }
  return value;
}

/**
 * Returns the domain of the parent of current iFrame
 */
export function getParentDomain() {
  let parentUrl = null;

  if (isIframe(window)) {
    parentUrl = document.referrer;
  }

  return parentUrl === null ? null : getDomainFromUrl(parentUrl);
}

/**
 * Returns true if trigger is createSource
 * Returns true if the new value of input is same as old value
 * @param {string} oldValue
 * @param {Event} event
 * @returns {boolean}
 */
export function checkToSendEvent(oldValue, event) {
  if (event.trigger === 'createSource') {
    return true;
  } else {
    return oldValue.trim() === event.target.value;
  }
}

export function spacesAdded(oldNumber, newNumber) {

  const originalSpaces = oldNumber.split(' ').length - 1;
  const newSpaces = newNumber.split(' ').length - 1;

  return newSpaces - originalSpaces;
}

export function removeExtraCharacters(value, maxLength) {
  if (value.length > maxLength) {
    return value = value.substr(0, maxLength);
  }

  return value;
}

