import {
  createComponent,
  destroy,
  generateComponentId,
  getComponentWindow,
  onEventHandler,
  registerComponentWithController,
  unmount,
  update
} from '../createComponent';
import {sanitizeOptionsForType} from '../../app/components/options';
import dataStore from '../dataStore';
import postRobot from '../../post-robot-wrapper';
import {config} from '../../app/components/config';
import {sendEventData} from '../../app/components/input-events';
import {getLocalizedStrings, getTaxSpecificStrings} from './tax-identifier-data';
import {getLocaleFromLanguage} from '../../app/components/localization/localized-messages';
import {filterByObjectPropertyValue} from '../tax-identifier-utils';
import {setElementErrorMessage} from '../../utils';

let elements = {};
let fields = [];
let componentData = {};
let controllerEmitter;
let controllerListener;

const COMPONENT_TYPE = 'taxidentifier';

/**
 *
 * @param controllerId
 * @param key
 * @param options
 * @returns {{unmount: unmount, controllerId: (*|null), options: (*|*), destroy: destroy, update: update, id: string, parentNode: null, type: string, mount: mountElement, key: *, on: onEventHandler}}
 */
export function createTaxIdentifier(controllerId, key, options) {
  const id = generateComponentId(COMPONENT_TYPE);

  const taxComponent = {
    id: id,
    key: key,
    type: COMPONENT_TYPE,
    parentNode: null,
    controllerId: controllerId,
    mount: mountElement,
    destroy: destroyElement,
    on: onEventHandler,
    options: sanitizeOptionsForType(options, COMPONENT_TYPE) || {},
    unmount: unmountElement,
    update: updateElement,
    window: Window
  };

  componentData = {
    componentType: COMPONENT_TYPE,
    componentId: id,
    controller: {
      id: controllerId,
      window: getComponentWindow(controllerId)
    },
    prevState: {
      empty: true,
      complete: false,
      error: null,
    },
    options: taxComponent.options,
    key: key
  };

  controllerEmitter = postRobot.client({ window: componentData.controller.window, domain: config.domain });
  controllerListener = postRobot.listener({window: componentData.controller.window, domain: config.domain});

  addHandleOptions(controllerListener, handleOptions);

  return taxComponent;
}

export function addContainerToDom(parentId, componentId) {
  document.getElementById(parentId).innerHTML += `<div id="${componentId}" class="DR-tax-identifier-container"></div>`;
}

const CUSTOMER_TYPE_INDIVIDUAL = 'individual';
const CUSTOMER_TYPE_BUSINESS = 'business';

function displayRadioButtons(countrySpec) {
  return individualTaxIdentifiersFound(countrySpec.individualTaxIdentifiersSchemas) && businessTaxIdentifiersFound(countrySpec.businessTaxIdentifiersSchemas);
}

function individualTaxIdentifiersFound(individualTaxIdentifiersSchemas) {
  return typeof individualTaxIdentifiersSchemas !== 'undefined' && individualTaxIdentifiersSchemas.length > 0;
}

function businessTaxIdentifiersFound(businessTaxIdentifiersSchemas) {
  return typeof businessTaxIdentifiersSchemas !== 'undefined' && businessTaxIdentifiersSchemas.length > 0;
}

function hasRequiredField(schemas) {
  return schemas
    .map(s => JSON.parse(s))
    .filter( s => typeof s.required !== 'undefined' && s.required.length > 0).length > 0;
}

function getContainerElement() {
  return document.getElementById(componentData.componentId);
}

function countrySpecProvided(countrySpec) {
  return typeof countrySpec !== 'undefined' && countrySpec.length > 0;
}

function canRenderTaxIdentifier(countrySpec) {
  return countrySpecProvided(countrySpec) &&
      (individualTaxIdentifiersFound(countrySpec[0].individualTaxIdentifiersSchemas) ||
          businessTaxIdentifiersFound(countrySpec[0].businessTaxIdentifiersSchemas));
}

function alreadyRendered() {
  return getContainerElement() !== null;
}

function addFieldDetailsToState(parsedSchema, localeDataForSchema, type) {
  fields.push({
    name: parsedSchema.title,
    pattern: parsedSchema.properties.taxIdentifier.pattern,
    localeDataForSchema: localeDataForSchema,
    type: type
  });
}

function isTypeIndividual(type) {
  return (
    type === 'individual'
  );
}

function isTypeBusiness(type) {
  return (
    type === 'business'
  );
}

function hasCustomerTypeInOptions(data) {
  return (
    typeof data.options !== 'undefined' && typeof data.options.taxIdentifier !== 'undefined' && (isTypeBusiness(data.options.taxIdentifier.type) || isTypeIndividual(data.options.taxIdentifier.type))
  );
}

function hasCustomerTypeInSession(data) {
  return (
    typeof data.sessionCustomerType !== 'undefined' && (isTypeBusiness(data.sessionCustomerType) || isTypeIndividual(data.sessionCustomerType))
  );
}

export function handleOptions(data) {

  const locale = data.instanceOptions.locale.length === 2 ? getLocaleFromLanguage(data.instanceOptions.locale) : data.instanceOptions.locale;
  const parentNodeId = componentData.parentNodeId;
  const componentId = componentData.componentId;

  componentData.instanceOptions = data.instanceOptions;
  componentData.options = data.options;
  componentData.countrySpec = data.countrySpec;
  componentData.sessionCustomerType = data.sessionCustomerType;

  const localizedStrings = getLocalizedStrings(locale);

  if(countrySpecProvided(data.countrySpec) && hasCustomerTypeInOptions(data)) {
    if (isTypeBusiness(data.options.taxIdentifier.type)) {
      const countrySpec = data.countrySpec[0];
      delete countrySpec.individualTaxIdentifiersSchemas;
    }
    if (isTypeIndividual(data.options.taxIdentifier.type)) {
      const countrySpec = data.countrySpec[0];
      delete countrySpec.businessTaxIdentifiersSchemas;
    }
  } else if(countrySpecProvided(data.countrySpec) && hasCustomerTypeInSession(data)) {
    if (isTypeBusiness(data.sessionCustomerType)) {
      const countrySpec = data.countrySpec[0];
      delete countrySpec.individualTaxIdentifiersSchemas;
    }
    if (isTypeIndividual(data.sessionCustomerType)) {
      const countrySpec = data.countrySpec[0];
      delete countrySpec.businessTaxIdentifiersSchemas;
    }
  }

  if (!canRenderTaxIdentifier(data.countrySpec)) {
    emitComponentReady();
    return;
  }

  const countrySpec = data.countrySpec[0];

  if (alreadyRendered()) {
    destroySubElements();
    resetData();
    document.getElementById(parentNodeId).removeChild(document.getElementById(componentId));
  }

  addContainerToDom(parentNodeId, componentId);

  addHeader(componentId, localizedStrings);

  const shouldDisplayRadioButton = displayRadioButtons(countrySpec);
  if (shouldDisplayRadioButton) {
    addRadioButtons(componentId, localizedStrings);
  }

  const individualTaxIdentifiersSchemas = countrySpec.individualTaxIdentifiersSchemas;
  const businessTaxIdentifiersSchemas = countrySpec.businessTaxIdentifiersSchemas;
  if (individualTaxIdentifiersFound(individualTaxIdentifiersSchemas)) {
    const personalPurchaseContainer = document.createElement('div');
    personalPurchaseContainer.id = componentId + 'personalPurchaseContainer';
    individualTaxIdentifiersSchemas.forEach(schema => {
      const parsedSchema = JSON.parse(schema);
      const localeDataForSchema = getTaxSpecificStrings(parsedSchema.title, locale);
      addFieldToDOM(CUSTOMER_TYPE_INDIVIDUAL, personalPurchaseContainer, parsedSchema.title, localeDataForSchema, componentId);
      addFieldDetailsToState(parsedSchema, localeDataForSchema, CUSTOMER_TYPE_INDIVIDUAL);
    });

    getContainerElement().appendChild(personalPurchaseContainer);

  }

  if (businessTaxIdentifiersFound(businessTaxIdentifiersSchemas)) {
    const businessPurchaseContainer = document.createElement('div');
    businessPurchaseContainer.id = componentId + 'businessPurchaseContainer';
    businessTaxIdentifiersSchemas.forEach(businessSchema => {
      const parsedSchema = JSON.parse(businessSchema);
      const localeDataForSchema = getTaxSpecificStrings(parsedSchema.title, locale);
      addFieldToDOM(CUSTOMER_TYPE_BUSINESS, businessPurchaseContainer, parsedSchema.title, localeDataForSchema, componentId);
      addFieldDetailsToState(parsedSchema, localeDataForSchema, CUSTOMER_TYPE_BUSINESS);
    });

    getContainerElement().appendChild(businessPurchaseContainer);

  }

  if (shouldDisplayRadioButton) {
    document.getElementById(componentId + 'personalPurchaseRadioButton').click();
  }

  const controllerId = componentData.controller.id;

  elements = createElements(controllerId, componentData.key, data.options);

}

function addFieldToDOM(customerType, container, fieldType, localeData, componentId) {
  const row = document.createElement('div');
  row.className = 'DR-row DR-tax-identifier-field';

  const dataParent = document.createElement('div');
  dataParent.className = 'DR-fieldGroup';

  const nameField = document.createElement('label');
  nameField.innerHTML = localeData.displayName;
  nameField.className = 'DR-field-label';

  const inputFieldParent = document.createElement('div');
  inputFieldParent.id = `${fieldType}-${componentId}`;
  inputFieldParent.className = 'DR-field-parent';

  const errorMessage = document.createElement('div');
  errorMessage.className = 'DR-error-message';
  errorMessage.id = `${fieldType}-${componentId}-error`;

  dataParent.appendChild(nameField);
  dataParent.appendChild(inputFieldParent);
  row.appendChild(dataParent);
  row.appendChild(errorMessage);

  container.appendChild(row);

}

/**
 *
 * @param controllerId
 * @param key
 * @returns {{cardcvv: {parentId: string, component}, cardnumber: {parentId: string, component}, cardexpiration: {parentId: string, component}}}
 */
export function createElements(controllerId, key, options) {

  fields.forEach(field => {
    const defaultOptions = {
      options: {
        placeholderText: ''
      },
      focus: false,
      empty: true,
      complete: false,
      ready: false,
      value: '',
      elementType: field.name,
      componentType: field.name,
      validationPattern: field.pattern,
    };

    const accessibilityName = `taxid-${field.name.toLowerCase()}`;

    if (typeof options.classes !== 'undefined') {
      defaultOptions.classes = options.classes;
    }

    if (typeof options.style !== 'undefined') {
      defaultOptions.style = options.style;
    }

    elements[field.name] = {
      parentId: field.name,
      elementType: field.name,
      options: defaultOptions,
      component: createComponent('dynamicfield', controllerId, key, defaultOptions, accessibilityName)
    };
  });

  const data = dataStore.get(key);
  Object.keys(elements).forEach(function (key) {
    const componentData = elements[key];

    data.components = Object.assign({}, data.components, { [componentData.component.id]: componentData.component.id });
    const component = componentData.component;

    registerComponentWithController(controllerId, component, componentData.options);

    component.on('ready', () => {handleFieldReady(componentData.elementType);});
    component.on('blur', (details) => {handleFieldBlur(componentData.elementType, details);});
    component.on('focus', (details) => {handleFieldFocus(componentData.elementType, details);});
    component.on('change', (details) => {handleFieldChange(componentData.elementType, details);});
  });

  return elements;
}


export function mountElement(parentNodeId) {
  const parent = document.getElementById(parentNodeId);
  componentData.window = window;
  const data = dataStore.get(this.key);

  data.components[this.type] = { 'parent': parent, 'options': sanitizeOptionsForType(this.options, this.type) };
  dataStore.set(this.key, data);

  this.parentNode = parent;

  const componentId = this.id;

  componentData.parentNodeId = parentNodeId;

  return controllerEmitter.send('mountClientComponent', {
    componentId: componentData.componentId,
    componentType: componentData.componentType
  }).then((response) => {
    handleOptions(response.data);
    mountElements(componentId, elements);
  });

}

function mountElements(parentNode, components) {
  Object.keys(components).forEach(function (key) {
    const componentData = components[key];
    const elementDivId = componentData.parentId + '-' +parentNode;
    componentData.component.mount(elementDivId);
  });
}

export function unmountElement() {
  Object.keys(elements).forEach(function (elementType) {
    if (elements[elementType].component.parentNode) {
      elements[elementType].component.unmount();
    }
  });
  unmount.call(this);
}

function updateElement(options) {
  update.call(this, options);
  Object.keys(elements).forEach(function (elementType) {
    elements[elementType].component.update(options);
  });
}

function destroySubElements() {
  Object.keys(elements).forEach(function (elementType) {
    elements[elementType].component.destroy();
  });
}

function resetData() {
  elements = {};
  fields = [];
}

function destroyElement() {
  destroySubElements();
  resetData();
  return destroy.call(this);
}

function updateElementsState(elements, element, details) {
  elements[element] = Object.assign(elements[element], details);
}

export function handleFieldChange(elementType, details) {
  updateElementsState(elements, elementType, details);
  handleOnChangeEvent(elements, elementType, details, componentData);
}

export function handleFieldBlur(elementType, details) {
  updateElementsState(elements, elementType, {focus: false});
  handleOnBlurEvent(elements, elementType, details, componentData);
}

export function handleFieldFocus(elementType, details) {
  updateElementsState(elements, elementType, {focus: true});
  handleOnFocusEvent(elements, elementType, details, componentData);
}

export function handleFieldReady(elementType) {
  updateElementsState(elements, elementType, {ready: true});
  handleReady(elements);
}

function setErrorMessages(uniqueId, elements) {
  Object.keys(elements).forEach(function (elementType) {
    const el = document.getElementById(`${elementType}-${uniqueId}`);
    const closestParentPurchaseContainer = el.closest('[id$="PurchaseContainer"]');
    const customerType = closestParentPurchaseContainer.id.endsWith('personalPurchaseContainer') ? 'individual' : 'business';
    const errorMessageFromCountrySpec = getErrorMessageFromCountrySpec(elementType, customerType);
    const error = getError(elementType);
    if (error) {
      const parentId = elements[elementType].parentId;
      const errorMessage = errorMessageFromCountrySpec === '' ? error.message : errorMessageFromCountrySpec;
      setElementErrorMessage(`${parentId}-${uniqueId}-error`, errorMessage);
    }
  });
}

function resetErrorMessages(uniqueId, elements) {
  Object.keys(elements).forEach(function (elementType) {
    const parentId = elements[elementType].parentId;
    setElementErrorMessage(`${parentId}-${uniqueId}-error`, '');
  });
}

function handleOnChangeEvent(elements, elementType, eventData, componentData) {

  eventData.identifier = {
    value: elements[elementType].value,
    type: elementType,
    customerType: fields.find( f => f.name === elementType).type
  };

  eventData.elementType = componentData.elementType;

  delete eventData.value;

  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'change', eventData);

  resetErrorMessages(componentData.componentId, elements);

  if(eventData.error) {
    setErrorMessages(componentData.componentId, elements);
  }

}

function handleOnBlurEvent(elements, eventType, eventData, componentData) {
  eventData.elementType = componentData.elementType;
  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'blur', eventData);
}

function handleOnFocusEvent(elements, eventType, eventData, componentData) {
  eventData.elementType = componentData.elementType;
  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'focus', eventData);
}

function handleReady(elements) {
  const elementsNotReady = Object.keys(elements).filter( key =>elements[key].ready !== true);
  if(elementsNotReady.length === 0 && !componentData.isUpdate) {
    emitComponentReady();
  }
}

function getErrorMessageFromCountrySpec(type, customerType) {
  let errorMessageFromCountrySpec = '';
  const typeTaxIdentifiers = filterByObjectPropertyValue(componentData.countrySpec[0].taxIdentifiersInfo, 'type', type);
  const typeAndCustomerTypeTaxIdentifiers = filterByObjectPropertyValue(typeTaxIdentifiers, 'customerType', customerType);
  if(typeof typeAndCustomerTypeTaxIdentifiers[0].locales !== 'undefined') {
    const errorMessagesFromCountrySpec = typeAndCustomerTypeTaxIdentifiers[0].locales;
    const underscoreLocale = componentData.instanceOptions.locale.replace('-', '_');
    const locales = Object.keys(errorMessagesFromCountrySpec);
    const locale = locales.find(key => key === underscoreLocale);
    if(typeof locale !== 'undefined' && typeof errorMessagesFromCountrySpec[locale].helpText !== 'undefined' && errorMessagesFromCountrySpec[locale].helpText !== '') {
      errorMessageFromCountrySpec = errorMessagesFromCountrySpec[locale].helpText;
    }
  }
  return errorMessageFromCountrySpec;
}

function getTaxIdentifierData() {
  let taxIdentifiers = {};

  if(canRenderTaxIdentifier(componentData.countrySpec)) {
    const businessTaxIdentifiers = filterByObjectPropertyValue(componentData.countrySpec[0].taxIdentifiersInfo, 'customerType', 'business');
    const individualTaxIdentifiers = filterByObjectPropertyValue(componentData.countrySpec[0].taxIdentifiersInfo, 'customerType', 'individual');
    const businessTaxIdentifiersRequired = filterByObjectPropertyValue(businessTaxIdentifiers, 'isRequired', true);
    const businessTaxIdentifiersOptional = filterByObjectPropertyValue(businessTaxIdentifiers, 'isRequired', false);
    const individualTaxIdentifiersRequired = filterByObjectPropertyValue(individualTaxIdentifiers, 'isRequired', true);
    const individualTaxIdentifiersOptional = filterByObjectPropertyValue(individualTaxIdentifiers, 'isRequired', false);
    const individual = {
      required: individualTaxIdentifiersRequired.map(t => t.type),
      optional: individualTaxIdentifiersOptional.map(t => t.type)
    };
    const business = {
      required: businessTaxIdentifiersRequired.map(t => t.type),
      optional: businessTaxIdentifiersOptional.map(t => t.type)
    };

    if(hasCustomerTypeInOptions(componentData)) {
      if(isTypeIndividual(componentData.options.taxIdentifier.type)) {
        taxIdentifiers.individual = individual;
      }
      if(isTypeBusiness(componentData.options.taxIdentifier.type)) {
        taxIdentifiers.business = business;
      }
    } else if(hasCustomerTypeInSession(componentData)) {
      if(isTypeIndividual(componentData.sessionCustomerType)) {
        taxIdentifiers.individual = individual;
      }
      if(isTypeBusiness(componentData.sessionCustomerType)) {
        taxIdentifiers.business = business;
      }
    }
    else {
      taxIdentifiers.individual = individual;
      taxIdentifiers.business = business;
    }
  }
  return {
    hasTaxIdentifier: canRenderTaxIdentifier(componentData.countrySpec),
    businessTaxIdentifierRequired: canRenderTaxIdentifier(componentData.countrySpec) && businessTaxIdentifiersFound(componentData.countrySpec[0].businessTaxIdentifiersSchemas) && hasRequiredField(componentData.countrySpec[0].businessTaxIdentifiersSchemas),
    individualTaxIdentifierRequired: canRenderTaxIdentifier(componentData.countrySpec) && individualTaxIdentifiersFound(componentData.countrySpec[0].individualTaxIdentifiersSchemas)  && hasRequiredField(componentData.countrySpec[0].individualTaxIdentifiersSchemas),
    taxIdentifiers: taxIdentifiers
  };
}

export function emitComponentReady() {
  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'ready', getTaxIdentifierData());
}


function getError(elementType) {
  return elements[elementType].hasOwnProperty('error') && elements[elementType].error ? elements[elementType].error : null;
}

function addRadioButtons(uniqueId, localizedStrings) {

  const purchaseTypeElement = document.createElement('div');
  purchaseTypeElement.innerHTML = localizedStrings.purchaseType;
  purchaseTypeElement.className = 'DR-shopper-type';

  const radioButtonParent = document.createElement('div');
  radioButtonParent.id = uniqueId+'radioContainer';
  radioButtonParent.className = 'DR-flex-row DR-standard-padding'; // 'radioButtonParent';

  const personalPurchaseRadioParent = document.createElement('div');
  personalPurchaseRadioParent.className = 'DR-radioGroup';
  const personalPurchaseRadioButtonLabel = document.createElement('label');
  personalPurchaseRadioButtonLabel.htmlFor = 'personalPurchaseRadioButton';
  personalPurchaseRadioButtonLabel.innerText =  localizedStrings.personalPurchase;
  const personalPurchaseRadioButton = document.createElement('input');
  personalPurchaseRadioButton.checked = true;
  personalPurchaseRadioButton.id = uniqueId + 'personalPurchaseRadioButton';
  personalPurchaseRadioButton.type = 'radio';
  personalPurchaseRadioButton.name = 'shopperType';
  personalPurchaseRadioButton.value = 'individual';
  personalPurchaseRadioButton.onclick = function () {
    document.getElementById(uniqueId + 'personalPurchaseContainer').style.display = 'block';
    document.getElementById(uniqueId + 'businessPurchaseContainer').style.display = 'none';
  };

  const businessPurchaseRadioParent = document.createElement('div');
  businessPurchaseRadioParent.className = 'DR-radioGroup';
  const businessPurchaseRadioButtonLabel = document.createElement('label');
  businessPurchaseRadioButtonLabel.htmlFor = 'businessPurchaseRadioButton';
  businessPurchaseRadioButtonLabel.innerText = localizedStrings.businessPurchase;
  const businessPurchaseRadioButton = document.createElement('input');
  businessPurchaseRadioButton.id = uniqueId + 'businessPurchaseRadioButton';
  businessPurchaseRadioButton.type = 'radio';
  businessPurchaseRadioButton.name = 'shopperType';
  businessPurchaseRadioButton.value = 'business';
  businessPurchaseRadioButton.onclick = function () {
    document.getElementById(uniqueId + 'personalPurchaseContainer').style.display = 'none';
    document.getElementById(uniqueId + 'businessPurchaseContainer').style.display = 'block';
  };

  radioButtonParent.appendChild(purchaseTypeElement);

  personalPurchaseRadioParent.appendChild(personalPurchaseRadioButton);
  personalPurchaseRadioParent.appendChild(personalPurchaseRadioButtonLabel);

  radioButtonParent.appendChild(personalPurchaseRadioParent);

  businessPurchaseRadioParent.appendChild(businessPurchaseRadioButton);
  businessPurchaseRadioParent.appendChild(businessPurchaseRadioButtonLabel);

  radioButtonParent.appendChild(businessPurchaseRadioParent);

  getContainerElement().appendChild(radioButtonParent);

}

function addHeader(uniqueId, localizedStrings) {
  const header = document.createElement('div');
  header.id = uniqueId+'header';
  header.innerText = localizedStrings.taxId;
  header.className = 'DR-title';
  getContainerElement().appendChild(header);
}

/**
 * Adds handleOptions to component
 * @param controllerListener
 * @param {Function} handleOptions
 */
export function addHandleOptions(controllerListener, handleOptions) {
  controllerListener.on('taxIdentifierOptions'+componentData.componentId, (event) => {
    handleOptions(event.data);
    componentData.isUpdate = true;
    mountElements(componentData.componentId, elements);
  });
}