import {getIframeTitle} from '../app/components/accessibility';
/**
 * Checks to make sure node exists
 * @param {HTMLElement} node
 * @returns {*}
 */
function getSafeNode(node) {
  if (node instanceof HTMLElement
    && typeof node.appendChild === 'function') {
    return node;
  }

  return null;
}

/**
 * Creates frame for element
 * @param {string} type
 * @param {HTMLElement} node
 * @param {string} src
 * @param {object} attributes
 * @param {string} elementHeight
 * @param {string} locale
 * @param {string} id
 * @returns {HTMLElement}
 */
export function createFrame(type, node, src, attributes, elementHeight, locale, id, nameForAccessibility) {
  node = getSafeNode(node);

  if (node === null) {
    throw new Error('Cannot create a frame without a valid node to attach to.');
  }

  if (src === null || typeof src !== 'string') {
    throw new Error('createFrame() requires a valid src attribute');
  }

  if (!attributes) {
    attributes = {};
  }

  let iframeStyle = `height: ${elementHeight}; width: 100%; margin: 0px; padding: 0px; border: none;`;

  let koreanCardIframeStyle = `height: ${elementHeight}; width: 100%; margin: 0px; padding: 0px; border: none; z-index: 10000`;

  let adyenIframeStyle = 'position: absolute; border:none; height: 100%; width: 100%; z-index: 10000';

  if (type === 'controller' || type === 'td') {
    attributes = Object.assign(attributes, {
      width: '0',
      height: '0',
      frameBorder: '0',
      style: 'display: block'
    });
  } else if (type === 'dr3dsecure') {
    attributes = Object.assign(attributes, {
      style: adyenIframeStyle
    });
  } else if (type === 'koreancard') {
    attributes = Object.assign(attributes, {
      style: koreanCardIframeStyle
    });
  }
  else {
    attributes = Object.assign(attributes, {
      style: iframeStyle
    });
  }

  let frame = document.createElement('iframe');
  frame.setAttribute('src', src);
  frame.setAttribute('scrolling', 'no');
  frame.setAttribute('referrerpolicy', 'no-referrer-when-downgrade');

  if(typeof nameForAccessibility !== 'undefined') {
    const iframeTitle = getIframeTitle(locale, nameForAccessibility);
    frame.setAttribute('title', iframeTitle);
  }

  if (type === 'googlepay') {
    frame.setAttribute('allowpaymentrequest', 'true');
    frame.setAttribute('sandbox', 'allow-forms allow-popups allow-scripts allow-top-navigation allow-modals allow-same-origin allow-popups-to-escape-sandbox');
  }

  Object.keys(attributes).forEach(function (key) {
    frame.setAttribute(key, attributes[key]);
  });

  node.appendChild(frame);

  return frame;
}