import {handleEvent, sendEventData} from './input-events';
import {
  convertClientShippingAddressErrorsForPaymentRequestUpdateEvent,
  convertPaymentRequestApiShippingAddressForEvent,
  convertShippingOptions,
  getDetailsFromOptions,
  onBuyClicked,
  paymentRequestApiResponseToPaymentServiceRequest,
  toSourceEventData,
  updateShippingOptionsSelectedAttribute
} from './payment-api';
import {mergeOptions} from './options';
import {sendCreateSourceRequest} from './google-apple-pay-events';

/**
 * Emits component ready event
 * @param {object} componentData
 */
export function emitComponentReady(componentData) {
  handleEvent(componentData, 'ready');
}

/**
 * Emits component cancelled event
 * @param {object} componentData
 */
export function emitComponentCancelled(componentData) {
  handleEvent(componentData, 'cancel');
}

/**
 * Returns complete function for payment source request
 * @param {Function} resolve
 * @param {object} paymentRequestResponseData
 * @returns {Function}
 */
export function createCompleteFunction(resolve, paymentRequestResponseData) {
  return function (status) {
    resolve({instrumentResponse: paymentRequestResponseData, status: status});
    return status;
  };
}

/**
 * Takes PaymentRequest API payment response and sends it to DR Payment Service
 * @param {object} paymentRequestData
 * @param {Function} resolve
 * @param {object} instanceData
 */
export function processPayment(paymentRequestData, resolve, instanceData ) {
  const complete = createCompleteFunction(resolve, paymentRequestData);

  const paymentServiceRequest = paymentRequestApiResponseToPaymentServiceRequest(paymentRequestData, instanceData.getPaymentOptions());

  if (typeof instanceData.getPaymentOptions().billingAddress !== 'undefined') {
    paymentServiceRequest.sessionId = instanceData.getPaymentOptions().sessionId;
    delete paymentServiceRequest.amount;
    paymentServiceRequest.owner = instanceData.getPaymentOptions().billingAddress;
    paymentRequestData.details = {
      billingAddress: instanceData.getPaymentOptions().billingAddress
    };
  }

  if (typeof instanceData.getPaymentOptions().sessionId !== 'undefined') {
    paymentServiceRequest.sessionId = instanceData.getPaymentOptions().sessionId;
  }

  // Send payment / credit card data to controller to call payment service
  sendCreateSourceRequest(instanceData.controllerEmitter, instanceData.componentData, paymentRequestData, paymentServiceRequest, toSourceEventData, complete);

}

/**
 * Sends shipping address change event to client
 * @param {object} googleShippingAddress
 * @param {string} selectedShippingOption
 * @param {Function} resolve
 * @param {object} instanceData
 * @returns {{updateWith: (function(*=): {requestShipping: boolean, total: {amount: {currency: *, value: *}, label: *}, shippingAddressErrors: (*|changedData.shippingAddressErrors|{country, city}|expected.shippingAddressErrors|{}), error: *, shippingOptions: Array, displayItems: Array}), shippingAddress: {firstName: string, lastName: string, address: {country: string, city: string, postalCode: string, state: string, line2: *, line1: string}, phone: string, name: string, email: string}}}
 */
export function sendShippingAddressChangeEvent(googleShippingAddress, selectedShippingOption, resolve, instanceData) {
  const shippingAddress = convertPaymentRequestApiShippingAddressForEvent(googleShippingAddress);

  const paymentOptions = instanceData.getPaymentOptions();
  // set the previously selected one as selected in the data
  paymentOptions.shippingOptions.forEach(shippingOption => {
    shippingOption.selected = shippingOption.id === selectedShippingOption;
  });

  const updateWith = function (clientPassedInData) {
    let errorData = {};
    if (clientPassedInData.status === 'failure') {
      errorData = convertClientShippingAddressErrorsForPaymentRequestUpdateEvent(clientPassedInData.error, instanceData);
    }

    const mergedDataWithoutErrors = mergeOptions(paymentOptions, clientPassedInData);
    if (clientPassedInData.status === 'success') {
      delete mergedDataWithoutErrors['error'];
    }

    instanceData.setOptions(mergedDataWithoutErrors);

    const mergedData = Object.assign({}, mergedDataWithoutErrors, errorData);

    const newPaymentOptions = updateShippingOptionsSelectedAttribute(mergedData);

    const detailsData = getGoogleTransactionInfo(newPaymentOptions);

    if (Object.keys(errorData).length > 0) {
      resolve({
        error: errorData.error,
        newTransactionInfo: detailsData,
        newShippingOptionParameters: getGoogleDefaultShippingOptions(mergedData)
      });
    } else {
      resolve({
        newTransactionInfo: detailsData,
        newShippingOptionParameters: getGoogleDefaultShippingOptions(mergedData)
      });
    }



    return detailsData;
  };

  const eventData = {
    shippingAddress: shippingAddress,
    updateWith: updateWith,
  };

  sendEventData(instanceData.componentData.controller, instanceData.componentData.componentId, instanceData.componentData.componentType, 'shippingaddresschange', eventData);

  return eventData;
}

/**
 * sends shipping option change event to client
 * @param {string} selectedShippingOption
 * @param {Function} resolve
 * @param {object} instanceData
 * @returns {{updateWith: (function(*=): Object), shippingOption: *}}
 */
export function sendShippingOptionChangeEvent(selectedShippingOption, resolve, instanceData) {

  const updateWith = function (clientPassedInData) {
    const paymentOptions = instanceData.getPaymentOptions();
    // set the selected one as selected in the data
    paymentOptions.shippingOptions.forEach(shippingOption => {
      shippingOption.selected = shippingOption.id === selectedShippingOption;
    });

    const data = mergeOptions(paymentOptions, clientPassedInData);

    instanceData.setOptions(data);

    const updatedDetails = updateShippingOptionsSelectedAttribute(data);

    const detailsData = getGoogleTransactionInfo(updatedDetails);
    resolve({
      newTransactionInfo: detailsData,
      newShippingOptionParameters: getGoogleDefaultShippingOptions(data)
    });
    return detailsData;
  };

  const paymentOptions = instanceData.getPaymentOptions();
  // If there are shippingOptions, find the selected shipping option
  const foundShippingOption = paymentOptions.shippingOptions && paymentOptions.shippingOptions.find(shippingOption => {
    return shippingOption.id === selectedShippingOption;
  });

  const eventData = {
    shippingOption: foundShippingOption,
    updateWith: updateWith,
  };

  sendEventData(instanceData.componentData.controller, instanceData.componentData.componentId, instanceData.componentData.componentType, 'shippingoptionchange', eventData);

  return eventData;
}


/**
 * Sends click event to client
 * @param {object} instanceData
 * @param {Function} resolve
 * @returns {{updateWith: (function(*=): Object)}}
 */
export function sendClickEvent(instanceData, resolve) {

  if (waitForClientUpdateWithFunction(instanceData.getPaymentOptions())) {

    const updateWith = function (clientPassedInData) {
      const data = mergeOptions(instanceData.getPaymentOptions(), clientPassedInData);
      instanceData.setOptions(data);
      resolve(getDetailsFromOptions(instanceData.getPaymentOptions()));
      return data;
    };

    const result = {
      updateWith: updateWith
    };

    sendEventData(instanceData.componentData.controller, instanceData.componentData.componentId, instanceData.componentData.componentType, 'click', result);

    return result;
  } else {
    resolve(getDetailsFromOptions(instanceData.getPaymentOptions()));

    const result = {};

    sendEventData(instanceData.componentData.controller, instanceData.componentData.componentId, instanceData.componentData.componentType, 'click', result);

    return result;
  }

}

/**
 * Returns true if client has waitOnClick set to true
 * @param {object} paymentOptions
 * @returns {boolean}
 */
export function waitForClientUpdateWithFunction(paymentOptions) {
  return typeof paymentOptions.waitOnClick !== 'undefined' && paymentOptions.waitOnClick === true;
}

/**
 * Handles pay button click
 * @param {object} instanceData
 * @returns {Promise<any|never>}
 */
export async function onPayButtonClick(instanceData, paymentData) {
  return onBuyClicked(processPayment, emitComponentCancelled, sendClickEvent, instanceData, paymentData);
}

export function createDisplayItems(paymentOptions) {

  if (paymentOptions.displayItems && Array.isArray(paymentOptions.displayItems)) {
    const displayItems = [];
    paymentOptions.displayItems.forEach(function (displayItem, index) {
      displayItems[index] = {
        label: displayItem.label,
        type: 'LINE_ITEM',
        price: displayItem.amount.toString(),
        status: displayItem.isPending === true ? 'PENDING' : 'FINAL',
      };
    });
    return displayItems;
  }
  return undefined;
}

export function getGoogleTransactionInfo(paymentOptions) {
  return {
    displayItems: createDisplayItems(paymentOptions),
    countryCode: paymentOptions.country.toUpperCase(),
    currencyCode: paymentOptions.currency,
    totalPriceStatus: paymentOptions.total.isPending === true ? 'ESTIMATED' : 'FINAL',
    totalPrice: paymentOptions.total.amount,
    totalPriceLabel: paymentOptions.total.label
  };

}

export function getGoogleDefaultShippingOptions(paymentOptions) {

  if (typeof paymentOptions.shippingOptions === 'undefined' || paymentOptions.shippingOptions.length === 0) {
    return [];
  }

  const defaultSelected = paymentOptions.shippingOptions.filter( so => so.selected === true );

  return {
    defaultSelectedOptionId: defaultSelected.length > 0 ? defaultSelected[0].id : paymentOptions.shippingOptions[0].id,
    shippingOptions: convertShippingOptions(paymentOptions.shippingOptions)
  };
}