import data from './delayedPaymentInstructions.json';
import {getMappedLocale} from '../../app/components/localization/localized-messages';

function getData(locale) {
  return data[locale];
}

export function getDelayedPaymentInstructionsLocalizedStrings(titles, locale) {
  const mappedLocale = getMappedLocale(locale);
  const translations = {};

  titles.forEach((title) => {
    translations[title] = getData(mappedLocale)[title];
  });
  return translations;
}